import {
  Component, EventEmitter, Input, OnChanges, Output, SimpleChanges
} from '@angular/core';
import { QuizMode, QuizElement } from '../../../../../model/document-element.model';
import { ElementTypeKind } from '../../../../../model/element-type.model';

@Component({
  moduleId: module.id,
  selector: 'fill-in-the-blank-quiz-option',
  templateUrl: 'fill-in-the-blank-quiz-option.component.html',
  styleUrls: ['quiz-option.component.scss']
})

export class FillInTheBlankQuizOptionComponent implements OnChanges {
  QuizMode: typeof QuizMode = QuizMode;

  readonly _A_CHAR_HEX_CODE: number = 0x041;

  langDisplay: any;

  @Input() element: QuizElement = new QuizElement(ElementTypeKind.MultipleQuiz);
  @Input() fillInTheBlankQuizMode: QuizMode = QuizMode.Edit;
  @Input() answerOptionKey: string;
  @Input() answerOptionIndex: number = 0;
  @Input() isGrayOut: boolean = false;

  @Output() deleteAnswerOptionIndexChange: EventEmitter<string> = new EventEmitter<string>();
  @Output() contentElementChange: EventEmitter<any> = new EventEmitter<any>();

  constructor() {
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['element'] && changes['element'].currentValue) {

    }
  }

  get answerOptionLetterIndex(): string {
    let loop = (this.answerOptionIndex + 1) / 26;
    return String.fromCharCode(this._A_CHAR_HEX_CODE + this.answerOptionIndex % 26) + (loop > 1 ? Math.floor(loop).toString() : '');
  }

  get answerOption(): string {
    let option = '';
    let dom = window.document.createElement('DIV');
    dom.innerHTML = this.element.displayText;
    let spanNodeList = dom.getElementsByTagName('SPAN');

    for (let i = 0, length = spanNodeList.length; i < length; i++) {
      if (spanNodeList[i].getAttribute('data-blank') === this.answerOptionKey) {
        spanNodeList[i].removeAttribute('style');
        option += '<p>' + spanNodeList[i].outerHTML + '</p>';
      }
    }

    return option;
  }

  onDelete(event: MouseEvent) {
    event.stopPropagation();
    this.deleteAnswerOptionIndexChange.emit(this.answerOptionKey);
  }
}



