import {
  Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges
} from '@angular/core';
import { QuizMode, MultipleQuizType, QuizElement } from '../../../../model/document-element.model';
import { ElementTypeKind } from '../../../../model/element-type.model';
import {ElementResult} from '../../../../model/quiz-element-report.model';
import {SimulationSetting} from '../../../../model/simulation.settings.model';
import {Utility} from '../../../helper/utilities';

@Component({
  moduleId: module.id,
  selector: 'multiple-quiz-display',
  templateUrl: 'multiple-quiz-display.component.html',
  styleUrls: ['multiple-quiz-display.component.scss', 'quiz-element-display.component.scss']
})

export class MultipleQuizDisplayComponent implements OnInit, OnChanges {
  MultipleQuizType: typeof MultipleQuizType = MultipleQuizType;

  langDisplay: any;
  answerOptions: string[] = [];
  answers: number[] = [];
  isCorrect: boolean = false;
  results: boolean [] = [];
  totalCorrectUserAnswers: number = 0;

  @Input() element: QuizElement = new QuizElement(ElementTypeKind.MultipleQuiz);
  @Input() multipleQuizMode: QuizMode = QuizMode.View;
  @Input() verifyUserAnswerMode: boolean = false;
  @Input() simulationSettings: SimulationSetting = new SimulationSetting();

  @Output() submitAnswer: EventEmitter<[any, ElementResult]> = new EventEmitter<[any, ElementResult]>();

  constructor() {
  }

  ngOnInit() {

  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['element'] && changes['element'].currentValue) {
      this.answerOptions = this.element.multipleQuizAnswerOptions.slice(0);
      this.answers = [];
    }
  }

  onChooseAnswer(index: number) {
    if (this.element.multipleQuizType === MultipleQuizType.MultipleChoice) {
      this.answers = [index];
    } else if (this.element.multipleQuizType === MultipleQuizType.MultipleMark) {
      let existsIndex = this.answers.findIndex(ans => ans === index);

      if (existsIndex !== -1) {
        this.answers.splice(existsIndex, 1);
        return;
      }

      this.answers.push(index);
      this.answers.sort((a, b) => a - b);
    }
  }

  onSubmitAnswer() {
    if (!this.answers.length) {
      return;
    }

    let same = (this.answers.length === this.element.multipleQuizAnswers.length) && this.answers.every((element, index) => {
        return element === this.element.multipleQuizAnswers[index];
      });
    this.submitAnswer.emit([this.answers, new ElementResult(this.element.id, same)]);
  }

  onVerifyUserAnswer(userAnswer: number[]) {
    this.isCorrect = (userAnswer.length === this.element.multipleQuizAnswers.length) && userAnswer.every((element, index) => {
      return element === this.element.multipleQuizAnswers[index];
    });

    this.answers = userAnswer;
    this.results = this.element.multipleQuizAnswers.map(answer => false);
    this.answers.forEach(answer => {
      let correct = this.element.multipleQuizAnswers.indexOf(answer) > -1;
      this.results[answer] = correct;
    });
    this.totalCorrectUserAnswers = this.results.filter(correct => correct).length;
  }

  get correctAnswerLetterIndexes(): string {
    return Utility.getCorrectAnswerLetterIndexes(this.element.multipleQuizAnswers);
  }
}



