export class SiteThemeColors {
  public static DefaultMainAccentColor: string = '#FFCC00';
  public static DefaultLinkActionColor: string = '#3CA7F1';
  public static DefaultLinkActionRolloverColor: string = '#1F4E79';

  mainAccent: string = SiteThemeColors.DefaultMainAccentColor;
  linkAction: string = SiteThemeColors.DefaultLinkActionColor;
  linkActionRollover: string = SiteThemeColors.DefaultLinkActionRolloverColor;

  get isDefault() {
    return this.mainAccent === SiteThemeColors.DefaultMainAccentColor &&
      this.linkAction === SiteThemeColors.DefaultLinkActionColor &&
      this.linkActionRollover === SiteThemeColors.DefaultLinkActionRolloverColor;
  }

  fromJson(json: any) {
    if (!json) {
      return this;
    }

    this.mainAccent = json.mainAccent;
    this.linkAction = json.linkAction;
    this.linkActionRollover = json.linkActionRollover;

    return this;
  }

  toJson() {
    return {
      mainAccent: this.mainAccent,
      linkAction: this.linkAction,
      linkActionRollover: this.linkActionRollover
    }
  }
}



