import { Component, Input } from '@angular/core';
import { DocumentLinkElement } from '../../../model/document-element.model';

@Component({
  moduleId: module.id,
  selector: 'document-link-display',
  templateUrl: 'document-link-display.component.html',
  styleUrls: ['document-link-display.component.scss']
})

export class DocumentLinkDisplayComponent {
  @Input() element: DocumentLinkElement = new DocumentLinkElement();

  preventEmitParent(event: MouseEvent) {
    event.stopPropagation();
  }
}



