import {
  Component, ElementRef, EventEmitter, Input, OnChanges, Output, SimpleChanges, ViewChild
} from '@angular/core';
import { DocumentElement, StepElement } from '../../model/document-element.model';
import { ElementTypeKind } from '../../model/element-type.model';

declare const fabric: any;

@Component({
  selector: 'image-canvas',
  templateUrl: 'image-canvas.component.html',
  styleUrls: ['image-canvas.component.scss']
})

export class ImageCanvasComponent implements OnChanges {
  @Input() element: DocumentElement = new DocumentElement;
  @ViewChild('canvas') canvasRef: ElementRef;

  canvas: any;

  ngOnChanges(changes: SimpleChanges) {
    if (changes['element'] && changes['element'].currentValue) {
      this.loadCanvas();
    }
  }

  changeElement(element: DocumentElement) {
    this.element = element;
    this.loadCanvas();
  }

  loadCanvas() {
    if (this.element.kind !== ElementTypeKind.Step) {
      return;
    }

    let step = this.element as StepElement;

    if (!step.originalScreenRect) {
      return;
    }

    this.canvas = new fabric.CanvasEx(this.canvasRef.nativeElement);
    this.canvas.setHeight(step.originalScreenRect.height);
    this.canvas.setWidth(step.originalScreenRect.width);

    if (step.drawing && step.drawing.length) {
      fabric.util.enlivenObjects(JSON.parse(step.drawing), (objects: any) => {
        objects.forEach((o: any) => {
          o.selectable = false;
          this.canvas.add(o);
        });
      });
    }
    this.canvas.renderAll();
  }
}



