export class DemoSimulationSetting {
  movementTime: number = 0;
  pauseBefore: number = 0;
  pauseAfter: number = 0;
  clickLength: number = 0;
  betweenKeys: number = 0;
  autoStartDemo: boolean = false;
  pauseDemoOnNotes: boolean = false;
  showStepList: boolean = false;
  clickAndTypeSounds: boolean = true;

  fromJson(json: any) {
    if (!json) {
      return this;
    }

    this.movementTime = +json.movementTime;
    this.pauseBefore = +json.pauseBefore;
    this.pauseAfter = +json.pauseAfter;
    this.clickLength = +json.clickLength;
    this.betweenKeys = +json.betweenKeys;
    this.autoStartDemo = json.autoStartDemo;
    this.pauseDemoOnNotes = json.pauseDemoOnNotes;
    this.showStepList = json.showStepList;
    this.clickAndTypeSounds = json.clickAndTypeSounds;

    return this;
  }

  toJson() {
    return {
      movementTime: +this.movementTime,
      pauseBefore: +this.pauseBefore,
      pauseAfter: +this.pauseAfter,
      clickLength: +this.clickLength,
      betweenKeys: +this.betweenKeys,
      autoStartDemo: this.autoStartDemo,
      pauseDemoOnNotes: this.pauseDemoOnNotes,
      showStepList: this.showStepList,
      clickAndTypeSounds: this.clickAndTypeSounds
    };
  }
}



