import { Component, EventEmitter, Input, OnChanges, Output, SimpleChanges } from '@angular/core';
import { QuizElement } from '../../../../model/document-element.model';
import { ElementTypeKind } from '../../../../model/element-type.model';
import {ElementResult} from '../../../../model/quiz-element-report.model';
import {SimulationSetting} from '../../../../model/simulation.settings.model';

@Component({
  moduleId: module.id,
  selector: 'true-false-quiz-display',
  templateUrl: 'true-false-quiz-display.component.html',
  styleUrls: ['true-false-quiz-display.component.scss', 'quiz-element-display.component.scss']
})

export class TrueFalseQuizDisplayComponent implements OnChanges {
  @Input() element: QuizElement = new QuizElement(ElementTypeKind.TrueFalseQuiz);
  @Input() simulationMode: boolean = false;
  @Input() verifyUserAnswerMode: boolean = false;
  @Input() simulationSettings: SimulationSetting = new SimulationSetting();
  @Output() submitAnswer: EventEmitter<[any, ElementResult]> = new EventEmitter<[any, ElementResult]>();
  selectedAnswer: boolean;
  langDisplay: any;
  isCorrect: boolean = false;
  isCorrectAnswer: boolean = false;

  constructor () {
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['element'] && changes['element'].currentValue) {
      this.selectedAnswer = null;
    }
  }

  onSubmitAnswer() {
    if (this.selectedAnswer === null || typeof this.selectedAnswer === 'undefined') {
      return;
    }

    this.submitAnswer.emit([this.selectedAnswer, new ElementResult(this.element.id, this.selectedAnswer === this.element.trueFalseAnswer)]);
  }

  onVerifyUserAnswer(userAnswer: boolean) {
    this.isCorrect = userAnswer === this.element.trueFalseAnswer;
    this.selectedAnswer = userAnswer;
    this.isCorrectAnswer = this.element.trueFalseAnswer;
  }
}



