import { Component, ElementRef, EventEmitter, Input, OnChanges, Output, SimpleChanges, ViewChild } from '@angular/core';
import { DocumentElement, StepElement } from '../../../model/document-element.model';
import { AuthoringService } from '../../../services/authoring.service';
import { SimulationSetting } from '../../../model/simulation.settings.model';
import { DocumentTypeModel, SimulationType } from '../../../model/document-type.model';
import { QuizElementReportModel } from '../../../model/quiz-element-report.model';
import { MdSliderChange } from '../../../shared/material/slider';
import { Constants } from '../../../shared/helper/constants';
import {ElementTypeKind} from '../../../model/element-type.model';
import { ActionKind } from '../../../model/recorder-event.model';

@Component({
  moduleId: module.id,
  selector: 'content-simulation-control',
  templateUrl: 'content-simulation-control.component.html',
  styleUrls: ['content-simulation-control.component.scss']
})

export class ContentSimulationControlComponent implements OnChanges {
  @Input() elements: DocumentElement [] = [];
  @Input() simulationType: number = 0;
  @Input() simulationSettings: SimulationSetting = new SimulationSetting();
  @Input() showQuizResult: boolean = false;
  @Input() quizReport: QuizElementReportModel = new QuizElementReportModel();
  @Input() selectedDoctype: DocumentTypeModel = null;
  @Output() selectedElementChanged: EventEmitter<[DocumentElement, number]> = new EventEmitter<[DocumentElement, number]>();
  @Output() showStepListFlagChanged: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Output() fullScreenToogled: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Output() controlSimulationEmit: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Output() nextElementEmit: EventEmitter<any> = new EventEmitter<any>();
  @Output() previousElementEmit: EventEmitter<any> = new EventEmitter<any>();
  @Output() viewQuizReportDetail: EventEmitter<DocumentElement> = new EventEmitter<DocumentElement>();
  @Output() goToOtherElement: EventEmitter<any> = new EventEmitter<any>();
  @Output() viewQuizReportSummarize: EventEmitter<any> = new EventEmitter<any>();
  @Output() finalInteractiveEmit: EventEmitter<any> = new EventEmitter<any>();
  @Output() selectedChildStepChanged: EventEmitter<[number, number, number]> = new EventEmitter<[number, number, number]>();
  @ViewChild('stepListContainer') stepListContainer: ElementRef;
  @ViewChild('areaNextButton') areaNextButton: ElementRef;
  @ViewChild('areaNextStep') areaNextStep: ElementRef;
  @ViewChild('btnNext') btnNext: ElementRef;
  @ViewChild('btnNextStep') btnNextStep: ElementRef;
  @Output() zoomContentChanged: EventEmitter<number> = new EventEmitter<number>();
  @Output() zoomContentReset: EventEmitter<any> = new EventEmitter<any>();
  @Input() zoomPercentage: string;
  @Input() scaleRatio: number;
  @Input() isBookContent : boolean = false;
  @Input() currentStepIndex: number = 1;
  @Input() isShowNextButton: boolean = false;
  @Input() isShowStepList: boolean = false;
  @Output() clickedNextButton: EventEmitter<any> = new EventEmitter<any>();
  @Output() fitToScreenChanged: EventEmitter<any> = new EventEmitter<any>();
  @Output() clickStepListIconEmit: EventEmitter<boolean> = new EventEmitter<boolean>();
  selectedElement: DocumentElement = new DocumentElement();
  isFullScreen: boolean = false;
  showStepList: boolean = true;
  isPlaySimulation: boolean = true;
  showStepListControl: boolean = true;
  elementListViewModes: { [id: string]: SimulationElementViewMode; } = {};
  langDisplay: any;
  langDisplayBuildAStep: any;
  simulationTypeModel: typeof SimulationType = SimulationType;
  stepInstructionFontSize: string = '';
  stepInstructionFont = Constants.FONT_FAMILY;
  constructor(private authoringService: AuthoringService) {
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['simulationType'] && changes['simulationType'].currentValue) {
      this.onChangeSimulationControlStatus(this.simulationSettings.demoSimulationSetting.autoStartDemo);

      if (this.simulationType === SimulationType.Test) {
        this.showStepListControl = this.simulationSettings.testSimulationSetting.showControls;

        if (!this.showStepListControl) {
          this.toogleStepList();
        }
      } else if (this.simulationType === SimulationType.Demo) {
        this.showStepListControl = this.simulationSettings.demoSimulationSetting.showStepList;

        if (!this.simulationSettings.demoSimulationSetting.showStepList) {
          this.toogleStepList();
        }
      } else {
        this.showStepList = true;
        this.showStepListControl = true;
      }
    }

    if (changes['elements'] && changes['elements'].currentValue) {
      if (this.elements && this.elements.length) {
        this.initViewModes(this.elements);        
        this.selectedElementChange(this.elements[0], 0, false);
      }
    }

    if (changes['simulationSettings']) {
      this.initStepInstructionFontSize();
    }
  }

  resetFlag() {
    this.isFullScreen = false;
    this.showStepList = true;
    this.isPlaySimulation = true;
  }

  selectedElementChange(selectedElement: DocumentElement, index: number, noteEmit: boolean = false) {
    this.selectedElement = selectedElement;
    if (!noteEmit) {
      this.selectedElementChanged.emit([selectedElement, index]);
    }
  }

  toogleStepList() {
    this.showStepList = !this.showStepList;
    this.showStepListFlagChanged.emit(this.showStepList);
  }

  onControlSimulation() {
    this.onChangeSimulationControlStatus(!this.isPlaySimulation);
  }

  onChangeSimulationControlStatus(flag: boolean) {
    this.isPlaySimulation = flag;
    this.controlSimulationEmit.emit(this.isPlaySimulation);
  }

  onNextElement() {
    if (this.currentStepIndex === this.elements.length - 1 &&
      (this.selectedElement as StepElement).action === ActionKind.Type) {
      this.finalInteractiveEmit.emit('emit');
    } else {
      this.nextElementEmit.emit('emit');
    }
  }

  onPreviousElement() {
    this.previousElementEmit.emit('emit');
  }

  onGoToOtherElement(step: number) {
    this.goToOtherElement.emit(step);
    this.onChangeSimulationControlStatus(false);
  }

  initViewModes(elements: DocumentElement []) {
    elements.forEach(e => {
      this.elementListViewModes[e.id] = SimulationElementViewMode.Normal;
      if (e.subElements && e.subElements.length)
      {
        this.initViewModes(e.subElements);
      }
    });
  }

  preventDefault() {
    event.preventDefault();
    event.stopPropagation();
  }

  zoomContent(value: number) {
    this.zoomContentChanged.emit(value * Constants.rangeZoom);
  }

  formatLabel(value: number) {
    return value;
  }

  sliderChanged(event: MdSliderChange) {
    if (event.value - 1 !== this.currentStepIndex) {
      this.onGoToOtherElement(event.value - 1);
    }
  }

  getTitlePlayOrPause() {
    if (this.isPlaySimulation) {
      return this.langDisplay && this.langDisplay.PASUE ? this.langDisplay.PASUE : 'Pause';
    } else {
      return this.langDisplay && this.langDisplay.PLAY ? this.langDisplay.PLAY : 'Play';
    }
  }

  fitToScreen() {
    this.fitToScreenChanged.emit();
  }

  onClickNextButton() {
    if ((this.selectedElement as StepElement).action === ActionKind.Type
      && (this.simulationType === SimulationType.Interactive || this.simulationType === SimulationType.Test)) {
      if (this.currentStepIndex === this.elements.length - 1) {
        this.finalInteractiveEmit.emit({ countStepWrong: true, elementId: this.selectedElement.id });
      } else {
        this.nextElementEmit.emit({ countStepWrong: true, elementId: this.selectedElement.id });
      }
    } else {
      this.clickedNextButton.emit();
    }
  }

  onClickStepListButton() {
    this.isShowStepList = !this.isShowStepList;
    this.clickStepListIconEmit.emit(this.isShowStepList);
  }

  isNoInstructionFrame() {
   return this.selectedElement && this.selectedElement.kind === ElementTypeKind.Step &&
     (this.selectedElement as StepElement).convertedFromFrame &&
      (!(this.selectedElement as StepElement).controlType && !(this.selectedElement as StepElement).action);
  }

  get isShowTheNextButton() {
    if (this.isShowNextButton && !this.showQuizResult) {
      if (this.isShowTypeActionBubble) {
        if (this.simulationType === SimulationType.Interactive)
          return false;
      }
      return true;
    }
    return false;
  }

  get setPositionForTypeInstruction() {
    let style = {};
    let button: ElementRef = null;
    let offset = 0;
    if (this.simulationType === SimulationType.Interactive) {
      button = this.areaNextStep;
      offset = 20;
      style['bottom'] = '50px';
    } else {
      button = this.areaNextButton;
    }

    if (button && button.nativeElement) {
      let bounding = button.nativeElement.getBoundingClientRect();
      if (bounding) {
        style['left'] = `${bounding.left - 20 - offset}px`;
      }
    }
    return style;
  }

  get messageForTypeAction() {
    if (this.simulationType === SimulationType.Interactive) {
      return this.langDisplay && this.langDisplay.PRESS_NEXT_STEP_TO_CONTINUE ? this.langDisplay.PRESS_NEXT_STEP_TO_CONTINUE : 'Press the Next Step button to continue';
    }
    return this.langDisplay && this.langDisplay.PRESS_NEXT_TO_CONTINUE ? this.langDisplay.PRESS_NEXT_TO_CONTINUE : 'Click Next to mark this step as incorrect and proceed';
  }

  get setPositionForInstruction() {
    let style = {};
    if (this.areaNextButton && this.areaNextButton.nativeElement) {
      let bounding = this.areaNextButton.nativeElement.getBoundingClientRect();
      if (bounding && !this.isBookContent) {
        style['left'] = `${bounding.left - 20}px`;
      }
      else{
        if (this.btnNext && this.btnNext.nativeElement) {
          let bounding = this.btnNext.nativeElement.getBoundingClientRect();
          if (bounding) {
            style['right'] = `${window.innerWidth - bounding.right}px`;
          }
        }
      }
    }
    return style;
  }

  get isShowBubble() {
    if (this.showQuizResult) {
      return false;
    }

    if (this.elements.length && this.selectedElement) {
      return this.selectedElement.kind === ElementTypeKind.Video || this.selectedElement.kind === ElementTypeKind.PowerPointSlideStep;
    }
    return false;
  }

  get isShowTypeActionBubble() {
    if (this.showQuizResult) {
      return false;
    }

    if (this.elements.length && this.selectedElement) {
      return ((this.selectedElement as StepElement).action === ActionKind.Type
        && (this.simulationType === SimulationType.Interactive || this.simulationType === SimulationType.Test));
    }
    return false;
  }

  get setPositionForBubble() {
    let style = {};
    if (this.btnNext && this.btnNext.nativeElement) {
      let bounding = this.btnNext.nativeElement.getBoundingClientRect();
      if (bounding) {
        let windowWidth = window.innerWidth;
        let right = windowWidth - bounding.right;
        if (right <= 0) {
          right = 1;
        }
        style['right'] = `${right}px`;
      }
    }
    return style;
  }

  get setPositionForTypeBubble() {
    let style = {};

    let button: ElementRef = null;
    if (this.simulationType === SimulationType.Interactive) {
      button = this.btnNextStep;
    } else {
      button = this.btnNext;
    }

    if (button && button.nativeElement) {
      let bounding = button.nativeElement.getBoundingClientRect();
      if (bounding) {
        let windowWidth = window.innerWidth;
        style['right'] = `${windowWidth - bounding.right}px`;
      }
    }
    return style;
  }

  private initStepInstructionFontSize() {
    this.stepInstructionFontSize = this.simulationSettings.stepInstructionSetting.stepInstructionFontSize;
    if (!this.stepInstructionFontSize) {
      this.stepInstructionFontSize = Constants.STEP_TITLE_FONT_SIZE;
    }
  }
}

enum SimulationElementViewMode {
  Normal = 1,
  AnswerCorrect = 2,
  AnswerWrong = 3
}



