export class StepInstructionSetting {
  stepInstructionColor: string = '';
  noteColor: string = '';
  clickHighlightColor: string = '';
  stepInstructionFont: string = '';
  stepInstructionFontSize: string = '';
  stepInstructionFontColor: string = '';
  isCommonFont: boolean = true;

  fromJson(json: any) {
    if (!json) {
      return this;
    }

    this.stepInstructionColor = json.stepInstructionColor;
    this.noteColor = json.noteColor;
    this.clickHighlightColor = json.clickHighlightColor;
    this.stepInstructionFontSize = json.stepInstructionFontSize;
    this.stepInstructionFontColor = json.stepInstructionFontColor;
    this.stepInstructionFont = json.stepInstructionFont;
    this.isCommonFont = json.isCommonFont;
    return this;
  }

  toJson() {
    return {
      stepInstructionColor: this.stepInstructionColor,
      noteColor: this.noteColor,
      clickHighlightColor: this.clickHighlightColor,
    }
  }
}



