import { ModuleWithProviders, NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { MessageDialog } from './dialogs/message.dialog';
import { TrueFalseQuizDisplayComponent } from './component/recording/quiz-element-display/true-false-quiz-display.component';
import { FillInTheBlankQuizOptionComponent } from './component/recording/quiz-element-display/quiz-option/fill-in-the-blank-quiz-option.component';
import { MultipleQuizOptionComponent } from './component/recording/quiz-element-display/quiz-option/multiple-quiz-option.component';
import { MultipleQuizDisplayComponent } from './component/recording/quiz-element-display/multiple-quiz-display.component';
import { FillInTheBlankQuizDisplayComponent } from './component/recording/quiz-element-display/fill-in-the-blank-quiz-display.component';
import { MatInputModule } from '@angular/material/input';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatButtonModule, MatDialogModule, MatSnackBarModule } from '@angular/material';
import { MatSelectModule } from '@angular/material/select';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { SecurityTrustHtmlPipe } from './pipes/securityTrustHtml.pipe';
import { MatRadioModule } from '@angular/material/radio';
import { DocumentLinkDisplayComponent } from './component/recording/document-link-display.component';
import { NoWhiteSpace } from './pipes/noWhiteSpace.pipe';
import { VideoSecurityTrustHtmlPipe } from './pipes/videoSecurityTrustHtml.pipe';
import { DisableContextMenuDirective } from './directives/disable-default-context-menu.directive';

@NgModule({
  declarations: [
    MessageDialog, TrueFalseQuizDisplayComponent, MultipleQuizDisplayComponent, SecurityTrustHtmlPipe,
    MultipleQuizOptionComponent, FillInTheBlankQuizOptionComponent, FillInTheBlankQuizDisplayComponent,
    DocumentLinkDisplayComponent, NoWhiteSpace, VideoSecurityTrustHtmlPipe, DisableContextMenuDirective
  ],
  imports: [CommonModule, RouterModule, MatInputModule, FormsModule, ReactiveFormsModule,
    MatFormFieldModule, MatSelectModule, MatButtonModule, MatRadioModule, MatSnackBarModule,
    MatDialogModule
  ],
  exports: [CommonModule, RouterModule,FormsModule, MessageDialog, TrueFalseQuizDisplayComponent, MatInputModule, MatSnackBarModule,
    MatDialogModule, MultipleQuizDisplayComponent, MultipleQuizOptionComponent, FillInTheBlankQuizOptionComponent, MatRadioModule,
    FillInTheBlankQuizDisplayComponent, MatFormFieldModule, MatSelectModule, MatButtonModule, SecurityTrustHtmlPipe, NoWhiteSpace,
    DocumentLinkDisplayComponent, VideoSecurityTrustHtmlPipe, DisableContextMenuDirective],
  entryComponents: [
    MessageDialog
  ],
})
export class SharedModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: SharedModule
    };
  }
}



