import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Constants } from '../shared/helper/constants';
import { Observable } from 'rxjs/Observable';
import { SiteThemeColors } from '../model/site-theming.model';

@Injectable()
export class SiteThemingService {
  private mainAccentColorLabel = '%%MainAccent%%';
  private linkActionColorLabel = '%%LinkAction%%';
  private linkActionRolloverColorLabel = '%%LinkActionRollover%%';
  private siteThemeId = 'site-theme-id';
  private siteThemeDynamicCss: string = `
  // Template
    .light-blue {
        color: ${this.linkActionColorLabel} !important;
    }
    .link-action, a.link-action {
      color: ${this.linkActionColorLabel} !important;
    }
    
    .link-action:hover, a.link-action:hover {
      color: ${this.linkActionRolloverColorLabel};
    }
    
    .main-accent-color {
      color: ${this.mainAccentColorLabel} !important;
    }
    
    .link-action svg path,
    .show-hide-category svg path {
      fill: ${this.linkActionColorLabel};
    }
    
    .active, .add-text span, .add-text i, 
    .department .department-list-container .department-list-header .department-list-button-header a .mdi,
    .department .department-list-container .department-list-header .department-list-button-header a,
    .sidenav a.active-link, 
    .category-container .category-list-add-new-header .category-list-add-new-button-header a, 
    .category-list-container .category-list-add-new-header .category-list-add-new-button-header a,
    .customer-user-invitation .doc-mgr-permission i.mdi-checkbox-marked,
    .layout-getting-started .doc-mgr-permission i.mdi-checkbox-marked,
    .category-container .category-list-add-new-header .category-list-add-new-button-header a .mdi, 
    .category-list-container .category-list-add-new-header .category-list-add-new-button-header a .mdi,
    .import-document-container .check-override-metadata i.mdi-checkbox-marked,
    .deleted-document-manager-container .list-action a.icon-selected .mdi,
    .step-panel .step-contexts-wrapper .step-contexts .step-context-process span, .plan-pricing .preview-editor h1,
    .billing-plan h1.access-plan:not(.white-color) {
      color: ${this.mainAccentColorLabel} !important;
    }
    
    .mat-button:focus, .mat-button:hover, .mat-button:active:focus,
    .btn-warning-white-text, .font-style-buttons .mdi.active, .billing-plan .active, .layout-getting-started .active {
        background-color: ${this.mainAccentColorLabel};
    }
    
    .content-bar-card-container.active {
      border: 3px ${this.mainAccentColorLabel} solid !important;
    }
    
    a.light-blue.text-uppercase.text-bold {
      color: ${this.linkActionColorLabel};
    }
    
    .progress-button button {
      background-color: ${this.mainAccentColorLabel} !important;
      border: 1px solid ${this.mainAccentColorLabel} !important;
    }
    
    .progress-button button:hover {
      background-color: ${this.mainAccentColorLabel} !important;
    }
    
    .btn-warning {
      background-color: ${this.mainAccentColorLabel} !important;
    }
    
    a:hover {
      color: ${this.linkActionRolloverColorLabel} !important;
    }
    
    .header-item, .card-title, project-card .material-icons,
    .light-blue, .custom-attribute-settings-container .sub-menu .active *,
    .element-settings-container .sub-menu .active *,
    .document-settings-container .sub-menu .active *,
    .document-manager .document-manager-project-link,
    .box-container .box .box-body .box-row,
    .box-container .box .box-footer a,
    .box-container .box .box-body .divTable .divTableCell,
    .link a, .link-content a,
    .custom-attribute-settings-container .sub-menu .active *,
    .document-downloader, .document-action .action-list .action-document-content,
    .document-action .action-list .action-document-icon, .text-primary,
    .rating-and-comment .comment-container .comment .comment-footer .remove,
    .comment-queue .divTableRow .divTableCell a,
    .qa-issues-container .qa-issues-button > * > span,
    .view-qa-issue-dialog .fix-issue-button,
    .add-recording-dialog .main-confirm-button-area > * strong,
    .add-recording-dialog .main-confirm-button-area > * i,
    .cancel-account-contact a,
    .content-list-row .add-to-tenant-content-container i,
     .fork-element-editor .material-icons {
        color: ${this.linkActionColorLabel} !important;
    }
    
    .comment-queue .comment-queue-highlight-row.divTableRow .divTableCell a {
        color: #fff !important;
    }
    .header-item:hover, .card-title-hover:hover, span.light-blue:hover, 
    .element-settings-container .sub-menu :first-child:hover,
    .document-settings-container .sub-menu :first-child:hover,
    .document-manager .document-manager-project-link:hover,
    .box-container .box .box-body .box-row:hover,
    .box-container .box .box-footer a:hover,
    .box-container .box .box-body .divTable .divTableCell:hover,
    .link a:hover, .link-content a:hover, div.light-blue:hover,
    .custom-attribute-settings-container .sub-menu :first-child:hover,
    .document-downloader:hover, .document-action .action-list .action-document-content:hover,
    strong.light-blue:hover, .comment-queue .divTableRow .divTableCell a:hover,
    .qa-issues-container .qa-issues-button > * > span:hover,
    .view-qa-issue-dialog .fix-issue-button:hover, li.light-blue:hover,
    .add-recording-dialog .main-confirm-button-area > * strong:hover,
    .cancel-account-contact a:hover {
        color: ${this.linkActionRolloverColorLabel} !important;
    }

    .register-page .step-register ul .active a,
    .user-getting-started .step-action ul .active a {
      background: ${this.mainAccentColorLabel} !important;
    }

    .single-sign-on-settings-container .single-sign-on-settings-main .single-sign-on-settings-main-instruction .file-instruction .link-download-instruction .icon-download-instruction:hover,
    .single-sign-on-settings-container .single-sign-on-settings-main .single-sign-on-settings-main-instruction .file-instruction .link-download-instruction .text-download-instruction:hover {
        color: ${this.linkActionRolloverColorLabel} !important;
    }
    
    .single-sign-on-settings-container .single-sign-on-settings-main .single-sign-on-settings-main-instruction .file-instruction .link-download-instruction .icon-download-instruction,
    .single-sign-on-settings-container .single-sign-on-settings-main .single-sign-on-settings-main-instruction .file-instruction .link-download-instruction .text-download-instruction {
        color: ${this.linkActionColorLabel} !important;
    }


    .single-sign-on-settings-container .single-sign-on-settings-main .single-sign-on-settings-main-instruction .file-instruction .link-download-instruction .text-download-instruction:hover {
        color: ${this.linkActionRolloverColorLabel}
    }
    
    .box-container .box .box-body .divTable .divTableBody .divTableRow:hover .divTableCell {
      color: ${this.linkActionRolloverColorLabel}
    }
    
    .mat-progress-spinner svg path {
      stroke: ${this.mainAccentColorLabel} !important;
    }
    
    .checkbox-main-accent {
      color: ${this.mainAccentColorLabel} !important;
    }
        
    .uil-spin-css .path {
      stroke: ${this.mainAccentColorLabel} !important;
    }
              
    .box-container .box .box-body .box-row:hover {
    background-color: #F7F7F7;
      color: ${this.linkActionRolloverColorLabel};
    }
    
    .mat-radio-inner-circle, .notifications .media-left .badge {
      background: ${this.mainAccentColorLabel};
    }
    
    .mat-radio-checked .mat-radio-outer-circle {
      border-color: ${this.mainAccentColorLabel};
    }
    
    .doctype-container .active,
    .task-detail-container-style .task-detail .hide-application-tree span,
    .task-detail-container-style .task-detail .row .action-document-content,
    .task-detail-container-style .task-detail .row .action-document-icon {
      color: ${this.linkActionColorLabel};
    }
    
    .success.progress-button button {
      border-color: ${this.mainAccentColorLabel};
      background-color: ${this.mainAccentColorLabel};
    }

    .doctype-container .active:hover,
    .task-detail-container-style .task-detail .hide-application-tree span:hover,
    .task-detail-container-style .task-detail .row .action-document-content:hover {
      color: ${this.linkActionRolloverColorLabel};
    }
    
    .mat-checkbox-checked.mat-primary .mat-checkbox-background {
      background-color: ${this.mainAccentColorLabel}
    }
    
    .mat-checkbox-checked.mat-accent .mat-checkbox-background {
      background-color: ${this.mainAccentColorLabel} !important;
    }
    
    .success.progress-button button {
      border-color: ${this.mainAccentColorLabel};
      background-color: ${this.mainAccentColorLabel};
    }
    
    .document-manager .list-action a.selected .mdi, .document-manager .list-action .document-branches .divTableRow.expanded.branch-selected a.divTableCell .mdi,  .document-manager .document-branches .divTableRow.expanded.branch-selected .list-action a.divTableCell .mdi, .document-manager .list-action .document-manager-hight-light-row > a.divTableCell .mdi,
    .document-advisor-context .document-context-list .list-item .item .process-name {
      color: ${this.mainAccentColorLabel}
    }
    
    .progress-button svg.progress-circle path {
      stroke: ${this.mainAccentColorLabel} !important;
    }
    
    .static-element-fork .static-content-item-title a,
    .static-element-fork .static-content-item-title a span,
    .static-element-fork .static-content-item-title a span strong {
        color: ${this.linkActionColorLabel} !important;
    }
    
    .static-element-fork .static-content-item-title a:hover,
    .static-element-fork .static-content-item-title a:hover span,
    .static-element-fork .static-content-item-title a:hover span strong {
        color: ${this.linkActionRolloverColorLabel} !important;
    }
    
    .accent-color {
      color: ${this.mainAccentColorLabel} !important;
    }
    
    .related-contents-container .highLight {
      background-color: ${this.mainAccentColorLabel} !important;
    }

    .step-element-loading-wheel-icon:after,
    .recording-edit-loading-wheel-icon:after {
      border-color: ${this.mainAccentColorLabel} ${this.mainAccentColorLabel} ${this.mainAccentColorLabel} transparent !important;
    }

  `;

  constructor() {
  }

  loadDynamicCss(colors: SiteThemeColors) {
    let css = this.siteThemeDynamicCss
      .replace(new RegExp(this.mainAccentColorLabel, "g"), colors.mainAccent)
      .replace(new RegExp(this.linkActionColorLabel, "g"), colors.linkAction)
      .replace(new RegExp(this.linkActionRolloverColorLabel, "g"), colors.linkActionRollover);

    let head = document.getElementsByTagName('head')[0];
    let s = document.createElement('style');
    s.id = this.siteThemeId;
    s.setAttribute('type', 'text/css');
    if ((s as any).styleSheet) {   // IE
      (s as any).styleSheet.cssText = css;
    } else {                // the world
      s.appendChild(document.createTextNode(css));
    }

    head.appendChild(s);
  }

}



