import { Component, Input, ViewChild, ElementRef, OnInit, OnDestroy, SimpleChanges, EventEmitter, Output, OnChanges } from '@angular/core';

import { Observable, Subscription } from 'rxjs';
import { CoreDocumentElement, DocumentElement, ElementTypeKind, PublicLibService, SectionElement, StepElement, VideoElement } from 'opus-content-lib';

import { BookContentTypeModel, BookModel } from '../../../model/book.model';
import { SimulationSetting } from '../../../model/simulation.settings.model';
import { ContentSimulationElementComponent } from '../../content-simulation-element/content-simulation-element.component';
import { DocumentMetadataModel, DocumentViewModel } from '../../../model/document.model';
import { OfflineService } from '../../../services/offline.service';
import { Utility } from '../../../shared/helper/utilities';
import { DocumentTypeModel } from '../../../model/document-type.model';

declare var pipwerks: any;

@Component({
  selector: 'book-content-detail',
  templateUrl: 'book-content-detail.component.html',
  styleUrls: ['book-content-detail.component.scss']
})
export class BookContentDetailComponent implements OnInit, OnChanges, OnDestroy {
  @Input() element: DocumentElement = new DocumentElement();
  @Input() document = new DocumentMetadataModel();
  @Input() book: BookModel = new BookModel();
  @Input() selectedDocType: DocumentTypeModel = new DocumentTypeModel();
  @Input() simulationSettings : SimulationSetting = new SimulationSetting();
  @Input() requiredContentTypes : BookContentTypeModel[] = [];
  @ViewChild('content') content: ElementRef;
  
  @ViewChild('contentSimulation') contentSimulation: ContentSimulationElementComponent;
  selectingDoc = new DocumentViewModel();
  isEAssistantDoctype: boolean = false;
  isSimulationDoctypes: boolean = false;
  simulationType: number = 0;
  isExistEassistant: boolean = false;
  intervalPushElements: Subscription = new Subscription();
  isLoading : boolean = false;
  elements: DocumentElement [] = [];
  flattenedElements: any;
  selectedDoctype: DocumentTypeModel = new DocumentTypeModel();
  isFirstLoading: boolean = true; 
  lmsConnected : boolean = false;

  constructor(private offlineService: OfflineService, private publicLibService: PublicLibService) {
  }

  ngOnInit(){
    try {
      this.lmsConnected = pipwerks.SCORM.init();
    } 
    catch (ex) {
      console.log(ex);
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['element'] && changes['element'].currentValue) {
      this.selectingDoc.elements = [];
      this.selectingDoc.metadata = {};
      this.selectingDoc.url = '';
      this.selectingDoc.type = '';
      setTimeout(() => {
        if (this.element instanceof CoreDocumentElement) {

          let coreElement = this.element as CoreDocumentElement;
          this.offlineService.getElements(coreElement.links[0].documentName, coreElement.links[0].documentId).subscribe(res => {
            let useVariants = this.selectedDocType.documentTypeSetting.useFieldTable;
            res = res.map((el) => DocumentElement.getActiveElement(el)).filter(el => el !== null);
            res = useVariants ? DocumentElement.convertToElementsConsolidateTable(res) : res;
            let filteredElements = this.filterElementByDocType(this.selectedDocType, res);
            filteredElements.forEach(element => {
              this.replaceResource(element);
            });
            this.selectingDoc.elements = filteredElements;
          });
          this.offlineService.getMetadata(coreElement.links[0].documentName, coreElement.links[0].documentId).subscribe(res => {
            this.selectingDoc.metadata = res;
            if (coreElement && coreElement.links[0] && coreElement.links[0].title){
              this.selectingDoc.metadata.title = coreElement.links[0].title;
            }
          });
          this.selectingDoc.type = coreElement.links[0].type;

          if (this.selectingDoc.type === 'Pdf') {
            this.offlineService.getDocumentInfo(coreElement.links[0].documentName, coreElement.links[0].documentId).subscribe(res => {
              this.selectingDoc.url = `./data/resource/${res.name}.pdf#view=fith`;
            });
          } else if (this.selectingDoc.type === 'Webpage') {
            this.offlineService.getDocumentInfo(coreElement.links[0].documentName, coreElement.links[0].documentId).subscribe(res => {
              this.selectingDoc.url = `./data/resource/${res.name}/index.html`;
            });
          } else {
            this.selectingDoc.url = coreElement.links[0].url;
          }
        }
        else if (this.element instanceof SectionElement) {
          let sectionElement = this.element as SectionElement;
          this.selectingDoc.url = sectionElement && sectionElement.links[0] ? sectionElement.links[0].url : '';
        }

        if (this.selectedDocType) {
          this.selectedDoctypeChanged(this.selectedDocType);
        }
      });     
    }

    if (changes['selectedDocType'] && changes['selectedDocType'].currentValue) {
      this.isLoading = true;
      this.selectedDoctypeChanged(this.selectedDocType);
      setTimeout(() => {
        this.isLoading = false;
      }, 10);
    }
  }

  replaceResource(element: DocumentElement) {
    let coreElement = element as any;
    if (coreElement.links && coreElement.links[0]) {
      if (coreElement.kind === ElementTypeKind.DocumentLink && (coreElement.links[0].type === 'Pdf' || coreElement.links[0].url.indexOf('.pdf') >= 0)) {
        coreElement.links[0].url = `./data/resource/${coreElement.links[0].documentName}.pdf#view=fith`;
      } else if (coreElement.kind === ElementTypeKind.DocumentLink && (coreElement.links[0].type === 'Webpage' || coreElement.links[0].url.indexOf('.html') >= 0)) {
        let fileHtml = coreElement.links[0].url.split('/').reverse()[0];
        coreElement.links[0].url = `./data/resource/${coreElement.links[0].documentName}/${fileHtml}`;
      }
    }
    if (element.subElements && element.subElements.length > 0) {
      element.subElements.forEach(subElement => {
        this.replaceResource(subElement);
      })
    }
  }

  filterElementByDocType(selectedDoctype: DocumentTypeModel, inputElements: DocumentElement[]): DocumentElement[] {
    let elements = this.getAllElementsByDoctype(selectedDoctype, inputElements);
    let filterFn = (element: DocumentElement): boolean => {
      let result = true;

      if (element.kind === ElementTypeKind.Step) {
        result = !(element as StepElement).isBuildAStepElement;
      }
      if (element.kind === ElementTypeKind.Video && !((element as VideoElement).videoUrl)) {
        result = false;
      }
      return result;
    };

    elements = elements.filter((element) => {
      if (element.kind === ElementTypeKind.Group) {
        element.subElements = element.subElements.filter((sub) => filterFn(sub));
      }
      if (element.kind === ElementTypeKind.Fork) {
        element.subElements.forEach(sub => {
          if (sub.subElements.length > 0) {
            sub.subElements = sub.subElements.filter((step) => filterFn(step));
          }
        });
      }
      return filterFn(element);
    });

    return elements;
  }


  getAllElementsByDoctype(doctype: DocumentTypeModel, elements: DocumentElement[]) {
    let filterFn = (eles: DocumentElement[], selectedDoctype: DocumentTypeModel) => {
      let filterElements: DocumentElement [] = [];
      if (eles && eles.length > 0) {
        filterElements = this.filterElementsByDoctype(eles, selectedDoctype);
      }
      return filterElements;
    };
    return filterFn(elements, doctype);
  }

  filterElementsByDoctype(elements: DocumentElement [], currentDoctype: DocumentTypeModel, allDocTypes?: DocumentTypeModel[], inEditor?: boolean): DocumentElement [] {
    return this.publicLibService.filterElementsByDoctype(elements, currentDoctype, allDocTypes, inEditor);
  }

  scrollContentToTop(element: DocumentElement) {
    if (this.content && element) {
      let htmlContent = this.content.nativeElement as HTMLElement;
      htmlContent.scrollTop = 0;
    }
  }

  get isCoreElement() {
    let isCoreElement = this.element instanceof CoreDocumentElement;
    return isCoreElement;
  }

  selectedDoctypeChanged(selectedItem: DocumentTypeModel, isCustomScroll: boolean = false) {
    if (!this.isExistEassistant || this.isFirstLoading) {
      this.selectedDoctype = selectedItem;
      this.isFirstLoading = false;
      if (this.contentSimulation) {
        this.contentSimulation.resetFlag();
      }

      this.isEAssistantDoctype = selectedItem.isEAssistantDoctype;

      this.isSimulationDoctypes = this.selectedDoctype.isSimulation;
      this.simulationType = this.isSimulationDoctypes ? selectedItem.simulationType : 0;

      let elements = this.getAllElementsByDoctype(this.selectedDoctype,this.selectingDoc.elements);
      let filterFn = (element: DocumentElement): boolean => {
        let result = true;

        if (element.kind === ElementTypeKind.Step) {
          result = !(element as StepElement).isBuildAStepElement;
        }
        if (element.kind === ElementTypeKind.Video && !((element as VideoElement).videoUrl)) {
          result = false;
        }
        return result;
      };

      elements = elements.filter((element) => {
        if (element.kind === ElementTypeKind.Group) {
          element.subElements = element.subElements.filter((sub) => filterFn(sub));
        }
        if (element.kind === ElementTypeKind.Fork) {
          element.subElements.forEach(sub => {
            if (sub.subElements.length > 0) {
              sub.subElements = sub.subElements.filter((step) => filterFn(step));
            }
          });
        }
        return filterFn(element);
      });
      this.lazyPushElements(elements);
      if (this.content && !isCustomScroll) {
        (this.content.nativeElement as HTMLElement).scrollTop = 0;
      }
    }   
   if(!this.isSimulationDoctypes){
    this.offlineService.setRequiredTypeVisited(selectedItem.id);
    }

  }

  lazyPushElements(elements: DocumentElement[]) {
    this.intervalPushElements.unsubscribe();
    this.elements = [];
    this.flattenedElements = [];
    if (DocumentTypeModel.isStaticDocType(this.selectedDoctype)) {
      let lastLoadedIndex = 0;
      this.intervalPushElements = Observable.interval(50).subscribe(() => {
        if (lastLoadedIndex < elements.length) {
          lastLoadedIndex = lastLoadedIndex + 1;
          this.elements = elements.slice(0, lastLoadedIndex);
          this.flattenedElements = Utility.flatternArray(this.elements.map(e => {
            return Utility.filterDocumentElement(e);
          }));
        } else {
          this.intervalPushElements.unsubscribe();
        }
      });
    }
    else
    {
      this.elements = elements;
      this.flattenedElements = Utility.flatternArray(this.elements.map(e => {
        return Utility.filterDocumentElement(e);
      }));
    }
  }

  selectDoctypeChange(type: DocumentTypeModel) {
    this.selectedDocType = type;
    this.isSimulationDoctypes = this.selectedDocType.isSimulation;
    this.simulationType = this.isSimulationDoctypes ? this.selectedDocType.simulationType : 0;
    this.selectedDoctypeChanged(this.selectedDocType);
    }

    simulationTypeVisited(simulationType : number){
      this.offlineService.setRequiredTypeVisited(simulationType);
    }

    ngOnDestroy() {
      var visitedTypes = this.requiredContentTypes.filter(t=>t.isVisited == true);
      if(visitedTypes.length != 0 && visitedTypes.length == this.requiredContentTypes.length)
      {
        if(this.lmsConnected)
          pipwerks.SCORM.completeLesson();
      }
    }
}



