import { AfterViewInit, Component, ElementRef, EventEmitter, Input, OnChanges, Output, SimpleChanges, ViewChild } from '@angular/core';
import { DocumentElement, PowerPointSlideStepElement, StepElement } from '../../../model/document-element.model';
import { SimulationSetting } from '../../../model/simulation.settings.model';
import { QuizElementReportModel } from '../../../model/quiz-element-report.model';
import { DocumentTypeModel, SimulationType } from '../../../model/document-type.model';
import { ElementTypeCategory, ElementTypeKind } from '../../../model/element-type.model';
import { AuthoringService } from '../../../services/authoring.service';
import { ForkElement } from 'opus-content-lib';
import { Utility } from '../../../shared/helper/utilities';
import { ActionKind } from '../../../model/recorder-event.model';
import {Rect} from '../../../model/rect.model';


const interact = require('interactjs');

@Component({
  selector: 'content-simulation-steps',
  templateUrl: 'content-simulation-steps.component.html',
  styleUrls: ['content-simulation-steps.component.scss']
})

export class ContentSimulationStepsComponent implements OnChanges, AfterViewInit {
  @Input() elements: DocumentElement [] = [];
  @Input() simulationType: number = 0;
  @Input() simulationSettings: SimulationSetting = new SimulationSetting();
  @Input() showQuizResult: boolean = false;
  @Input() quizReport: QuizElementReportModel = new QuizElementReportModel();
  @Input() selectedDoctype: DocumentTypeModel = null;
  @Output() selectedElementChanged: EventEmitter<[DocumentElement, number]> = new EventEmitter<[DocumentElement, number]>();
  @Output() controlSimulationEmit: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Output() viewQuizReportDetail: EventEmitter<[DocumentElement, number]> = new EventEmitter<[DocumentElement, number]>();
  @Output() goToOtherElement: EventEmitter<any> = new EventEmitter<any>();
  @Output() viewQuizReportSummarize: EventEmitter<any> = new EventEmitter<any>();
  @Output() selectedChildStepChanged: EventEmitter<[number, number, number]> = new EventEmitter<[number, number, number]>();
  @Input() currentStepIndex: number = 1;
  @Input() isShowStartOverPopup: boolean = false;
  @Input() isShowStepList: boolean = false;
  @Input() disableFeature: boolean = false;
  @Output() closeBoxEmit: EventEmitter<boolean> = new EventEmitter<boolean>();
  @ViewChild('stepListContainer') stepListContainer: ElementRef;
  @ViewChild('content') content: ElementRef;
  @ViewChild('containerStepList') containerStepList: ElementRef;

  selectedElement: DocumentElement = new DocumentElement();
  showStepList: boolean = true;
  isPlaySimulation: boolean = true;
  elementListViewModes: { [id: string]: SimulationElementViewMode; } = {};
  langDisplay: any;
  elementKind: typeof ElementTypeKind = ElementTypeKind;
  langDisplayBuildAStep: any;
  simulationElementViewMode: typeof SimulationElementViewMode = SimulationElementViewMode;
  timeOutScroll: any = null;

  constructor(private authoringService: AuthoringService) {
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['elements'] && changes['elements'].currentValue) {
      if (this.elements && this.elements.length) {
        this.elements = this.elements.filter( (element) => {
          if (element.kind === ElementTypeKind.Fork && (element as ForkElement).isEmptyFork) {
            return false;
          }
          return true;
        });
        this.initViewModes(this.elements);
        this.selectedElement = this.elements[this.currentStepIndex];
      }
    }

    if (changes['currentStepIndex'] && changes['currentStepIndex'].currentValue) {
      if (this.elements && this.elements.length) {
        this.selectedElement = this.elements[this.currentStepIndex];
      }
    }

    if (changes['isShowStepList']) {
      setTimeout(() => {
        this.scrollToStep();
        if (this.isShowStepList && !this.disableFeature) {
          this.handleSetAvoidOverlapControlArea();
        } else if (!this.isShowStepList) {
          this.containerStepList.nativeElement.style['z-index'] = '0';
        }
      }, 10);
    }
  }

  handleSetAvoidOverlapControlArea() {
    const containerPreventMoveAble = document.getElementById('prevent-move-able');
    this.resetInteract();
    this.containerStepList.nativeElement.style.top = '';
    this.containerStepList.nativeElement.style.left = '';
    const controlRect = (this.selectedElement as StepElement).controlRect ? (this.selectedElement as StepElement).controlRect : new Rect();
    const elementRect = this.containerStepList.nativeElement.getBoundingClientRect();
    const result = Utility.calculateRectAvoidOverlap(controlRect, elementRect, containerPreventMoveAble.offsetWidth, containerPreventMoveAble.offsetHeight);
    if (result.rect.top) {
      this.containerStepList.nativeElement.style.top = result.rect.top + 'px';
    }
    if (result.rect.left) {
      this.containerStepList.nativeElement.style.left = result.rect.left + 'px';
    }

    setTimeout(() => {
      this.containerStepList.nativeElement.style['z-index'] = '1000';
    }, 100);
  }

  resetInteract() {
    this.containerStepList.nativeElement.removeAttribute('data-x');
    this.containerStepList.nativeElement.removeAttribute('data-y');
    this.containerStepList.nativeElement.removeAttribute('style');
  }

  ngAfterViewInit() {
    interact('.step-list-box-container')
      .draggable({
        inertia: true,
        restrict: {
          restriction: '.prevent-move-able',
          endOnly: false,
          elementRect: {top: 0, left: 0, bottom: 1, right: 1}
        },
        autoScroll: false,
        allowFrom: '.mdi-drag',
        onmove: (event: any) => {
          Utility.dragMoveListener(event);
        }
      });
  }

  getElementTitle(e: DocumentElement): string {
    if (e.kind === ElementTypeKind.Step) {
      let step = e as StepElement;

      step.displayText = this.stripControlImageFromHtmlString(step.displayText);
      if (step.convertedFromFrame) {
        const title = this.langDisplayBuildAStep && this.langDisplayBuildAStep[step.title] ? this.langDisplayBuildAStep[step.title] : step.title;
        return this.removeControlImage(title);
      }
      //
      if (step.action === ActionKind.Change || step.action === ActionKind.Type) {
        let isContainTable = step.displayText.indexOf('<table>') < step.displayText.indexOf('</table>');
        const title = isContainTable ? step.variantTitle : step.displayText;
        return this.removeControlImage(title);
      } else {
        return this.removeControlImage(step.displayText);
      }
    } else if (e.kind === ElementTypeKind.PowerPointSlideStep) {
      return (e as PowerPointSlideStepElement).slideTitle;
    }

    return e.name;
  }

  removeControlImage(txt: string) {
    return txt.replace(/<img.*?class="controlimage"[^\>]+>/g, '');
  }

  stripControlImageFromHtmlString(htmlString: string) {
    let div = document.createElement('div');
    div.style.display = 'none';
    div.innerHTML = htmlString;

    let controlImageClasses = div.getElementsByClassName('controlimage');

    if (controlImageClasses.length > 0) {
      controlImageClasses[0].remove();
    }

    return div.innerHTML;
  }

  getStepIndex(index: number) {
    if (this.elements[index].kind !== ElementTypeKind.Step
      && this.elements[index].kind !== ElementTypeKind.PowerPointSlideStep) {
      return null;
    }

    let stepIndex = this.elements.filter(e => {
      return [ElementTypeKind.Step, ElementTypeKind.PowerPointSlideStep].indexOf(e.kind) !== -1;
    }).findIndex(e => e.id === this.elements[index].id);

    return stepIndex + 1;
  }

  selectedElementChange(selectedElement: DocumentElement, index: number) {
    this.selectedElement = selectedElement;
    if (this.isShowStepList) {
      setTimeout(() => {
        this.scrollToStep();
      }, 10);
    }
    this.selectedElementChanged.emit([selectedElement, index]);
  }

  onChangeSimulationControlStatus(flag: boolean) {
    this.isPlaySimulation = flag;
    this.controlSimulationEmit.emit(this.isPlaySimulation);
  }

  onGoToOtherElement(step: number) {
    this.goToOtherElement.emit(step);
    this.onChangeSimulationControlStatus(false);
  }

  initViewModes(elements: DocumentElement []) {
    elements.forEach(e => {
      this.elementListViewModes[e.id] = SimulationElementViewMode.Normal;
      if (e.subElements && e.subElements.length)
      {
        this.initViewModes(e.subElements);
      }
    });
  }

  onReportQuiz() {
    this.quizReport.results.forEach(r => {
      this.elementListViewModes[r.id] = r.result ? SimulationElementViewMode.AnswerCorrect
        : SimulationElementViewMode.AnswerWrong;
    });
  }

  actionClickToListStep(index: number) {
    if (index < 0) {
      return;
    }

    if (index >= this.elements.length) {
      return;
    }

    if (!this.showQuizResult) {
      // Go to other step
      if (this.simulationType !== SimulationType.Test) {
        this.onGoToOtherElement(index);
      }
    } else {
      // view Detail report
      let elCategory = this.authoringService.getElementTypeById(this.elements[index].elementTypeId).category;

      if ([ElementTypeCategory.Quiz, ElementTypeCategory.Step].indexOf(elCategory) === -1) {
        return;
      }
      this.selectedElement = this.elements[index];
      this.viewQuizReportDetail.emit([this.elements[index], index]);
    }
  }

  onViewReportSummarize() {
    this.viewQuizReportSummarize.emit();
  }

  goToIssue(e: DocumentElement) {
    if (!e) {
      return;
    }

    let idx = this.elements.findIndex((el) => el.id === e.id);

    if (idx === -1) {
      return;
    }

    this.onChangeSimulationControlStatus(false);
    this.selectedElementChange(e, idx);
  }

  actionClickToChildStep(i: number, pathIndex: number, childIndex: number) {
    event.preventDefault();
    event.stopPropagation();
    if (!this.showQuizResult) {
      // Go to other step
      this.onChangeSimulationControlStatus(false);
      this.selectedChildStepChanged.emit([i, pathIndex, childIndex]);
    } else {
      // view Detail report
      let elCategory = this.authoringService.getElementTypeById(this.elements[i].subElements[pathIndex].subElements[childIndex].elementTypeId).category;

      if ([ElementTypeCategory.Quiz, ElementTypeCategory.Step].indexOf(elCategory) === -1) {
        return;
      }
      this.selectedElement = this.elements[i].subElements[pathIndex].subElements[childIndex];
      this.viewQuizReportDetail.emit([this.elements[i].subElements[pathIndex].subElements[childIndex], i]);
      this.selectedChildStepChanged.emit([i, pathIndex, childIndex]);
    }
  }

  preventDefault() {
    event.preventDefault();
    event.stopPropagation();
  }

  closeBox() {
    this.closeBoxEmit.emit(true);
  }

  calcTop(el: any) {
    if (!el) {
      return 0;
    }
    if (el.classList.contains('step-element-flag')) {
      return el.offsetTop;
    }

    return el.offsetTop + this.calcTop(el.offsetParent);
  }

  scrollToStep() {
    if (this.content && this.content.nativeElement && !this.showQuizResult) {
      let nativeElement = this.content.nativeElement;
      let el = nativeElement.querySelector(`.selected`);
      if (el) {
        let top = this.calcTop(el);
        this.scrollTo(this.content, top, 1000);
      }
    }
  }

  scrollTo(element: ElementRef, top: number, duration: number) {
    let startTop = element.nativeElement.scrollTop,
      changeTop = top - startTop,
      currentTime = 0,
      increment = 20;
    if (this.timeOutScroll) {
      clearTimeout(this.timeOutScroll);
    }
    // t = current time
    // b = start value
    // c = change in value
    // d = duration
    let easeInOutQuad = (t: number, b: number, c: number, d: number) => {
      t /= d / 2;
      if (t < 1) {
        return c / 2 * t * t + b;
      }
      t--;
      return -c / 2 * (t * (t - 2) - 1) + b;
    };

    let animateScroll = () => {
      currentTime += increment;

      let offsetTop = easeInOutQuad(currentTime, startTop, changeTop, duration);
      element.nativeElement.scrollTop = offsetTop;

      if (currentTime < duration) {
        this.timeOutScroll = setTimeout(animateScroll, increment);
      }
    };

    animateScroll();
  }
}

enum SimulationElementViewMode {
  Normal = 1,
  AnswerCorrect = 2,
  AnswerWrong = 3
}



