import { NgModule } from '@angular/core';
import { ScormContentComponent } from './scorm-content/scorm-content.component';
import { ContentSimulationControlComponent } from './content-simulation-element/content-simulation-control/content-simulation-control.component';
import { ContentSimulationElementComponent } from './content-simulation-element/content-simulation-element.component';
import { ContentSimulationViewComponent } from './content-simulation-element/content-simulation-view/content-simulation-view.component';
import { SharedModule } from '../shared/shared.module';
import { ImageCanvasModule } from '../shared/image-canvas/image-canvas.module';
import {MdSliderModule} from '../shared/material/slider';
import {NoteInfoBoxComponent} from './note-info-box/note-info-box.component';
import {ContentSimulationStepsComponent} from './content-simulation-element/content-simulation-steps/content-simulation-steps.component';
import {SimulationCompleteDialog} from './content-simulation-element/simulation-complete-dialog/simulation-complete-dialog.component';
import {MdDialogModule} from '../shared/material/dialog';
import { BookViewContentComponent } from './book-view/book-view-content/book-view-content.component';
import { BookViewLayoutComponent } from './book-view/book-view-layout/book-view-layout.component';
import { BookContentDetailComponent } from './book-view/book-content-detail/book-content-detail.component';
import { OpusContentModule } from 'opus-content-lib';
import { ContentToolbarComponent } from './toolbar/content-toolbar.component';

const config = {
  ENV : 'DEV',
  ROOT: 'SCORM_OFFLINE'
};

@NgModule({
  declarations: [ScormContentComponent, ContentSimulationControlComponent, ContentSimulationElementComponent,
  ContentSimulationViewComponent, NoteInfoBoxComponent, ContentSimulationStepsComponent, SimulationCompleteDialog,
  BookViewContentComponent,
  BookViewLayoutComponent,
  BookContentDetailComponent,
  ContentToolbarComponent],
  imports: [SharedModule.forRoot(), MdSliderModule, MdDialogModule,
    OpusContentModule.forRoot(config)],
  exports: [ScormContentComponent, SimulationCompleteDialog, BookViewLayoutComponent],
  entryComponents: [
    SimulationCompleteDialog
  ],
})
export class ContentModule {
}



