export class QuizElementReportModel {
  userId: number = 0;
  results: ElementResult[] = [];
  userAnswers: any [] = [];

  get totalQuiz() {
    return this.results.length;
  }

  get totalCorrectAnswer() {
    return  this.results.filter(r => { return r.result; } ).length;
  }

  get score() {
    return ~~(this.totalCorrectAnswer / this.totalQuiz * 100);
  }
}

export class ElementResult
{
  id: string;
  result: boolean;
  constructor( id: string, result: boolean)
  {
    this.id = id;
    this.result = result;
  }
}



