export class ScreenConfigureEvent extends RecorderEvent {
  Command: string = '';

  fromJson(json: any): ScreenConfigureEvent{
    if(!json){
      return this;
    }

    super.fromJson(json);
    this.Command = json.Command;
    return this;
  }

  toJson(): any {
    let json = super.toJson();
    json.Command = this.Command;
    return json;
  }

  createFromObject(obj: any): ScreenConfigureEvent {
    if (!obj) {
      return this;
    }

    super.createFromObject(obj);
    this.Command = obj.Command || 0;
    return this;
  }
}



