import { Pipe, PipeTransform } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

@Pipe({
  name: 'videoSecurityTrustHtml'
})

export class VideoSecurityTrustHtmlPipe implements PipeTransform {
  provider = {
    youtube: {
      regex: /(?:https?:\/\/)?(?:www\.)?(?:m\.)?(?:youtube\.com|youtu\.be)\/(?:watch\?v=|embed\/)?([0-9a-zA-Z_\-]+)(.+)?/g,
      html: '<iframe width="640" height="360" src="https://www.youtube.com/embed/{0}?wmode=opaque" frameborder="0" allowfullscreen></iframe>'
    },
    vimeo: {
      regex: /(?:www\.|player\.)?vimeo.com\/(?:channels\/(?:\w+\/)?|groups\/(?:[^\/]*)\/videos\/|album\/(?:\d+)\/video\/|video\/|)(\d+)(?:[a-zA-Z0-9_\-]+)?/i,
      html: '<iframe width="640" height="360" src="https://player.vimeo.com/video/{0}" frameborder="0" allowfullscreen></iframe>'
    },
    mp4: {
      regex: /^((?:http(s)?:\/\/)?[\w.-]+(?:\.[\w\.-]+)+[\w\-\._~:/?#[\]@!\$&'\(\)\*\+,;=.]+)\.(mp4|Mp4|MP4)$/g,
      html: '<video width="640" height="360" src="{0}" controls type="video/mp4"></video>'
    },
    ogg: {
      regex: /^((?:http(s)?:\/\/)?[\w.-]+(?:\.[\w\.-]+)+[\w\-\._~:/?#[\]@!\$&'\(\)\*\+,;=.]+)\.(ogg|Ogg|OGG)$/g,
      html: '<video width="640" height="360" src="{0}" controls type="video/ogg"></video>'
    },
    webm: {
      regex: /^((?:http(s)?:\/\/)?[\w.-]+(?:\.[\w\.-]+)+[\w\-\._~:/?#[\]@!\$&'\(\)\*\+,;=.]+)\.(webm|Webm|WEBM)$/g,
      html: '<video width="640" height="360" src="{0}" controls type="video/webm"></video>'
    }
  };

  constructor(private sanitizer: DomSanitizer) {

  }

  transform(content: string) {
    if (!content) {
      content = '';
    }

    if (content.search(this.provider.youtube.regex) !== -1) {
      let match = content.match(this.provider.youtube.regex);
      content = this.provider.youtube.html.replace('{0}', RegExp.$1);

    } else if (content.search(this.provider.vimeo.regex) !== -1) {
      let match = content.match(this.provider.vimeo.regex);
      content = this.provider.vimeo.html.replace('{0}', RegExp.$1);
    } else if (content.search(this.provider.mp4.regex) !== -1) {
      content = this.provider.mp4.html.replace('{0}', content);
    } else if (content.search(this.provider.ogg.regex) !== -1) {
      content = this.provider.ogg.html.replace('{0}', content);
    } else if (content.search(this.provider.webm.regex) !== -1) {
      content = this.provider.webm.html.replace('{0}', content);
    }

    return this.sanitizer.bypassSecurityTrustHtml(content);
  }
}



