import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'noWhiteSpace'})

export class NoWhiteSpace implements PipeTransform {

  transform(value: string, exac: boolean = false): string {
    if (!value) {
      return value;
    }

    return value.replace(/(\s|&nbsp;)+/g, (match: string) => {
      return exac ? ' ' : match;
    });
  }
}



