import {
  AfterViewInit,
  ApplicationRef,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import {
  DocumentElement,
  ForkElement,
  GroupElement,
  NoteElement,
  PathElement,
  QuizElement,
  StepElement
} from '../../model/document-element.model';
import { DocumentMetadataModel } from '../../model/document.model';
import { ContentSimulationControlComponent } from './content-simulation-control/content-simulation-control.component';
import { ContentSimulationViewComponent } from './content-simulation-view/content-simulation-view.component';
import { ElementTypeCategory, ElementTypeKind } from '../../model/element-type.model';
import { NotificationService } from '../../services/notification.service';
import { Subscription } from 'rxjs/Subscription';
import { DocumentTypeModel, SimulationType } from '../../model/document-type.model';
import { SimulationSetting } from '../../model/simulation.settings.model';
import { AuthoringService } from '../../services/authoring.service';
import { ElementResult, QuizElementReportModel } from '../../model/quiz-element-report.model';
import { Utility } from '../../shared/helper/utilities';
import { ActionKind, ControlTypeKind } from '../../model/recorder-event.model';
import { Constants } from '../../shared/helper/constants';
import { ContentSimulationStepsComponent } from './content-simulation-steps/content-simulation-steps.component';
import {MdDialog} from '../../shared/material/dialog';
import {SimulationCompleteDialog} from './simulation-complete-dialog/simulation-complete-dialog.component';

declare const pipwerks: any;

@Component({
  moduleId: module.id,
  selector: 'app-content-simulation-element',
  templateUrl: 'content-simulation-element.component.html',
  styleUrls: ['content-simulation-element.component.scss']
})

export class ContentSimulationElementComponent implements  OnInit, OnChanges, AfterViewInit, OnDestroy {
  @Input() elements: DocumentElement[] = [];
  @Input() document: DocumentMetadataModel = new DocumentMetadataModel();
  @Input() simulationType: number = 0;
  @Input() simulationSettings: SimulationSetting = new SimulationSetting();
  @Input() selectedDoctype: DocumentTypeModel = null;
  @Input() isBookContent : boolean = false;
  @Output() fullScreenToogled: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Output() simulationTypeVisited: EventEmitter<number> = new EventEmitter<number>();
  @ViewChild('controlSimulation') controlSimulation: ContentSimulationControlComponent;
  @ViewChild('viewSimulation') viewSimulation: ContentSimulationViewComponent;
  @ViewChild('controlSimulationStep') controlSimulationStep: ContentSimulationStepsComponent;
  selectedElement: DocumentElement = null;
  showStepList: boolean = true;
  currentStepIndex: number = 0;
  isStopSimulation: boolean = false;
  isPlaySimulation: boolean = true;
  timeoutHandle: any = null;
  finishSimulationTimeoutHandle: any = null;
  startOverConfirmSubscription: Subscription = null;
  langDisplay: any;
  isFirstStart: boolean = false;
  haveQuizOrStepElement: boolean = false;
  showQuizResult: boolean = false;
  quizReport: QuizElementReportModel = new QuizElementReportModel();
  keyPressEvent: any;
  noteKeyPressEvent: any;
  currentChildStepIndex: [number, number] = [0, -1];
  indexIntoSubElement: boolean = false;
  lmsConnected = false;
  numberOfPress: number = 0;
  hasZoomControl: boolean = true;
  scaleRatio: number = 1;
  viewInit: boolean = false;
  isShowStepList: boolean = false;
  isShowStartOverPopup: boolean = false;
  waitingElementIsGeneric: boolean = false;
  constructor(private notificationService: NotificationService,
              private authoringService: AuthoringService,
              private appRef: ApplicationRef,
              private dialog: MdDialog) {
  }

  ngOnInit() {
    try {
      this.lmsConnected = pipwerks.SCORM.init();
      if (pipwerks.SCORM.data && !pipwerks.SCORM.data.completionStatus){
        pipwerks.SCORM.markAsIncomplete();
      }
    } catch (nolms) {
      console.log(nolms);
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['elements'] && changes['elements'].currentValue) {
      if (this.elements.length) {
        this.elements = this.elements.filter((element) => {
          let result = true;

          if ([ElementTypeKind.TrueFalseQuiz, ElementTypeKind.MultipleQuiz, ElementTypeKind.FillInTheBlankQuiz]
            .indexOf(element.kind) !== -1) {
            result = result && element.displayText !== null && element.displayText !== '' && element.displayText !== undefined;
          }

          if (element.kind === ElementTypeKind.MultipleQuiz) {
            let tmp = element as QuizElement;
            result = result && tmp.multipleQuizAnswerOptions
              && tmp.multipleQuizAnswerOptions.length >= 2 && !tmp.multipleQuizAnswerOptions.some((option) => !option);
          } else if (element.kind === ElementTypeKind.FillInTheBlankQuiz) {
            let tmp = element as QuizElement;
            result = result && tmp.blankQuizAnswerOptionKeys && tmp.blankQuizAnswerOptionKeys.length >= 2;
          }

          if (element.kind === ElementTypeKind.PowerPointSlides) {
            result = false;
          }
          if (result) {
            result = DocumentElement.getActiveElement(element) !== null;
          }
          return result;
        });

        this.resetFlag();
        let elements = Utility.flatternArray(this.elements.map(e => (e instanceof GroupElement) ? e.subElements : e));
        elements = Utility.flatternArray(elements.map(e => (e instanceof ForkElement) ? e.subElements : e));
        elements = Utility.flatternArray(elements.map(e => (e instanceof PathElement) ? e.subElements : e));
        this.haveQuizOrStepElement = elements.filter(element => {
          let category = this.authoringService.getElementTypeById(element.elementTypeId).category;
          return [ElementTypeCategory.Quiz, ElementTypeCategory.Step].indexOf(category) !== -1 || element.kind == ElementTypeKind.PowerPointSlideStep;
        }).length > 0;
        // Start loop
        if (this.controlSimulation) {
          this.callSelectedElementChange(this.elements[0], 0);
        }
      }
    }

    if (changes['selectedDoctype'] && changes['selectedDoctype'].currentValue) {
      this.onStartOver();
    }
  }

  ngAfterViewInit() {
    let _self = this;
    this.noteKeyPressEvent = (event: any) => {
      if (event.key !== 'Enter') {
        return;
      }

      this.onNextOverNoteElement(_self);
    };

    this.keyPressEvent = (event: any) => {
      let isPressStep = this.selectedElement.kind === ElementTypeKind.Step
        && (this.selectedElement as StepElement).action == ActionKind.Press
        && (this.selectedElement as StepElement).controlType == ControlTypeKind.Key;
      if (isPressStep) {
        event.preventDefault();
        if (event.key == 'Shift' || event.key == 'Alt' || event.key == 'Control') {
          return;
        }
        this.onNextOverStepElement(_self, event);
      }
    };

    window.document.addEventListener('keypress', this.noteKeyPressEvent, false);
    window.document.addEventListener('keydown', this.keyPressEvent, false);
    _self.viewInit = true;
  }

  ngOnDestroy() {
    if (this.noteKeyPressEvent) {
      window.document.removeEventListener('keypress', this.noteKeyPressEvent, false);
      this.noteKeyPressEvent = null;
    }
    if (this.keyPressEvent) {
      window.document.removeEventListener('keydown', this.keyPressEvent, false);
      this.keyPressEvent = null;
    }
  }

  selectedElementChanged(data: [DocumentElement, number]) {
    if (data[1] < 0) {
      return;
    }

    if (data[0] && data[0].kind === ElementTypeKind.Note) {
      let prior = this.getRefStepElementInStandaloneNote(data[0]);

      if (!prior || !(data[0] as NoteElement).isCustomBackground) {
        (data[0] as NoteElement).refBackgroundStepElement = prior;
      }
    }

    this.currentStepIndex = data[1];
    this.selectedElement = data[0];
    if (this.viewInit) {
      //this.appRef.tick();
    }
    if (!this.selectedElement) {
      return;
    }

    if (this.viewSimulation && this.controlSimulation && this.controlSimulation.stepListContainer) {
      let top = this.calculateElementTop(this.controlSimulation.stepListContainer.nativeElement.firstElementChild.childNodes, this.currentStepIndex);
      this.viewSimulation.scrollTo(this.controlSimulation.stepListContainer, top, 0, 200);
    }

    this.onNextElementIfElementIsStep();
    this.onNextIfElementIsGeneric();
  }

  getRefStepElementInStandaloneNote(documentElement: DocumentElement): StepElement {
    let doFind = (note: DocumentElement, elements: DocumentElement[]): StepElement => {
      let result: StepElement = null;

      if (!note || note.kind !== ElementTypeKind.Note) {
        return result;
      }

      let flattenElementsList =
        Utility.flatternArray(elements.map(e => e instanceof GroupElement ? e.subElements : e));
      let index = flattenElementsList.findIndex((e: any) => e.id === note.id) - 1;

      while (index > -1) {
        result = flattenElementsList[index];

        if (!(result instanceof StepElement)) {
          result = null;
          index--;
        } else {
          break;
        }
      }

      return result;
    };

    return doFind(documentElement, this.elements);
  }

  resetFlag() {
    this.isStopSimulation = false;
    this.isPlaySimulation = true;
    this.isFirstStart = true;
    this.indexIntoSubElement = false;
    this.currentChildStepIndex = [0, -1];
    if (this.timeoutHandle) {
      clearTimeout(this.timeoutHandle);
    }

    if (this.finishSimulationTimeoutHandle) {
      clearTimeout(this.finishSimulationTimeoutHandle);
    }

    this.finishSimulationTimeoutHandle = null;
    this.timeoutHandle = null;

    if (this.controlSimulation) {
      this.controlSimulation.resetFlag();
    }

    if (this.viewSimulation) {
      this.viewSimulation.resetTimer();
    }

    this.showStepList = true;
    this.hasZoomControl = true;
  }

  finishInitSimulationObject() {
    if (!this.viewSimulation) {
      return;
    }

    if (!this.viewSimulation.isTypeStep()) {
      if (!this.viewSimulation.cursorSelector) {
        return;
      }
    }

    if (this.isPlaySimulation && this.selectedElement) {
      let isFirstElementOnDemoSimulation = this.elements[0].id === this.selectedElement.id && this.simulationType === SimulationType.Demo;
      let pauseOnNotesOnDemoSimulation =
        this.selectedElement.subElements.filter(e => this.authoringService.getElementTypeById(e.elementTypeId).category === ElementTypeCategory.Note).length > 0
        && this.simulationType === SimulationType.Demo
        && this.simulationSettings.demoSimulationSetting.pauseDemoOnNotes;

      if (isFirstElementOnDemoSimulation && this.isFirstStart) {
        this.isFirstStart = false;
        this.controlSimulation.onChangeSimulationControlStatus(pauseOnNotesOnDemoSimulation ? false : this.simulationSettings.demoSimulationSetting.autoStartDemo);
      } else {
        this.controlSimulation.onChangeSimulationControlStatus(pauseOnNotesOnDemoSimulation ? false : this.isPlaySimulation);
      }
    }

    if (this.simulationType === SimulationType.Demo) {
      // Make sure the old process taking a next element is terminated when clicking multiple the previous button
      if (this.finishSimulationTimeoutHandle) {
        clearTimeout(this.finishSimulationTimeoutHandle);
        this.finishSimulationTimeoutHandle = null;
      }
      if (this.viewSimulation.cursorSelector && !this.viewSimulation.isTypeStep()) {
        this.viewSimulation.cursorSelector.finished.then(this.simulationFinishedEventHandle.bind(this));
      }
    }
  }

  simulationFinishedEventHandle() {
    if (this.finishSimulationTimeoutHandle) {
      return;
    }

    if (this.viewSimulation && !this.viewSimulation.cursor) {
      return;
    }

    let clickLength = this.simulationSettings.demoSimulationSetting.clickLength * 1000;
    let isTypeStep = this.viewSimulation.isTypeStep();

    if (isTypeStep) {
      clickLength = this.simulationSettings.demoSimulationSetting.betweenKeys * 1000;
    } else {
      if (!this.viewSimulation.isKeyControl) {
        this.viewSimulation._renderer.setElementAttribute(this.viewSimulation.cursor.nativeElement.firstElementChild, 'fill', '#000000');
      }

      if (this.viewSimulation.soundClickAudio && this.simulationSettings.demoSimulationSetting.clickAndTypeSounds) {
        this.viewSimulation.soundClickAudio.nativeElement.play();
      }
    }

    this.finishSimulationTimeoutHandle = setTimeout(() => {
      if (this.viewSimulation && !isTypeStep) {
        this.viewSimulation._renderer.setElementAttribute(this.viewSimulation.cursor.nativeElement.firstElementChild, 'fill', '#ffffff');
      }

      let pauseAfter = this.simulationSettings.demoSimulationSetting.pauseAfter * 1000;
      this.finishSimulationTimeoutHandle = setTimeout(() => {
        if (this.currentStepIndex + 1 >= this.elements.length) {
          if (this.elements[this.currentStepIndex].kind !== ElementTypeKind.Fork) {
            this.finishSimulationProcess();
          }
          else {
            if (this.elements[this.currentStepIndex].subElements.length
              && this.elements[this.currentStepIndex].subElements[this.currentChildStepIndex[0]].subElements.length
              && ((this.elements[this.currentStepIndex].subElements[this.currentChildStepIndex[0]].subElements.length - 1) === this.currentChildStepIndex[1])) {
              this.finishSimulationProcess();
            }
            else {
              if (this.viewSimulation)
                this.viewSimulation.clearInstructionTail();

              if (!this.indexIntoSubElement && this.controlSimulation) {
                this.callSelectedElementChange(this.elements[this.currentStepIndex + 1], this.currentStepIndex + 1);
              }
              else {
                this.selectNextChildElement();
              }
            }
          }
        } else {
          if (this.elements[this.currentStepIndex].kind === ElementTypeKind.Fork
            && this.elements[this.currentStepIndex].subElements.length
            && (this.elements[this.currentStepIndex].subElements[this.currentChildStepIndex[0]] as PathElement).isStopSimulation
            && this.elements[this.currentStepIndex].subElements[this.currentChildStepIndex[0]].subElements.length
            && ((this.elements[this.currentStepIndex].subElements[this.currentChildStepIndex[0]].subElements.length - 1) === this.currentChildStepIndex[1])) {
            this.finishSimulationProcess();
            return;
          }
          else {
            if (this.viewSimulation)
              this.viewSimulation.clearInstructionTail();
            if (!this.indexIntoSubElement && this.controlSimulation) {
              this.callSelectedElementChange(this.elements[this.currentStepIndex + 1], this.currentStepIndex + 1);
            }
            else {
              this.selectNextChildElement();
            }
          }
        }
        this.finishSimulationTimeoutHandle = null;
      }, pauseAfter);
    }, clickLength);
  }

  finalInteractive(data: any = null, buttonEvent: number = null) {
    if (this.simulationType === SimulationType.Demo) {
      return;
    }

    if (this.simulationType === SimulationType.Interactive
      && (this.elements[this.currentStepIndex] as StepElement).action === ActionKind.Click
      && buttonEvent !== null && buttonEvent !== 0) {
      return;
    }

    if (this.simulationType === SimulationType.Interactive
      && (this.elements[this.currentStepIndex] as StepElement).action === ActionKind.RightClick
      && buttonEvent !== null && buttonEvent !== 2) {
      return;
    }

    if (data && data.countStepWrong && this.viewSimulation) {
      this.viewSimulation.updateSubmitQuizAnswer([[], new ElementResult(data.elementId, false)]);
    }

    if (this.viewSimulation && this.viewSimulation.isTypeStep()) {
      this.viewSimulation.typingSimulation.nativeElement.value = '';
      this.viewSimulation.typingSimulation.nativeElement.display = 'none';
    }

    if (this.currentStepIndex + 1 >= this.elements.length) {
      if (this.elements[this.currentStepIndex].kind !== ElementTypeKind.Fork) {
        this.finishSimulationProcess();
      }
      else {
        if (this.elements[this.currentStepIndex].subElements.length
          && this.elements[this.currentStepIndex].subElements[this.currentChildStepIndex[0]].subElements.length
          && ((this.elements[this.currentStepIndex].subElements[this.currentChildStepIndex[0]].subElements.length - 1) === this.currentChildStepIndex[1])) {
          this.finishSimulationProcess();
        }
        else {
          if (this.viewSimulation)
            this.viewSimulation.clearInstructionTail();
          if (!this.indexIntoSubElement && this.controlSimulation) {
            this.callSelectedElementChange(this.elements[this.currentStepIndex + 1], this.currentStepIndex + 1);
          }
          else {
            this.selectNextChildElement();
          }
        }
      }
    } else {
      if (this.elements[this.currentStepIndex].kind === ElementTypeKind.Fork
        && this.elements[this.currentStepIndex].subElements.length
        && (this.elements[this.currentStepIndex].subElements[this.currentChildStepIndex[0]] as PathElement).isStopSimulation
        && this.elements[this.currentStepIndex].subElements[this.currentChildStepIndex[0]].subElements.length
        && ((this.elements[this.currentStepIndex].subElements[this.currentChildStepIndex[0]].subElements.length - 1) === this.currentChildStepIndex[1])) {
        this.finishSimulationProcess();
        return;

      }
      else {
        if (this.viewSimulation)
          this.viewSimulation.clearInstructionTail();

        if (!this.indexIntoSubElement && this.controlSimulation) {
          this.callSelectedElementChange(this.elements[this.currentStepIndex + 1], this.currentStepIndex + 1);
        }
        else {
          this.selectNextChildElement();
        }

      }
    }
  }

  finalQuiz() {
    this.onNextIfElementIsGeneric(true);
  }

  onNextOverVideoElement() {
    this.onNextIfElementIsGeneric(true);
  }

  beforeOnNextElement(data: any = null) {
    this.clearFinishSimulationTimeoutHandle();
    this.onNextElement(data);
  }

  onNextElement(data: any = null) {
    let currentIndex = this.currentStepIndex;
    if (!this.indexIntoSubElement) {
      currentIndex++;
    }

    if (data && data.countStepWrong && this.viewSimulation) {
      this.viewSimulation.updateSubmitQuizAnswer([[], new ElementResult(data.elementId, false)]);
    }

    if (currentIndex >= this.elements.length) {
      return;
    }

    if (this.isElementAtTheEndOfPath()){
      return;
    }

    this.preventCallbackWhenMoveToNewElement();
    let nextElement = this.elements[currentIndex];
    if (nextElement.kind !== ElementTypeKind.Fork && this.controlSimulation) {
      this.callSelectedElementChange(nextElement, currentIndex);
    } else if (nextElement.kind === ElementTypeKind.Fork &&  this.elements[this.currentStepIndex].kind !== ElementTypeKind.Fork) {
      this.callSelectedElementChange(nextElement, currentIndex);
    } else if (nextElement.kind === ElementTypeKind.Fork && nextElement.subElements.length > 0) {
      if (!this.indexIntoSubElement && this.currentChildStepIndex[0] > 0) {
        this.currentChildStepIndex = [0, -1];
      }
      if (this.currentChildStepIndex[0] < nextElement.subElements.length) {
        if (nextElement.subElements[this.currentChildStepIndex[0]].subElements.length > 0) {
          if (this.currentChildStepIndex[1] < nextElement.subElements[this.currentChildStepIndex[0]].subElements.length - 1) {
            let nextChildElement = nextElement.subElements[this.currentChildStepIndex[0]].subElements[this.currentChildStepIndex[1] + 1];
            this.currentChildStepIndex[1] = this.currentChildStepIndex[1] + 1;
            if (!this.indexIntoSubElement) {
              this.indexIntoSubElement = true;
            }
            this.callSelectedElementChange(nextChildElement, currentIndex);
          }
          else {
            this.currentChildStepIndex = [this.currentChildStepIndex[0] + 1, -1];
            this.onNextElement();
          }
        }
        else {
          this.currentChildStepIndex = [this.currentChildStepIndex[0] + 1, -1];
          this.onNextElement();
        }
      } else {
        this.indexIntoSubElement = false;
        this.currentChildStepIndex[0] = this.currentChildStepIndex[0] - 1;
        this.currentChildStepIndex[1] = nextElement.subElements[this.currentChildStepIndex[0]].subElements.length;
        this.onNextElement();
      }
    }
  }

  isElementAtTheEndOfPath(){
    let currentElement = this.elements[ this.currentStepIndex];
    if (!currentElement) return false
    if (currentElement.subElements[this.currentChildStepIndex[0]] && (currentElement.subElements[this.currentChildStepIndex[0]] as PathElement).isStopSimulation) {
      if (this.currentChildStepIndex[1] === currentElement.subElements[this.currentChildStepIndex[0]].subElements.length - 1){
        this.finishSimulationProcess();
        return true;
      }
    }
  }

  beforeOnPreviousElement() {
    this.clearFinishSimulationTimeoutHandle();
    this.onPreviousElement();
  }

  onPreviousElement() {
    let currentIndex = this.currentStepIndex;
    if (!this.indexIntoSubElement) {
      currentIndex--;
    }
    if (currentIndex < 0) {
      return;
    }
    this.preventCallbackWhenMoveToNewElement();
    let previousElement = this.elements[currentIndex];
    if (previousElement.kind !== ElementTypeKind.Fork && this.controlSimulation) {
      this.callSelectedElementChange(previousElement, currentIndex);
    }
    else if (previousElement.kind === ElementTypeKind.Fork && this.currentChildStepIndex[1] === 0) {
      this.callSelectedElementChange(previousElement, currentIndex);
    }
    else if (previousElement.kind === ElementTypeKind.Fork && previousElement.subElements.length > 0) {
      if (!this.indexIntoSubElement && this.currentChildStepIndex[0] >= 0) {
        if (previousElement.subElements && previousElement.subElements.length > 0) {
          for (let i = previousElement.subElements.length - 1; i >= 0; i--) {
            if (previousElement.subElements[i].subElements && previousElement.subElements[i].subElements.length > 0) {
              this.currentChildStepIndex = [i, previousElement.subElements[i].subElements.length];
              break;
            }
          }
        }
      }
      if (this.currentChildStepIndex[0] >= 0) {
        if (previousElement.subElements[this.currentChildStepIndex[0]].subElements.length > 0) {
          if (this.currentChildStepIndex[1] > 0) {
            let previousChildElement = previousElement.subElements[this.currentChildStepIndex[0]].subElements[this.currentChildStepIndex[1] - 1];
            this.currentChildStepIndex[1] = this.currentChildStepIndex[1] - 1;
            if (!this.indexIntoSubElement && this.controlSimulation) {
              this.indexIntoSubElement = true;
            }
            this.callSelectedElementChange(previousChildElement, currentIndex);
          }
          else {
            this.currentChildStepIndex[0] = this.currentChildStepIndex[0] - 1;
            if (this.currentChildStepIndex[0] >= 0) {
              this.currentChildStepIndex[1] = previousElement.subElements[this.currentChildStepIndex[0]].subElements.length;
            }
            this.onPreviousElement();
          }
        }
        else {
          this.currentChildStepIndex[0] = this.currentChildStepIndex[0] - 1;
          if (this.currentChildStepIndex[0] >= 0) {
            this.currentChildStepIndex[1] = previousElement.subElements[this.currentChildStepIndex[0]].subElements.length;
          }
          this.onPreviousElement();
        }
      } else {
        this.indexIntoSubElement = false;
        this.currentChildStepIndex = [0, -1];
        this.onPreviousElement();
      }
    }
  }

  beforeOnGoToOtherElement(event: number) {
    this.clearFinishSimulationTimeoutHandle();
    this.onGoToOtherElement(event);
  }

  onGoToOtherElement(event: number) {
    if (event >= this.elements.length) {
      return;
    }

    this.preventCallbackWhenMoveToNewElement();
    if (this.elements[event].kind === ElementTypeKind.Fork) {
      this.indexIntoSubElement = true;
      this.currentChildStepIndex = [0, -1];
    }
    else {
      this.indexIntoSubElement = false;
    }
    if (this.controlSimulation) {
      this.callSelectedElementChange(this.elements[event], event);
    }
  }

  toogleStepList(flag: boolean) {
    this.showStepList = flag;
    this.hasZoomControl = flag;
  }

  onFullScreenToogled(flag: boolean) {
    this.fullScreenToogled.emit(flag);
  }

  onControlSimulation(play: boolean) {
    this.isPlaySimulation = play;
    this.controlSimulation.isPlaySimulation = play;

    if (this.isPlaySimulation) {
      if (this.isStopSimulation) {
        this.isStopSimulation = false;
        this.startSimulationBeginInSelectedElement();
      } else if (this.selectedElement) {
        if (this.selectedElement.kind !== ElementTypeKind.Step
          && this.selectedElement.kind !== ElementTypeKind.PowerPointSlideStep) {
          this.onNextIfElementIsGeneric(true);
        } else if (this.viewSimulation) {
          this.viewSimulation.playSimulation();
        }
      }
    } else {
      if (this.timeoutHandle) {
        clearTimeout(this.timeoutHandle);
      }
      if (this.viewSimulation) {
        this.viewSimulation.pauseSimulation();
      }
    }
  }

  onViewQuizReportDetail(elementInfo: [DocumentElement, number]) {
    let [element, index] = elementInfo;
    if (this.viewSimulation) {
      this.viewSimulation.element = element;
    }

    if (this.viewSimulation && element.kind === ElementTypeKind.Step) {
      this.viewSimulation.screenImageUrl =
        this.authoringService.getResourceUrl(this.document.name, (element as StepElement).screenImage) + '.png';
    }

    let elements = Utility.flatternArray(this.elements.map(e => (e instanceof GroupElement) ? e.subElements : e));
    elements = Utility.flatternArray(elements.map(e => (e instanceof ForkElement) ? e.subElements : e));
    elements = Utility.flatternArray(elements.map(e => (e instanceof PathElement) ? e.subElements : e));
    let quiz = elements
      .filter(el => {
        return [ElementTypeKind.Step, ElementTypeKind.TrueFalseQuiz, ElementTypeKind.MultipleQuiz, ElementTypeKind.FillInTheBlankQuiz].indexOf(el.kind) !== -1;
      }).find(e => e.id === element.id);

    if (!quiz) {
      return;
    }

    if (this.viewSimulation) {
      let indexOfAnwser = this.quizReport.results.findIndex(r => r.id === quiz.id);
      this.viewSimulation.onViewQuizElementReportDetail(this.quizReport.userAnswers[indexOfAnwser], element.kind);
      if (indexOfAnwser === -1) {
        indexOfAnwser = index;
      }
      this.controlSimulation.currentStepIndex = indexOfAnwser;
    }
  }

  onStartOver() {
    pipwerks.SCORM.markAsIncomplete();
    this.showQuizResult = false;
    this.isStopSimulation = false;
    this.currentChildStepIndex = [0, -1];
    this.indexIntoSubElement = false;
    if (this.viewSimulation) {
      this.viewSimulation.viewReportDetailMode = false;
    }
    this.quizReport = new QuizElementReportModel();
    this.isFirstStart = true;
    if (this.controlSimulation) {
      this.callSelectedElementChange(this.elements[0], 0);
    }
    if (this.controlSimulationStep) {
      this.controlSimulationStep.initViewModes(this.elements);
    }
  }

  onComplete() {
    if (this.simulationType === SimulationType.Test) {
      this.pushResultToLMS(this.quizReport.totalCorrectAnswer, this.quizReport.totalQuiz);
      window.close();
    }
    this.simulationTypeVisited.emit(this.simulationType);
  }

  reportQuizProcess(isComplete: boolean = false) {
    this.showQuizResult = true;
    if (this.viewSimulation) {
      this.viewSimulation.viewReportDetailMode = false;
      if (isComplete) {
        this.pushResultToLMS(this.quizReport.totalCorrectAnswer, this.quizReport.totalQuiz);
        pipwerks.SCORM.completeLesson();
      }
    }
    if (this.controlSimulationStep) {
      this.controlSimulationStep.onReportQuiz();
    }
    setTimeout(() => {
      if (this.controlSimulationStep && this.controlSimulationStep.stepListContainer) {
        let top = this.calculateElementTop(this.controlSimulationStep.stepListContainer.nativeElement.firstElementChild.childNodes, this.currentStepIndex);
        if (this.viewSimulation) {
          this.viewSimulation.scrollTo(this.controlSimulationStep.stepListContainer, top, 0, 200);
        }
      }
    }, 0);
  }

  onNextOverNoteElement(_self: any = null) {
    if (!_self) {
      _self = this;
    }

    let isNote = _self.selectedElement.kind === ElementTypeKind.Note;
    let isStepContainNote = _self.selectedElement.kind === ElementTypeKind.Step
      && _self.selectedElement.subElements
      && _self.selectedElement.subElements.find(el => el.kind === ElementTypeKind.Note) != null;
    let isNoInstructionFrame =
    _self.selectedElement && _self.selectedElement.kind === ElementTypeKind.Step && (_self.selectedElement as StepElement).convertedFromFrame &&
      (!(_self.selectedElement as StepElement).controlType && !(_self.selectedElement as StepElement).action);
    let isPressAction = (_self.selectedElement as StepElement).action === ActionKind.Press
      && (_self.selectedElement as StepElement).controlType == ControlTypeKind.Key;
    if (_self.simulationType === SimulationType.Test && isStepContainNote && !isNoInstructionFrame && !isPressAction) {
      return;
    }

    if (isNote || isStepContainNote || isNoInstructionFrame || isPressAction) {
      if (_self.simulationType === SimulationType.Demo && !isNoInstructionFrame && !isPressAction) {
        _self.controlSimulation.onChangeSimulationControlStatus(true);
        return;
      }

      if (_self.currentStepIndex + 1 >= _self.elements.length) {
        if (_self.elements[_self.currentStepIndex].kind !== ElementTypeKind.Fork) {
          _self.finishSimulationProcess();
        }
        else {
          if (_self.elements[_self.currentStepIndex].subElements.length
            && _self.elements[_self.currentStepIndex].subElements[_self.currentChildStepIndex[0]].subElements.length
            && ((_self.elements[_self.currentStepIndex].subElements[_self.currentChildStepIndex[0]].subElements.length - 1) === _self.currentChildStepIndex[1])) {
            _self.finishSimulationProcess();
          }
          else {

            if (!_self.indexIntoSubElement && _self.controlSimulation) {
              _self.controlSimulation.selectedElementChange(_self.elements[_self.currentStepIndex + 1], _self.currentStepIndex + 1);
            }
            else {
              _self.selectNextChildElement();
            }
          }
        }
      } else {
        if (_self.elements[_self.currentStepIndex].kind === ElementTypeKind.Fork
          && _self.elements[_self.currentStepIndex].subElements.length
          && (_self.elements[_self.currentStepIndex].subElements[_self.currentChildStepIndex[0]] as PathElement).isStopSimulation
          && _self.elements[_self.currentStepIndex].subElements[_self.currentChildStepIndex[0]].subElements.length
          && ((_self.elements[_self.currentStepIndex].subElements[_self.currentChildStepIndex[0]].subElements.length - 1) === _self.currentChildStepIndex[1])) {
          _self.finishSimulationProcess();
          return;
        }
        else {
          if (!_self.indexIntoSubElement && _self.controlSimulation) {
            _self.controlSimulation.selectedElementChange(_self.elements[_self.currentStepIndex + 1], _self.currentStepIndex + 1);
          }
          else {
            _self.selectNextChildElement();
          }
        }
      }
    }
  }

  onNextOverStepElement(_self: any = null, event: any) {
    if (!_self) {
      _self = this;
    }

    let isPressStep = _self.selectedElement.kind === ElementTypeKind.Step
      && _self.selectedElement.action == ActionKind.Press
      && _self.selectedElement.controlType == ControlTypeKind.Key;

    if (_self.selectedElement.valueText === '') {
      if (_self.selectedElement.displayText.indexOf('Enter') >= 0) {
        _self.selectedElement.valueText = 'Enter';
      }
    }

    let elementKeyObject = _self.convertValueText(_self.selectedElement.valueText);
    let isPressExactly = elementKeyObject.shiftKey == event.shiftKey && elementKeyObject.ctrlKey == event.ctrlKey
      && elementKeyObject.altKey == event.altKey && elementKeyObject.key == event.key.toLowerCase();

    if (isPressStep && isPressExactly) {

      if (_self.viewSimulation) {
        if (_self.numberOfPress < 3) {
          _self.viewSimulation.updateSubmitQuizAnswer([[], new ElementResult(_self.selectedElement.id, true)]);
        }
        _self.numberOfPress = 0;
      }

      if (_self.simulationType === SimulationType.Demo) {
        _self.controlSimulation.onChangeSimulationControlStatus(true);
        return;
      }

      if (_self.currentStepIndex + 1 >= _self.elements.length) {
        if (_self.elements[_self.currentStepIndex].kind !== ElementTypeKind.Fork) {
          _self.finishSimulationProcess();
        }
        else {
          if (_self.elements[_self.currentStepIndex].subElements.length
            && _self.elements[_self.currentStepIndex].subElements[_self.currentChildStepIndex[0]].subElements.length
            && ((_self.elements[_self.currentStepIndex].subElements[_self.currentChildStepIndex[0]].subElements.length - 1) === _self.currentChildStepIndex[1])) {
            _self.finishSimulationProcess();
          }
          else {

            if (!_self.indexIntoSubElement && _self.controlSimulation) {
              _self.controlSimulation.selectedElementChange(_self.elements[_self.currentStepIndex + 1], _self.currentStepIndex + 1);
            }
            else {
              _self.selectNextChildElement();
            }
          }
        }

      } else {
        if (_self.elements[_self.currentStepIndex].kind === ElementTypeKind.Fork
          && _self.elements[_self.currentStepIndex].subElements.length
          && (_self.elements[_self.currentStepIndex].subElements[_self.currentChildStepIndex[0]] as PathElement).isStopSimulation
          && _self.elements[_self.currentStepIndex].subElements[_self.currentChildStepIndex[0]].subElements.length
          && ((_self.elements[_self.currentStepIndex].subElements[_self.currentChildStepIndex[0]].subElements.length - 1) === _self.currentChildStepIndex[1])) {
          _self.finishSimulationProcess();
          return;
        }

        else {
          if (!_self.indexIntoSubElement && _self.controlSimulation) {
            _self.controlSimulation.selectedElementChange(_self.elements[_self.currentStepIndex + 1], _self.currentStepIndex + 1);
          }
          else {
            _self.selectNextChildElement();
          }

        }
      }
    } else if (isPressStep && !isPressExactly) {
      _self.numberOfPress++;
      if (_self.numberOfPress >= 3) {
        _self.viewSimulation.updateSubmitQuizAnswer([[], new ElementResult(_self.selectedElement.id, false)]);
      }
    }

  }

  convertValueText(valueText: string) {
    let keys = valueText.toLowerCase().split(' + ');
    let returnObject: any = {
      shiftKey: false,
      ctrlKey: false,
      altKey: false,
      key: ''
    };
    keys.forEach(key => {
      switch (key) {
        case 'shift':
          returnObject.shiftKey = true;
          break;
        case 'ctrl':
          returnObject.ctrlKey = true;
          break;
        case 'alt':
          returnObject.altKey = true;
          break;
        default:
          returnObject.key = key;
          break;
      }
    });
    return returnObject;
  }

  onSelectChildElement(childIndex: number) {
    this.currentChildStepIndex = [childIndex, -1];
    this.indexIntoSubElement = true;
    this.selectNextChildElement();
    if (this.controlSimulation) {
      this.controlSimulation.onChangeSimulationControlStatus(this.selectedElement.kind === ElementTypeKind.Step);
    }
  }

  selectedChildStepChanged(data: [number, number, number]) {
    let [elementIndex, pathIndex, stepIndex] = data;
    this.indexIntoSubElement = true;
    this.currentChildStepIndex = [pathIndex, stepIndex];
    let selectedElement = this.elements[elementIndex];
    if (selectedElement.subElements.length > 0 && this.currentChildStepIndex[0] < selectedElement.subElements.length) {
      if (selectedElement.subElements[this.currentChildStepIndex[0]].subElements.length > 0
        && this.currentChildStepIndex[1] < selectedElement.subElements[this.currentChildStepIndex[0]].subElements.length) {
        let selectedChildElement = selectedElement.subElements[this.currentChildStepIndex[0]].subElements[this.currentChildStepIndex[1]];
        if (this.controlSimulation) {
          this.callSelectedElementChange(selectedChildElement, elementIndex);
        }
      }
    }
  }

  private calculateElementTop(elements: any, index: number) {
    let top = 0;

    for (let i = 0; i < index; i++) {
      top += elements[i + 2].offsetHeight;
    }
    if (this.indexIntoSubElement && this.elements[index].kind === ElementTypeKind.Fork) {
      top += elements[index + 2].querySelectorAll('.fork-title')[0].offsetHeight;
      for (let i = 0; i <= this.currentChildStepIndex[0]; i++) {
        if (i < this.currentChildStepIndex[0]) {
          top += elements[index + 2].querySelectorAll('.path-item-container')[i].offsetHeight;
        }
        else {
          top += elements[index + 2].querySelectorAll('.path-item-container')[i]
            .querySelectorAll('.path-title')[0].offsetHeight + 30;
        }
      }
      for (let i = 0; i < this.currentChildStepIndex[1]; i++) {
        top += elements[index + 2].querySelectorAll('.path-item-container')[this.currentChildStepIndex[0]]
          .querySelectorAll('.step-item-container')[i].offsetHeight;
      }
    }
    return top;
  }

  private onNextElementIfElementIsStep() {
    if (this.selectedElement.kind !== ElementTypeKind.Step) {
      return;
    }

    if (!this.controlSimulation) {
      return;
    }

    if (!this.viewSimulation || !this.viewSimulation.element || !this.selectedElement) {
      return;
    }

    if (this.viewSimulation.element.id === this.selectedElement.id) {
      this.timeoutHandle = setTimeout(() => {
        this.viewSimulation.startSimulationWithoutChangeElement(true);
      }, 0);
    }
  }

  private onNextIfElementIsGeneric(justPause?: boolean) {
    if (this.selectedElement && this.selectedElement.kind === ElementTypeKind.Step) {
      clearTimeout(this.timeoutHandle);
      this.waitingElementIsGeneric = false;
      return;
    }

    if (this.waitingElementIsGeneric === true) {
      return;
    }

    let onNextProcessing = () => {
      this.waitingElementIsGeneric = false;
      if (this.selectedElement.kind === ElementTypeKind.Step) {
        return;
      }
      if (this.currentStepIndex + 1 >= this.elements.length) {
        if (this.elements[this.currentStepIndex].kind !== ElementTypeKind.Fork) {

          this.finishSimulationProcess();
        }
        else {
          if (this.elements[this.currentStepIndex].subElements.length
            && this.elements[this.currentStepIndex].subElements[this.currentChildStepIndex[0]].subElements.length
            && ((this.elements[this.currentStepIndex].subElements[this.currentChildStepIndex[0]].subElements.length - 1) === this.currentChildStepIndex[1])) {
            this.finishSimulationProcess();
          }
          else {

            if (!this.indexIntoSubElement && this.controlSimulation) {
              this.callSelectedElementChange(this.elements[this.currentStepIndex + 1], this.currentStepIndex + 1);
            }
            else {
              this.selectNextChildElement();
            }
          }
        }
      } else {
        if (this.elements[this.currentStepIndex].kind === ElementTypeKind.Fork
          && this.elements[this.currentStepIndex].subElements.length
          && (this.elements[this.currentStepIndex].subElements[this.currentChildStepIndex[0]] as PathElement).isStopSimulation
          && this.elements[this.currentStepIndex].subElements[this.currentChildStepIndex[0]].subElements.length
          && ((this.elements[this.currentStepIndex].subElements[this.currentChildStepIndex[0]].subElements.length - 1) === this.currentChildStepIndex[1])) {
          this.finishSimulationProcess();
          return;
        }
        else {
          if (!this.indexIntoSubElement && this.controlSimulation) {
            this.callSelectedElementChange(this.elements[this.currentStepIndex + 1], this.currentStepIndex + 1);
          }
          else {
            this.selectNextChildElement();
          }
        }
      }
    };

    if (justPause) {
      onNextProcessing();
      return;
    }

    let elementType = this.authoringService.getElementTypeById(this.selectedElement.elementTypeId);

    if ([ElementTypeCategory.Video, ElementTypeCategory.Quiz].indexOf(elementType.category) !== -1
      || this.selectedElement.kind === ElementTypeKind.PowerPointSlideStep) {
      return;
    }

    if (elementType.category === ElementTypeCategory.Note) {
      if (this.simulationType !== SimulationType.Demo) {
        return;
      } else if (this.simulationSettings && this.simulationSettings.demoSimulationSetting
        && this.simulationSettings.demoSimulationSetting.pauseDemoOnNotes) {
        this.waitingElementIsGeneric = false;
        this.controlSimulation.onChangeSimulationControlStatus(false);
        return;
      }
    }

    if (elementType.category === ElementTypeCategory.Fork) {
      this.controlSimulation.onChangeSimulationControlStatus(false);
      this.indexIntoSubElement = true;
      this.currentChildStepIndex = [0, -1];
      return;
    }

    let durationTime = this.simulationSettings.demoSimulationSetting.movementTime * 1000;
    this.waitingElementIsGeneric = true;
    this.timeoutHandle = setTimeout(() => {
      onNextProcessing();
      this.timeoutHandle = null;
    }, durationTime);

    let isFirstElementOnDemoSimulation = this.elements[0].id === this.selectedElement.id && this.simulationType === SimulationType.Demo;

    if (isFirstElementOnDemoSimulation && this.isFirstStart) {
      this.isFirstStart = false;
      this.controlSimulation.onChangeSimulationControlStatus(this.simulationSettings.demoSimulationSetting.autoStartDemo);
    }
  }

  private finishSimulationProcess() {
    if (!this.elements.length || this.isShowStartOverPopup) {
      return;
    }

    if (this.haveQuizOrStepElement && this.simulationType === SimulationType.Test) {
      this.reportQuizProcess(true);
      return;
    }

    setTimeout(() => {
      pipwerks.SCORM.completeLesson();
      const dialogRef = this.dialog.open(SimulationCompleteDialog);
      this.startOverConfirmSubscription = dialogRef.afterClosed().subscribe(startOver => {
        if (startOver) {
          this.onStartOver();
        } else {
          if (this.simulationType === SimulationType.Demo) {
            if (this.lmsConnected) {
              this.pushResultToLMS(1, 1);
              window.close();
            } else {
              this.isStopSimulation = true;
              this.controlSimulation.onChangeSimulationControlStatus(false);
            }
          } else {
            if (this.lmsConnected) {
              this.pushResultToLMS(this.quizReport.totalCorrectAnswer, this.elements.length);
              window.close();
            } else {
              this.startSimulationBeginInSelectedElement();
            }
          }
        }
        this.isShowStartOverPopup = false;
      });
    }, 200);

    if (this.finishSimulationTimeoutHandle) {
      clearTimeout(this.finishSimulationTimeoutHandle);
    }
    this.isShowStartOverPopup = true;
  }

  private pushResultToLMS(rawScore: number, rawMax: number) {
    if (this.lmsConnected) {
      pipwerks.SCORM.setScore(rawScore,rawMax);
    }
  }

  private startSimulationBeginInSelectedElement() {
    if (this.currentStepIndex + 1 >= this.elements.length) {
      if (this.simulationType === SimulationType.Interactive) {
        return;
      }

      this.callSelectedElementChange(this.elements[0], 0);
    } else {
      // Start simulation without change element
      if (this.viewSimulation) {
        this.viewSimulation.startSimulationWithoutChangeElement();
      }
    }
  }

  private preventCallbackWhenMoveToNewElement() {
    if (this.finishSimulationTimeoutHandle) {
      clearTimeout(this.finishSimulationTimeoutHandle);
    }

    if (this.selectedElement.kind !== ElementTypeKind.Step) {
      if (this.timeoutHandle) {
        clearTimeout(this.timeoutHandle);
      }
    } else if (this.viewSimulation) {
      this.viewSimulation.pauseSimulation();
      this.viewSimulation.clearScrollTimeout();
    }
  }

  private selectNextChildElement() {
    let selectedElement = this.elements[this.currentStepIndex];
    if (selectedElement && selectedElement.subElements.length > 0 && this.currentChildStepIndex[0] < selectedElement.subElements.length) {
      if (selectedElement.subElements[this.currentChildStepIndex[0]].subElements.length > 0
        && this.currentChildStepIndex[1] < selectedElement.subElements[this.currentChildStepIndex[0]].subElements.length - 1) {
        let nextChildElement = selectedElement.subElements[this.currentChildStepIndex[0]].subElements[this.currentChildStepIndex[1] + 1];
        this.currentChildStepIndex[1] += 1;
        this.callSelectedElementChange(nextChildElement, this.currentStepIndex);
      }
      else {
        this.indexIntoSubElement = false;
        if (!(selectedElement.subElements[this.currentChildStepIndex[0]] as PathElement).isStopSimulation) {
          this.callSelectedElementChange(this.elements[this.currentStepIndex + 1], this.currentStepIndex + 1);
        }
        this.currentChildStepIndex[0] = selectedElement.subElements.length - 1;
        this.currentChildStepIndex[1] = selectedElement.subElements[this.currentChildStepIndex[0]].subElements.length;
      }
    }
    else {
      this.indexIntoSubElement = false;
      this.currentChildStepIndex[0] = selectedElement.subElements.length - 1;
      this.currentChildStepIndex[1] = selectedElement.subElements[this.currentChildStepIndex[0]].subElements.length;
      this.callSelectedElementChange(this.elements[this.currentStepIndex + 1], this.currentStepIndex + 1);
    }
  }

  get zoomPercentage() {
    return (this.scaleRatio * 100).toFixed() + '%';
  }

  zoomContent(value: number) {
    this.scaleRatio = Math.round(this.scaleRatio * 1e2) / 1e2;
    let newRatio = Math.round((this.scaleRatio + value) * 1e12) / 1e12;
    if (Math.round((this.scaleRatio * 100 % 10) * 1e12) / 1e12 > 0) {
      if (value > 0) {
        newRatio = Math.ceil(this.scaleRatio * 10) / 10;
      } else {
        newRatio = Math.round((this.scaleRatio - (this.scaleRatio % Constants.rangeZoom)) * 1e12) / 1e12;
      }
    }

    if (newRatio > Constants.maxContentZoom || newRatio < Constants.minContentZoom) {
      return;
    }

    this.scaleRatio = newRatio;
    this.viewSimulation.zoomContent(this.scaleRatio);
  }

  zoomReset() {
    this.scaleRatio = 1;
    this.viewSimulation.zoomContent(this.scaleRatio);
  }

  showNextButtonForPowerpoint() {
    return this.selectedElement && this.selectedElement.kind === ElementTypeKind.PowerPointSlideStep && !this.showQuizResult;
  }

  isNoInstructionFrame() {
   return this.selectedElement && this.selectedElement.kind === ElementTypeKind.Step && (this.selectedElement as StepElement).convertedFromFrame &&
    (!(this.selectedElement as StepElement).controlType && !(this.selectedElement as StepElement).action);
  }

  showNextButtonForNextFrame() {
    return !this.showQuizResult && (this.isNoInstructionFrame()
      || (this.isPressKeyStep() && (this.simulationType === SimulationType.Interactive || this.simulationType === SimulationType.Test)));
  }

  showNextButtonForTypeAction() {
    return this.selectedElement && (this.selectedElement as StepElement).action === ActionKind.Type
    && (this.simulationType === SimulationType.Interactive || this.simulationType === SimulationType.Test);
  }

  showNextButton() {
    return this.showNextButtonForPowerpoint()
    || this.showNextButtonForNextFrame()
    || this.showNextButtonForTypeAction()
    || (this.selectedElement && this.selectedElement.kind === ElementTypeKind.Video);
  }

  onNextStep() {
    if (this.showNextButtonForPowerpoint() || (this.selectedElement && this.selectedElement.kind === ElementTypeKind.Video)) {
      this.onNextOverVideoElement();
    } else if (this.showNextButtonForNextFrame()) {
      this.onNextOverNoteElement();
    }
  }

  fitToScreen() {
    this.scaleRatio = this.viewSimulation.fitToScreen();
  }

  onClickStepListIcon(isShow: boolean) {
    this.isShowStepList = isShow;
  }

  callSelectedElementChange(selectedElement: DocumentElement, index: number) {
    this.controlSimulation.selectedElementChange(selectedElement, index, true);
    if (this.controlSimulationStep) {
      this.controlSimulationStep.selectedElementChange(selectedElement, index);
    }
  }

  private isPressKeyStep () {
    return this.selectedElement && (this.selectedElement as StepElement).action === ActionKind.Press && (this.selectedElement as StepElement).controlType === ControlTypeKind.Key;
  }

  private clearFinishSimulationTimeoutHandle() {
    if (this.finishSimulationTimeoutHandle) {
      clearTimeout(this.finishSimulationTimeoutHandle);
      this.finishSimulationTimeoutHandle = null;
    }
    if (this.viewSimulation.cursorSelector) {
      this.viewSimulation.cursorSelector.pause();
    }
  }
}



