import { DefaultThemeAndFont } from './theme.model';
export class FontModel {
  id: number;
  name: string = DefaultThemeAndFont.FONT_NAME;
  url: string = '';
  tenantId: number;
  fontFamily: string = DefaultThemeAndFont.FONT_FAMILY;
  isCommon: boolean;

  fromJson(json: any): FontModel {
    if (!json) {
      return this;
    }

    this.id = json.id;
    this.name = json.name;
    this.url = json.url;
    this.tenantId = json.tenantId || null;
    this.isCommon = json.isCommon;

    return this;
  }

  clone(): FontModel {
    let font = new FontModel();

    font.id = this.id;
    font.name = this.name;
    font.tenantId = this.tenantId || null;
    font.fontFamily = this.fontFamily;
    font.url = this.url;
    font.isCommon = this.isCommon;
    return font;
  }
}



