import { Component } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef } from '@angular/material';

export enum MessageType {
  Success,
  Info,
  Warning,
  Error,
  Input
}

export enum InputFieldType {
  TextBox = 1,
  SelectBox = 2
}

@Component({
  moduleId: module.id,
  selector: 'message-dialog',
  templateUrl: 'message.dialog.html',
  styleUrls: ['message.dialog.scss']
})

export class MessageDialog {
  title: string = '';
  type: MessageType = MessageType.Info;
  buttons: string[] = ['OK'];
  buttonClasses: string[] = null;
  hasInputField: boolean = false;
  hasInputSuggest: boolean = false;
  inputLabel: string = 'Input';
  displayKey: string = 'name';
  inputQuery: string = '';
  inputFg: FormGroup;
  inputSuggestList: any [] = [];
  inputSuggestListTemp: any [] = [];
  showInputSuggest: boolean = false;
  inputFieldType: InputFieldType = InputFieldType.TextBox;
  htmlMessage: SafeHtml;
  InputFieldType = InputFieldType;
  showInvalidWarningMessage: boolean = false;
  invalidWarningMessage: string = null;
  emptyListWarningMessage: string = null;
  set message(msg: string) {
    this.htmlMessage = this.santinizer.bypassSecurityTrustHtml(msg);
  }

  constructor(private fb: FormBuilder, public dialogRef: MatDialogRef<MessageDialog>, private santinizer: DomSanitizer) {
    this.inputFg = fb.group({
      'Input': [null, Validators.compose([Validators.required])]
    });
  }

  buttonClicked(index: number) {
    if (this.hasInputField) {
      if (!this.inputFg.controls['Input'].valid && index === 0) {
        this.showInvalidWarningMessage = true;
        return;
      }

      this.showInvalidWarningMessage = false;

      this.dialogRef.close({button: index, data: this.inputFg.controls['Input'].value});
    } else {
      this.dialogRef.close(index);
    }
  }

  selectItem(item: any) {
    if (this.displayKey in item) {
      this.inputFg.controls['Input'].setValue(item[this.displayKey]);
    } else {
      this.inputFg.controls['Input'].setValue(item);
    }
    this.showInputSuggest = false;
  }

  filterInputSuggestList() {
    if (this.hasInputSuggest) {
      this.showInputSuggest = true;
      let query = this.inputFg.controls['Input'].value;
      if (query && query.trim() !== '') {
        this.inputSuggestListTemp = this.inputSuggestList.filter(item => {
          let result = (this.displayKey in item ? item[this.displayKey] : item).toLowerCase().indexOf(query.toLowerCase()) > -1;
          return result;
        });
      } else {
        this.inputSuggestListTemp = [];
        this.showInputSuggest = false;
      }
    }
  }
}



