import {
  Component, EventEmitter, Input, OnChanges, Output, SimpleChanges
} from '@angular/core';
import { QuizMode, MultipleQuizType, QuizElement } from '../../../../../model/document-element.model';
import { ElementTypeKind } from '../../../../../model/element-type.model';

@Component({
  moduleId: module.id,
  selector: 'multiple-quiz-option',
  templateUrl: 'multiple-quiz-option.component.html',
  styleUrls: ['quiz-option.component.scss']
})

export class MultipleQuizOptionComponent implements OnChanges {
  QuizMode: typeof QuizMode = QuizMode;
  MultipleQuizType: typeof MultipleQuizType = MultipleQuizType;

  readonly _A_CHAR_HEX_CODE: number = 0x041;

  langDisplay: any;

  @Input() element: QuizElement = new QuizElement(ElementTypeKind.MultipleQuiz);
  @Input() multipleQuizMode: QuizMode = QuizMode.Edit;
  @Input() multipleQuizType: MultipleQuizType = MultipleQuizType.MultipleChoice;
  @Input() answerOptionIndex: number = 0;
  @Input() answerOption: string = '';
  @Input() isChoose: boolean = false;
  @Input() isDelete: boolean = false;
  @Input() isDragging: boolean = false;
  @Input() isCorrect: boolean = false;

  @Output() deleteAnswerOptionIndexChange: EventEmitter<number> = new EventEmitter<number>();
  @Output() chooseAnswerOptionIndexChange: EventEmitter<number> = new EventEmitter<number>();

  @Output() isDraggingChange: EventEmitter<boolean> = new EventEmitter<boolean>();

  constructor() {
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['answerOption'] && changes['answerOption'].currentValue) {
      this.answerOption = this.answerOption.trim();
    }
  }

  get answerOptionLetterIndex(): string {
    let loop = (this.answerOptionIndex + 1) / 26;
    return String.fromCharCode(this._A_CHAR_HEX_CODE + this.answerOptionIndex % 26) + (loop > 1 ? Math.floor(loop).toString() : '');
  }

  onDelete(event: MouseEvent) {
    event.stopPropagation();
    this.deleteAnswerOptionIndexChange.emit(this.answerOptionIndex);
  }

  onChoose(event: MouseEvent) {
    if (this.multipleQuizMode === QuizMode.VerifyAnswer) {
      return;
    }

    event.stopPropagation();
    this.chooseAnswerOptionIndexChange.emit(this.answerOptionIndex);
  }


  onMouseOverDrag(value: boolean) {
    this.isDragging = value;
    this.isDraggingChange.emit(this.isDragging);
  }
}



