import {
  Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges
} from '@angular/core';
import { QuizMode, QuizElement } from '../../../../model/document-element.model';
import { ElementTypeKind } from '../../../../model/element-type.model';
import {ElementResult} from '../../../../model/quiz-element-report.model';
import {SimulationSetting} from '../../../../model/simulation.settings.model';
import {Utility} from '../../../helper/utilities';

@Component({
  moduleId: module.id,
  selector: 'fill-in-the-blank-quiz-display',
  templateUrl: 'fill-in-the-blank-quiz-display.component.html',
  styleUrls: ['fill-in-the-blank-quiz-display.component.scss']
})

export class FillInTheBlankQuizDisplayComponent implements OnInit, OnChanges {

  langDisplay: any;
  htmlDOMElement: HTMLElement = null;
  blankQuestion: string = '';
  answerMaps: string[][] = [];
  results: boolean [] = [];

  @Input() element: QuizElement = new QuizElement(ElementTypeKind.FillInTheBlankQuiz);
  @Input() fillInTheBlankQuizMode: QuizMode = QuizMode.View;
  @Input() verifyUserAnswerMode: boolean = false;
  @Input() simulationSettings: SimulationSetting = new SimulationSetting();

  @Output() submitAnswer: EventEmitter<[any, ElementResult]> = new EventEmitter<[any, ElementResult]>();
  isCorrect: boolean = false;
  totalCorrectUserAnswers: number = 0;
  dragDropTransferData: string = '';
  constructor() {
  }

  ngOnInit() {

  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['element'] && changes['element'].currentValue) {
      this.htmlDOMElement = window.document.createElement('DIV');
      this.htmlDOMElement.innerHTML = this.element.displayText;

      setTimeout(() => {
        if (!this.verifyUserAnswerMode) {
          this.reloadFillInTheBlankQuiz();
        }

        this.clearAnswer();
      }, 0);
    }
  }

  onSubmitAnswer() {
    let same = !this.answerMaps.some(([correct, ans]: [string, string]) => correct !== ans);
    this.submitAnswer.emit([this.answerMaps, new ElementResult(this.element.id, same)]);
  }

  onDragStart(event: DragEvent, guid: string) {
    if (this.isGrayOut(guid)) {
      event.preventDefault();
      return;
    }
    this.dragDropTransferData = guid;
    event.dataTransfer.setData('dragDropTransferData', guid);
  }

  onDrop(event: any) {
    if (this.verifyUserAnswerMode) {
      return;
    }

    let guid = this.dragDropTransferData;
    this.dragDropTransferData = '';

    if (event.target.dataset.blank) {
      event.target.setAttribute('style', 'color: #3CA7f1; border-bottom: 1px solid #3CA7F1; display: inline-block');
      event.target.innerHTML = this.getAnswerOption(guid);

      let ans = [event.target.dataset.blank, guid];
      let index = this.answerMaps.findIndex(([des, src]: [string, string]) => ans[0] === des);

      if (index !== -1) {
        this.answerMaps[index] = ans;
      } else {
        this.answerMaps.push(ans);
      }
    }
  }

  getAnswerOption(key: string): string {
    let option = '';
    let spanNodeList = this.htmlDOMElement.getElementsByTagName('SPAN');

    for (let i = 0, length = spanNodeList.length; i < length; i++) {
      if (spanNodeList[i].getAttribute('data-blank') === key) {
        option += spanNodeList[i].innerHTML;
      }
    }

    return option;
  }

  isGrayOut(guid: string) {
    return this.answerMaps.findIndex(([des, src]: [string, string]) => guid === src) !== -1;
  }

  clearAnswer() {
    this.answerMaps = [];

    if (!this.verifyUserAnswerMode) {
      setTimeout(() => {
        this.reloadFillInTheBlankQuiz();
      }, 0);
    }
  }

  reloadFillInTheBlankQuiz() {
    let cloneHTMLDOMElement = window.document.createElement('DIV');
    cloneHTMLDOMElement.innerHTML = this.element.displayText;
    let spanNodeList = cloneHTMLDOMElement.getElementsByTagName('SPAN');
    let uniqueGUID = '';
    for (let i = 0; i < spanNodeList.length; i++) {
      let guid = spanNodeList[i].getAttribute('data-blank');
      if (guid) {
        if (guid === uniqueGUID) {
          spanNodeList[i].outerHTML = '';
          i--;
          continue;
        }

        uniqueGUID = guid;
        spanNodeList[i].setAttribute('style', 'border-bottom: solid 1px #3CA7F1; display: inline-block; width: 100px');
        spanNodeList[i].innerHTML = '&nbsp;';
      }
    }

    this.blankQuestion = cloneHTMLDOMElement.outerHTML;
    window.document.getElementById('fill-in-the-blank-quiz-display-question').innerHTML = this.blankQuestion;
  }

  get isNext(): boolean {
    return this.answerMaps.length === this.element.blankQuizAnswerOptionKeys.length;
  }

  onVerifyUserAnswers(userAnswer: string[][]) {
    setTimeout(() => {
      this.isCorrect = !userAnswer.some(([correct, ans]: [string, string]) => correct !== ans);
      this.answerMaps = userAnswer;
      this.results = this.answerMaps.map(([correct, ans]: [string, string]) => correct === ans);
      this.totalCorrectUserAnswers = this.results.filter(correct => correct).length;
      this.fillUserAnswer();
    }, 0);
  }

  fillUserAnswer() {
    let cloneHTMLDOMElement = window.document.createElement('DIV');
    cloneHTMLDOMElement.innerHTML = this.element.displayText;
    let spanNodeList = cloneHTMLDOMElement.getElementsByTagName('SPAN');
    let uniqueGUID = '';
    for (let i = 0; i < spanNodeList.length; i++) {
      let guid = spanNodeList[i].getAttribute('data-blank');
      if (guid) {
        if (guid === uniqueGUID) {
          spanNodeList[i].outerHTML = '';
          i--;
          continue;
        }

        uniqueGUID = guid;

        let userAnswer = this.getAnswerOption(this.answerMaps[i][1]);

        if (this.answerMaps[i][0] === this.answerMaps[i][1]) {
          spanNodeList[i].setAttribute('style', 'border-bottom: solid 1px #71D545;color: #71D545;');
        } else {
          spanNodeList[i].setAttribute('style', 'border-bottom: solid 1px #F13C3C;color: #F13C3C;');
        }
        spanNodeList[i].innerHTML = userAnswer;
      }
    }

    this.blankQuestion = cloneHTMLDOMElement.outerHTML;
    window.document.getElementById('fill-in-the-blank-quiz-display-question-verify').innerHTML = this.blankQuestion;
  }

  get correctAnswerLetterIndexes(): string {
    return Utility.getCorrectAnswerLetterIndexes(this.element.blankQuizAnswers);
  }
}



