import { Observable } from 'rxjs/Observable';
import { Injectable } from '@angular/core';
import { CoreDocumentElement, DocumentElement, DocumentLinkElement, ElementTypeKind } from 'opus-content-lib';
import { OfflineService } from './offline.service';
import { DocumentModel } from '../model/document.model';

@Injectable()
export class DocumentBranchServiceV2  {
  constructor(private offlineService: OfflineService) {}

  checkAndGrayOutCoreDocumentsAreNotReadyInBookPromise(elements: DocumentElement[], isBookPublished: boolean): Observable<boolean> {
    let coreElements: CoreDocumentElement[] = DocumentElement.fetchAllElementMatchedElementType(ElementTypeKind.CoreDocument, elements)
      .map(d => d as CoreDocumentElement)
      .filter(c => c.links && c.links.length);
    let documentLinks: DocumentLinkElement[] = DocumentElement.fetchAllElementMatchedElementType(ElementTypeKind.DocumentLink, elements)
      .map(d => d as DocumentLinkElement)
      .filter(c => c.links && c.links.length);
    let documentObs: any[] = [];
    documentObs = this.composeDocumentToCheckAndGrayOutCoreDocumentsAreNotReadyInBook(elements);

    let confirm = new Promise<boolean>((resolve, reject) => {
      Observable.zip(...documentObs)
        .finally(() => {
          resolve(true);
        })
        .subscribe((documents: DocumentModel[]) => {
        documents = documents.filter(d => !(d.id === 0 && !d.name));
        coreElements.forEach(c => {
          let document = documents.find((d) => (d.id === c.links[0].documentId && c.links[0].documentId && c.links[0].documentId > 0)
            || (d.name === c.links[0].documentName && c.links[0].documentName && c.links[0].documentName.trim() !== ''));
          if (document) {
            c.isDeletedLink = document.isDocDeleted;
            c.isUnpublishedLink = document.isDocUnpublished;
          } else {
            c.isDeletedLink = true;
          }
          c.isFromBookPublished = isBookPublished;
        });
      });
    });

    return Observable.fromPromise(confirm);
  }

  private composeDocumentToCheckAndGrayOutCoreDocumentsAreNotReadyInBook(elements: DocumentElement[]): any[] {
    let coreElements: CoreDocumentElement[] = DocumentElement.fetchAllElementMatchedElementType(ElementTypeKind.CoreDocument, elements)
      .map(d => d as CoreDocumentElement)
      .filter(c => c.links && c.links.length);

    let documentLinks: DocumentLinkElement[] = DocumentElement.fetchAllElementMatchedElementType(ElementTypeKind.DocumentLink, elements)
      .map(d => d as DocumentLinkElement)
      .filter(c => c.links && c.links.length);

    let documentIds: number[] = [];
    let documentNames: string[] = [];
    coreElements.forEach(c => {
      let documentId = c.links[0].documentId;
      let documentName = c.links[0].documentName;
      if (documentId > 0) {
        documentIds.push(documentId);
      } else if (documentName && documentName.trim().length > 0) {
        documentNames.push(documentName);
      }
    });

    documentLinks.forEach(doc => {
      doc.links.forEach(link => {
        let documentId = link.documentId;
        let documentName = link.documentName;
        if (documentId > 0) {
          documentIds.push(documentId);
        } else if (documentName && documentName.trim().length > 0) {
          documentNames.push(documentName);
        }
      });
    });

    documentIds = Array.from(new Set(documentIds)); // unique
    let documentObs: any[] = [];
    documentIds.forEach((id) => {
      documentObs.push(this.offlineService.getDocumentInfo('' , id));
    });

    documentNames = Array.from(new Set(documentNames)); // unique
    documentNames.forEach((name) => {
      documentObs.push(this.offlineService.getDocumentInfo(name, 0));
    });

    return documentObs;
  }
}




