export class TestSimulationSetting {
  showStepInstruction: boolean = false;
  showControls: boolean = false;
  numberOfIncorrectAttempts: number = 0;
  showCorrectAnswers: boolean = false;

  fromJson(json: any) {
    if (!json) {
      return this;
    }

    this.showStepInstruction = json.showStepInstruction;
    this.showControls = json.showControls;
    this.numberOfIncorrectAttempts = json.numberOfIncorrectAttempts || 0;
    this.showCorrectAnswers = json.showCorrectAnswers;

    return this;
  }

  toJson() {
    return {
      showStepInstruction: this.showStepInstruction,
      showControls: this.showControls,
      numberOfIncorrectAttempts: this.numberOfIncorrectAttempts,
      showCorrectAnswers: this.showCorrectAnswers
    };
  }
}



