import {
  Component, Input, Output, EventEmitter, ViewChild, ElementRef, OnChanges,
  SimpleChanges,
  OnInit
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { AuthCacheService, CacheEntry } from 'opus-content-lib';
import { Http, ResponseContentType, Headers } from '@angular/http';
import { DocumentMetadataDocType } from '../../model/document.model';
import { DocumentTypeModel,DocumentDocTypeId } from '../../model/document-type.model';
import { SimulationSetting } from '../../model/simulation.settings.model';
import { OfflineService } from '../../services/offline.service';
declare const saveAs: any;

@Component({
  selector: 'content-toolbar',
  templateUrl: 'content-toolbar.component.html',
  styleUrls: ['content-toolbar.component.scss']
})
export class ContentToolbarComponent implements OnInit ,OnChanges {
  langDisplay: any;
  langIcons: any;
  langDoctype: any;
  langDownload: any;

  @Input() isEAssistantDoctype = false;
  // @Input() currentUser: UserDetailProfile = new UserDetailProfile();
  @Input() taskTitle: string = '';
  @Input() canViewTask: boolean = true;
  @Input() taskId: number;
  @Input() canQAPassFailTask: boolean = false;
  @Input() isViewBook: boolean = false;
  @Input() defaultDoctypeId: number = 0;
  @Input() advisorLoading: boolean = false;
  @Input() isLoading: boolean = false;
  @Input() doctypes: DocumentMetadataDocType[] = [];
  @Input() allDoctypes: DocumentTypeModel[] = [];
  @Input() inActiveInfoButton: boolean = true;
  @Input() inActiveStepListButton: boolean = true;
  @Input() blockEvent: boolean = false;
  @Input() triggerDocType: number = null;
  @Input() simulationSettings: SimulationSetting = new SimulationSetting();
  @Input() documentBranchId: number = 0;
  @Input() documentId = 0;
  @Input() isViewBookContent : boolean = false;
  @Input() doctypeSelected = 0; 
  @Output() selectedDoctypeChangedEmit: EventEmitter<DocumentTypeModel> = new EventEmitter<DocumentTypeModel>();
  @Output() clickInfoButton: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Output() clickStepListButton: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Output() downloadChangedEmit: EventEmitter<boolean> = new EventEmitter<boolean>();
  langDisplaySubMenu: any;
  currentUrl: string = '';
  isPartnerSystem: boolean = false;
  isMasterImpersonate: boolean = false;
  doctypesFiltered: DocumentTypeModel[] = [];
  documentTypeSelected: DocumentTypeModel = new DocumentTypeModel();
  timeZoneOffSet: number = new Date().getTimezoneOffset();
  hideControl = false;
  @ViewChild('contentToolbar') contentToolbarHTMLElement: ElementRef;
  @ViewChild('navHeader') navHeaderHTMLElement: ElementRef;
  @ViewChild('contentTitle') contentTitleHTMLElement: ElementRef;
  @ViewChild('contentTitleDisplay') contentTitleDisplayHTMLElement: ElementRef;
  @ViewChild('navProfile') navProfileHTMLElement: ElementRef;
  @ViewChild('contentDocType') contentDocType: ElementRef;
  isDocTypeRequired : boolean = false;
  constructor(private offlineService : OfflineService) {  }

  ngOnChanges (changes: SimpleChanges) {
    if (changes['doctypes'] || changes['allDoctypes']) {
      if (this.allDoctypes.length && this.doctypes.length) {
        this.doctypesFiltered = this.allDoctypes.filter(a => this.doctypes.filter(d => d.id === a.id).length > 0 &&
          a.id !== DocumentDocTypeId.Eassistant &&
          a.id !== DocumentDocTypeId.FollowMe);
      }
    }

    if ((changes['doctypes'] && changes['doctypes'].currentValue) || (changes['allDoctypes'] && changes['allDoctypes'].currentValue) ||
    (changes['defaultDoctypeId'] && changes['defaultDoctypeId'].currentValue)) {
      if (this.doctypes.length && this.allDoctypes.length) {
        let firstDocType = this.doctypes[0];
        let firstDocTypeId = firstDocType && firstDocType.id;
        let defaultDocId = this.hideControl ? DocumentDocTypeId.DemoSimulation : firstDocTypeId;
        let defaultDocType = this.doctypes.find(d => d.id === this.defaultDoctypeId);
        this.doctypeChanges(defaultDocType ? this.defaultDoctypeId : defaultDocId);
      }
    }

    if (changes['triggerDocType']) {
      this.doctypeChanges(this.triggerDocType);
    }
    if (changes['documentId']) {
      this.doctypeChanges(this.triggerDocType);
    }
  }

  ngOnInit(){

  }

  translateDoctype(doctypes: DocumentTypeModel[]): DocumentTypeModel[] {
    if (doctypes.length > 0) {
      doctypes = doctypes.map(doctype => {
        doctype.name = this.translateDoctypeName(doctype.id);
        return doctype;
      });
    }
    return doctypes;
  }

  translateDoctypeName(docType: number): string {
    switch (docType) {
      case 0: {
        return 'ALL DOCS';
      }
      case 8: {
        return 'FOLLOW ME';
      }
      case 1: {
        return 'STANDARD';
      }
      case 2: {
        return 'GENERIC';
      }
      case 3: {
        return 'ENDUSER';
      }
      case 4: {
        return 'DEMO SIMULATION';
      }
      case 5: {
        return 'INTERACTIVE SIMULATION';
      }
      case 6: {
        return 'TEST SIMULATION';
      }
      case 7: {
        return 'ACCESSIBILITY';
      }
      case 9: {
        return 'EASSISTANT';
      }
      default: {
        return '';
      }
    }
  }

  doctypeChanges(doctypeId: number) {
    if (this.doctypeSelected !== doctypeId && doctypeId != null) {
      let type = this.allDoctypes.find(a => a.id === doctypeId);
      if (type) {
        this.doctypeSelected = doctypeId;
        
        this.selectedDoctypeChangedEmit.emit(type);
        this.documentTypeSelected = type;
      } else if (this.allDoctypes && this.allDoctypes.length) {
        this.doctypeSelected = this.allDoctypes[0].id;
        this.selectedDoctypeChangedEmit.emit(this.allDoctypes[0]);
        this.documentTypeSelected = this.allDoctypes[0];
      }      
    }
  }

  isSimulationDoctype() {
    return this.doctypeSelected === DocumentDocTypeId.DemoSimulation || this.doctypeSelected === DocumentDocTypeId.InteractiveSimulation || this.doctypeSelected === DocumentDocTypeId.TestSimulation;
  }

  getRequiredDoctype(doctypeId : number) : boolean{
    var doctype = this.offlineService.getDoctypeById(doctypeId).shift();
    return doctype != undefined ? doctype.isRequired != null ? doctype.isRequired : false : false;
  }

  doctypeVisited(doctypeId :number)
  {
    var docType = this.offlineService.getDoctypeById(doctypeId).shift();
    return docType != undefined ? docType.isVisited && docType.isRequired && this.doctypeSelected != doctypeId : false;
  }
}



