import { Component, OnInit, ViewChild } from '@angular/core';
import { CoreDocumentElement, DocumentElement, ElementGroup, ElementTypeKind, PublicLibService, SectionElement } from 'opus-content-lib';

import { BookViewContentComponent } from '../book-view-content/book-view-content.component';

import { Title } from '@angular/platform-browser';
import { Http, ResponseContentType } from '@angular/http';
import { Input } from '@angular/core';
import { BookContentTypeModel, BookDocType, BookModel } from '../../../model/book.model';
import { DocumentMetadataModel } from '../../../model/document.model';
import { UserDetailProfile } from '../../../model/user-profile.model';
import { DocumentTypeModel } from '../../../model/document-type.model';
import { SimulationSetting } from '../../../model/simulation.settings.model';
import { OfflineService } from '../../../services/offline.service';
import { ThemeService } from '../../../services/theme.service';
import { SiteThemingService } from '../../../services/site-theming.service';
import { DocumentBranchServiceV2 } from '../../../services/document.service';
import { SiteThemeColors } from '../../../model/site-theming.model';
import { ThemeModel } from '../../../model/theme.model';
import { FontModel } from '../../../model/font.model';
import { Utility } from '../../../shared/helper/utilities';
import { AuthoringService } from '../../../services/authoring.service';

declare const saveAs: any;
@Component({
  selector: 'book-view-layout',
  templateUrl: 'book-view-layout.component.html',
  styleUrls: ['book-view-layout.component.scss']
})
export class BookViewLayoutComponent implements OnInit {
  selectedElement: DocumentElement = new DocumentElement();
  bookId: number = 0;
  book: BookModel = new BookModel();
  isMaster: boolean = false;
  toggle: boolean = false;
  document = new DocumentMetadataModel();
  isLoading: boolean = false;
  isLoadingToc: boolean = false;
  currentUser: UserDetailProfile = new UserDetailProfile();
  rate: number;
  doctypes: DocumentTypeModel[] = [];
  elements: DocumentElement[];
  loadingType: number = 0;
  fontStyleId: string = 'font-style-id';
  fontFamily: string = 'document-font';
  className: string;
  selectedDocType: DocumentTypeModel = new DocumentTypeModel();
  public elementGroups: ElementGroup [] = [];
  removedElementTypeKind: ElementTypeKind[] = [ElementTypeKind.ConsolidateTableStep, ElementTypeKind.Path];
  @ViewChild('bookViewContent') bookViewContent: BookViewContentComponent;
  isCustomeDashboard = false;
  isBookContent : boolean = false;
  @Input() elementId : string = '';
  simulationSettings : SimulationSetting = new SimulationSetting();
  isSimulationDoctypes: boolean = false;
  currentElement = null;
  requiredContentTypes : BookContentTypeModel[] = [];
  defaultDoctyeSelected : DocumentTypeModel = new DocumentTypeModel();

  constructor(private offlineService: OfflineService,
              private publicLibService: PublicLibService,
              private themeService: ThemeService,
              private titleService: Title,
              private siteThemingService: SiteThemingService,
              private http: Http,
              private documentBranchServiceV2: DocumentBranchServiceV2,
              private authoringService: AuthoringService) {}

  ngOnInit() {
    this.offlineService.loadDataBookFromSrc().subscribe(res => {
      this.document = res[0] as DocumentMetadataModel;
      let elements = res[1] as DocumentElement[];
      let elementGroups = res[2] as ElementGroup[];
      this.doctypes = res[3] as DocumentTypeModel[];
      let docTypeId = res[4] as number;
      let siteTheme = res[5] as SiteThemeColors;
      let theme = res[6] as ThemeModel;
      let font = res[7] as FontModel;
      this.currentUser =  res[8] as UserDetailProfile;
      this.book = res[9] as BookModel;
      this.simulationSettings = res[10] as SimulationSetting;
      this.requiredContentTypes = res[11] as BookContentTypeModel[];
      this.titleService.setTitle(this.document.title);
      this.siteThemingService.loadDynamicCss(siteTheme);
      this.loadTheme(theme, font);
      this.loadElementGroups(elementGroups);
      this.loadDocumentType(this.doctypes, docTypeId);

      if(this.elementId != '')
      {
        this.getElementById(elements);
        this.onSelectedElement(this.currentElement);
      }
      else{
        this.loadBookRecording(elements);
      }
      
    });
    this.authoringService.loadDataFromSrc().subscribe();
  }  

  getElementById(elements : DocumentElement[])
  {
    let filterFn = (element: DocumentElement) => {
      if (element.id === this.elementId) {
        this.currentElement = element;
      }
    };
    elements.filter((element) => {
      filterFn(element);
    if(!this.currentElement)
    {
      element.subElements.forEach(sub => {
         filterFn(sub);
        if(!this.currentElement){
          if (sub.subElements.length > 0) {
             sub.subElements.filter((step) => filterFn(step));
           }
        }
      });
    }
  });
  }

  loadDocumentType(docTypes: DocumentTypeModel[], docTypeId: number) {
    this.selectedDocType = docTypes.filter(d => d.id === docTypeId).shift();
    this.defaultDoctyeSelected = docTypes.filter(d => d.name === this.getBookTypeById()).shift();
  }
  
  loadTheme(theme: ThemeModel, font: FontModel) {
    if (theme) {
      this.themeService.theme = theme;
      if (theme.fontId) {
        if (font.isCommon) {
          this.themeService.fontFamily = font.name;
        } else {
          this.loadFont(font);
          this.themeService.fontFamily = this.fontFamily;
        }
        this.themeService.loadDynamicCss();
      } else {
        this.themeService.loadDynamicCss();
      }
      this.className = this.themeService.theme.className;
    }
  }
  getAllElementsByDoctype(doctype: DocumentTypeModel, elements: DocumentElement[]) {
    let filterFn = (eles: DocumentElement[], selectedDoctype: DocumentTypeModel) => {
      let filterElements: DocumentElement [] = [];
      if (eles && eles.length > 0) {
       // filterElements = this.filterElementsByDoctype(eles, selectedDoctype);
      }
      return filterElements;
    };
    return filterFn(elements, doctype);
  }
  // filterElementsByDoctype(elements: DocumentElement [], currentDoctype: DocumentTypeModel, allDocTypes?: DocumentTypeModel[], inEditor?: boolean): DocumentElement [] {
  //   return this.publicLibService.filterElementsByDoctype(elements, currentDoctype, allDocTypes, inEditor);
  // }

  replaceResource(element: DocumentElement) {
    let coreElement = element as any;
    if (coreElement.links && coreElement.links[0]) {
      if ((coreElement.kind === ElementTypeKind.DocumentLink ||
        coreElement.kind === ElementTypeKind.Section) && (coreElement.links[0].type === 'Pdf' || (coreElement.links[0].url && coreElement.links[0].url.indexOf('.pdf') >= 0))) {
        coreElement.links[0].url = `./data/resource/${coreElement.links[0].documentName}.pdf#view=fith`;
      } else if ((coreElement.kind === ElementTypeKind.DocumentLink ||
        coreElement.kind === ElementTypeKind.Section) && (coreElement.links[0].type === 'Webpage' || (coreElement.links[0].url && coreElement.links[0].url.indexOf('.html') >= 0))) {
        let fileHtml = coreElement.links[0].url.split('/').reverse()[0];
        coreElement.links[0].url = `./data/resource/${coreElement.links[0].documentName}/${fileHtml}`;
      }
    }
    if (element.subElements && element.subElements.length > 0) {
      element.subElements.forEach(subElement => {
        this.replaceResource(subElement);
      })
    }
  }

  loadBookRecording(elements: DocumentElement[]) {
    elements.forEach(element => {
      this.replaceResource(element);
    });
    this.elements = elements;
    // Filter remove table of content element and core document element with no document added in links
    this.elements = this.elements.filter(e => e.kind !== ElementTypeKind.TableOfContent && !(e instanceof CoreDocumentElement && e.links.length <= 0));
    //this.documentBranchServiceV2.checkAndGrayOutCoreDocumentsAreNotReadyInBookPromise(this.elements, this.book.isPublished).finally(() => {
      this.isLoadingToc = false;
      if (this.book.isPublished) {
        this.elements = this.elements.filter(e => !Utility.isGrayedOutElement(e));
      }
      this.setOverrideSectionNumber(this.elements);
      DocumentElement.renameSectionElements(this.elements);
    //}).subscribe();
    let element = this.elements.length > 0 ? this.elements[0] : new DocumentElement();
    this.onSelectedElement(element);
  }

  public setOverrideSectionNumber (elements : DocumentElement[])
  {
    elements.forEach(element => {
      if (element instanceof SectionElement) {
        let contentHtml = document.createElement('div');
        contentHtml.innerHTML = DocumentElement.replaceImgTagCallToS3(element.manualDisplayText ? element.manualDisplayText : element.defaultDisplayText);
        let titleDefaultIndex =  contentHtml.getElementsByClassName('section_default_title_index');
        let defaultIndex = titleDefaultIndex.length > 0 ? titleDefaultIndex[0].innerHTML.trim() : ''; 
        let contentHtmlDDT = document.createElement('div');
        contentHtmlDDT.innerHTML = DocumentElement.replaceImgTagCallToS3(element.defaultDisplayText);
        let titleDefaultIndexDDT =  contentHtmlDDT.getElementsByClassName('section_default_title_index');
        let defaultIndexDDT = titleDefaultIndexDDT.length > 0 ? titleDefaultIndexDDT[0].innerHTML.trim() : '';

        if(defaultIndexDDT.length > 0)
        {
          if(defaultIndex.length > 0 && defaultIndex != '<br>'){
            element.displaySectionIndex = true;
          }
          else{
            element.displaySectionIndex = false;
          }
        }

        element.overrideSectionNumber = element.sectionNumber != defaultIndex && defaultIndex != '<br>' ? defaultIndex : '';

        if (element.subElements.length > 0) {
          this.setOverrideSectionNumber(element.subElements);
        }
      }
    });
  }

  selectedTOCElementChanged(element: DocumentElement) {
    this.onSelectedElement(element);
  }

  onSelectedElement(element: DocumentElement) {
    this.isBookContent = false;
    this.selectedElement = element;
    this.offlineService.resetRequiredTypeVisited(false);
    
    if (element instanceof CoreDocumentElement) {
      if(!element.isUnpublishedLink)
        this.isBookContent = true;
        this.selectedDocType = this.doctypes[0];
    }

    if (this.bookViewContent) {
      this.bookViewContent.scrollContentToTop(element);
    }
  }

  loadFont(f: FontModel) {
    let fontId = f.url ? f.url.split('/').pop() : '';
    if (!fontId) {
      return;
    }
    let urlOffline = `./data/resource/${fontId}`;

    let style = document.getElementById(`${this.fontStyleId}`);
    if (style) {
      style.remove();
    }
    let newStyle = document.createElement('style');
    newStyle.id = `${this.fontStyleId}`;
    newStyle.appendChild(document.createTextNode(`\
@font-face {\
    font-family: ${this.fontFamily};\
    src: url(${urlOffline}) format('truetype');
}\
`));
    document.head.appendChild(newStyle);
  }

  getBookTypeById(): string {
    if (this.currentUser && this.currentUser.bookType) {
      switch (this.currentUser.bookType) {
        case BookDocType.Standard: {
          return 'Standard';
        }
        case BookDocType.Enduser: {
          return 'Enduser';
        }
        case BookDocType.Accessibility: {
          return '508';
        }
        default: {
          return 'Generic';
        }
      }
    } else {
      return 'Generic';
    }
  }
  loadElementGroups(elementGroups: ElementGroup[]) {
    this.elementGroups = elementGroups.map(group => {
      group.types = group.types.filter(type => !this.removedElementTypeKind.some(k => k === type.kind));
      return group;
    });
    this.publicLibService.setElementGroup(this.elementGroups);
  }

  selectDoctypeChange(type: DocumentTypeModel) {
    this.selectedDocType = type;
    this.isSimulationDoctypes = this.selectedDocType.isSimulation;
    this.requiredContentTypes.filter(t => t.documentTypeId == type.id).map(t=>t.isVisited = true);
    }

   
}



