import {
  Component,
  Input,
  Output,
  EventEmitter,
  AfterViewInit,
  ViewChild,
  ElementRef,
  SimpleChanges,
  OnChanges,
  DoCheck
} from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import {SimulationSetting} from '../../model/simulation.settings.model';
import {DocumentElement, NoteElement, StepElement} from '../../model/document-element.model';
import {ElementTypeKind} from '../../model/element-type.model';
import {Utility} from '../../shared/helper/utilities';
import { Rect } from '../../model/rect.model';
import { NoteElementConstants } from '../../shared/helper/constants';

const interact = require('interactjs');

@Component({
  selector: 'note-info-box',
  templateUrl: 'note-info-box.component.html',
  styleUrls: ['note-info-box.component.scss']
})
export class NoteInfoBoxComponent implements AfterViewInit, OnChanges {
  @Input() isShowResumeClick: boolean = true;
  @Input() dropZone: string = '';
  @Input() simulationSettings: SimulationSetting = new SimulationSetting();
  @Input() containerWidth: number = 0;
  @Input() scale: number = 1;
  @Input() containerHeight: number = 0;
  @Input() controlRect: any;
  @Input() isPlay: boolean = false;
  @Input() element: DocumentElement = null;
  @Input() screenImage: any = null;
  @Input() bubbleRect: Rect;
  @Output() resumePlayEmit: EventEmitter<any> = new EventEmitter<any>();
  @ViewChild('noteElementDrag') noteElementDrag: ElementRef;
  @ViewChild('noteElementContainer') noteElementContainer: ElementRef;
  langDisplay: any;
  bufferOffset: number = 20;
  currentNoteIndex: number = 0;
  noteList: NoteElement[] = [];

  constructor() {
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['containerWidth'] && changes['containerWidth'].currentValue && changes['containerWidth'].firstChange || 
        changes['containerHeight'] && changes['containerHeight'].currentValue && changes['containerHeight'].firstChange) {
      this.handleSetPositionNote();
    }
    if (changes['element'] && changes['element'].currentValue) {
      this.initNoteList();
      this.handleSetPositionNote();
    }
    if (changes['bubbleRect'] && changes['bubbleRect'].currentValue) {
      this.handleSetPositionNote();
    }
  }

  initNoteList() {
    this.noteList = [];
    if (this.isNoteSingle) {
      this.noteList.push(this.element as NoteElement);
    } else {
      this.noteList = this.element.subElements.filter(s => s.kind === ElementTypeKind.Note).map(m => m as NoteElement);
    }
  }

  ngAfterViewInit() {
    interact('.note-area-move-able')
      .draggable({
        inertia: true,
        restrict: {
          restriction: '.' + this.dropZone,
          endOnly: false,
          elementRect: {top: 0, left: 0, bottom: 1, right: 1}
        },
        autoScroll: false,
        allowFrom: '.mdi-drag',
        onmove: (event: any) => {
          this.dragMoveListener(event);
        },
      });
    this.handleSetPositionNote();
  }

  clickResume() {
    this.isPlay = !this.isPlay;
    this.resumePlayEmit.emit(this.isPlay);
  }

  dragMoveListener(event) {
    let target = event.target;
    let x = (parseFloat(target.getAttribute('data-x')) || 0) + event.dx;
    let y = (parseFloat(target.getAttribute('data-y')) || 0) + event.dy;

    target.style.webkitTransform =
      target.style.transform =
        'translate(' + x + 'px, ' + y + 'px)';
    target.setAttribute('data-x', x);
    target.setAttribute('data-y', y);
  }

  isNoteOverlapControl(controlRect, noteRect) {
    return !(controlRect.right < noteRect.left || controlRect.left > noteRect.right || controlRect.bottom < noteRect.top || controlRect.top > noteRect.bottom);
  }

  getValueInRange(x, min, max) {
    if (x >= min && x <= max) {
      return x;
    }
    if (x < min) {
      return min;
    } else if (x > max) {
      return max;
    }
  }

  getNoteContent() {
    if (this.noteList.length) {
      if (this.currentNoteIndex === this.noteList.length) {
        this.currentNoteIndex = this.currentNoteIndex - 1;
      }
      return this.cleanNoteContent(this.noteList[this.currentNoteIndex].displayText);
    }
    return '';
  }

  cleanNoteContent(noteContent: string) {
    return noteContent
      .replace(/fa fa-question-circle/g, '')
      .replace(/fa fa-question/g, '')
      .replace('&nbsp;', '');
  }

  handleSetPositionNote() {
    if (!this.noteElementDrag.nativeElement) {
      return;
    }

    if (!this.controlRect) {
      this.controlRect = {left: 0, top: 0, width: 0, height: 0};
    }

    let leftNoteElement, topNoteElement;
    let note: NoteElement;

    if (this.element.kind === ElementTypeKind.Note) {
      note = this.element as NoteElement;
    } else {
      let subNote = this.element.subElements.filter(s => s.kind === ElementTypeKind.Note);
      if (subNote.length) {
        note = subNote[0] as NoteElement;
      }
    }

    if (note) {
      if (note.bounds.left === 0 && note.bounds.top === 0) {
        let noteClientRectTop = 0;
        if (this.screenImage !== null) {
          let screenImageRect = this.screenImage.getBoundingClientRect();
          noteClientRectTop = screenImageRect.top - 64; //skip toolbar
          if (window.document.querySelector('.btn-next')) {
            noteClientRectTop += 80;
          }
        }
        leftNoteElement = this.containerWidth - this.noteElementContainer.nativeElement.offsetWidth - this.bufferOffset;// - noteClientRectLeft;
        topNoteElement = this.containerHeight - this.noteElementContainer.nativeElement.offsetHeight - this.bufferOffset;// - noteClientRectTop
        ;

        if (this.scale > 1) {
          leftNoteElement = leftNoteElement / this.scale;
          topNoteElement = topNoteElement / this.scale;
        } else {

          leftNoteElement = leftNoteElement * this.scale;
          topNoteElement = topNoteElement * this.scale;
        }
      } else {
        leftNoteElement = (note && note.bounds && note.bounds.left != null) ? Math.max(0, note.bounds.left) : 0;
        topNoteElement = (note && note.bounds && note.bounds.top != null) ? Math.max(0, note.bounds.top) : 0;
      }

      let screenRect = null;
        if ((this.element as StepElement).screenRect) {
          screenRect = (this.element as StepElement).screenRect;
        } else if ((this.element as NoteElement).refBackgroundStepElement) {
          screenRect = (this.element as NoteElement).refBackgroundStepElement.screenRect;
        }

        if (screenRect) {
          topNoteElement = topNoteElement - (screenRect.top);
          leftNoteElement = leftNoteElement - (screenRect.left);
        }
    }

    let result = {
      rect: new Rect(leftNoteElement, topNoteElement, note.bounds.width < NoteElementConstants.DefaultWidth ? NoteElementConstants.DefaultWidth : note.bounds.width,
        note.bounds.height< NoteElementConstants.DefaultHeight ? NoteElementConstants.DefaultHeight : note.bounds.height),
      //rect: new Rect(leftNoteElement, topNoteElement, this.noteElementContainer.nativeElement.offsetWidth, this.noteElementContainer.nativeElement.offsetHeight),
      overlap: false
    };

    let maxRight = this.containerWidth / this.scale - this.noteElementContainer.nativeElement.offsetWidth;
      let maxTop = this.containerHeight / this.scale - this.noteElementContainer.nativeElement.offsetHeight;
      if (result.rect.left > maxRight) {
        result.rect.left = maxRight;
      }
      if (result.rect.top > maxTop) {
        result.rect.top = maxTop;
      }
      
      let scaleY = 1;
      const imageBlock = document.getElementById('simulation-view-image');
      if (imageBlock) {
        const height = imageBlock.getBoundingClientRect().height || 0;
        const actualImage = parseFloat(imageBlock.style.height) || 1; // Default to 1 to avoid division by 0
        if (actualImage > 0) {
          scaleY = height / actualImage;
        }
      }

      if(note.isCustomSize){
        this.noteElementContainer.nativeElement.style.top = `${result.rect.top * scaleY}px`;
        this.noteElementContainer.nativeElement.style.left = `${result.rect.left * scaleY}px`;
        this.noteElementContainer.nativeElement.style.height = `${result.rect.height * scaleY}px`;
        this.noteElementContainer.nativeElement.style.width = `${result.rect.width * scaleY}px`;
      } else {
        this.noteElementContainer.nativeElement.style.top = `100px`;
        this.noteElementContainer.nativeElement.style.left = `100px`;
        this.noteElementContainer.nativeElement.style.height = `150px`;
        this.noteElementContainer.nativeElement.style.width = `500px`;
      }
   
    this.noteElementContainer.nativeElement.setAttribute('default-x', result.rect.left);
    this.noteElementContainer.nativeElement.setAttribute('default-y', result.rect.top);

  }

  previousNote() {
    this.currentNoteIndex = this.currentNoteIndex - 1;
  }

  nextNote() {
    this.currentNoteIndex = this.currentNoteIndex + 1;
  }

  get isNoteSingle() {
    return this.element.kind === ElementTypeKind.Note;
  }
}



