import { StepInstructionSetting } from './step-instruction.setttings.model';
import { DemoSimulationSetting } from './demo-simulation.settings.model';
import { TestSimulationSetting } from './test-simulation.settings.model';

export class SimulationSetting {
  stepInstructionSetting: StepInstructionSetting;
  demoSimulationSetting: DemoSimulationSetting;
  testSimulationSetting: TestSimulationSetting;

  constructor () {
    this.stepInstructionSetting = new StepInstructionSetting();
    this.demoSimulationSetting = new DemoSimulationSetting();
    this.testSimulationSetting = new TestSimulationSetting();
  }

  fromJson(json: any) {
    if (!json) {
      return this;
    }

    this.stepInstructionSetting = this.stepInstructionSetting.fromJson(json.stepInstructionSetting);
    this.demoSimulationSetting = this.demoSimulationSetting.fromJson(json.demoSimulationSetting);
    this.testSimulationSetting = this.testSimulationSetting.fromJson(json.testSimulationSetting);

    return this;
  }

  toJson() {
    return {
      stepInstructionSetting: this.stepInstructionSetting.toJson(),
      demoSimulationSetting: this.demoSimulationSetting.toJson(),
      testSimulationSetting: this.testSimulationSetting.toJson()
    }
  }
}



