import { Component, OnDestroy, OnInit } from '@angular/core';
import { Constants } from './shared/helper';

declare var dataJson: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent 
{
  title = 'app';
  elementId : string = '';
  lmsConnected : boolean = false;
  currentElementId : string = '';
  processedUnload : boolean = false;
  startTimeStamp : Date = null;
constructor(){
  var queryString = document.location.search;
  console.log(queryString);

  if(queryString){
    this.elementId = queryString.replace("?elementId=","");
}
}

get isBook() {
  return dataJson.scormType === Constants.BOOK_SCORM;
}
}



