import {
  AfterViewInit,
  ApplicationRef,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  Output,
  Renderer,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import {
  AudioElement,
  DocumentElement,
  NoteElement,
  PathElement,
  PowerPointSlideStepElement,
  StepElement
} from '../../../model/document-element.model';
import {DocumentMetadataModel} from '../../../model/document.model';
import {AuthoringService} from '../../../services/authoring.service';
import {ElementTypeCategory, ElementTypeKind} from '../../../model/element-type.model';
import {Rect} from '../../../model/rect.model';
import {Point} from '../../../model/point.model';
import {DomSanitizer} from '@angular/platform-browser';
import {Utility} from '../../../shared/helper/utilities';
import {DocumentTypeModel, SimulationType} from '../../../model/document-type.model';
import {SimulationSetting} from '../../../model/simulation.settings.model';
import {ActionKind, ControlTypeKind} from '../../../model/recorder-event.model';
import {ElementResult, QuizElementReportModel} from '../../../model/quiz-element-report.model';
import {TrueFalseQuizDisplayComponent} from '../../../shared/component/recording/quiz-element-display/true-false-quiz-display.component';
import {MultipleQuizDisplayComponent} from '../../../shared/component/recording/quiz-element-display/multiple-quiz-display.component';
import {FillInTheBlankQuizDisplayComponent} from '../../../shared/component/recording/quiz-element-display/fill-in-the-blank-quiz-display.component';
import {ImageCanvasComponent} from '../../../shared/image-canvas/image-canvas.component';
import {Constants} from '../../../shared/helper/constants';
import {ContentSimulationStepsComponent} from '../content-simulation-steps/content-simulation-steps.component';
import {ElementIssueModel} from 'opus-content-lib/src/app/models/element-issue.model';

declare const anime: any;
declare const SVG: any;
const CAN_PLAY_TYPE: any = {
  Maybe: 'maybe',
  Probably: 'probably',
  CannotPlay: ''
};


@Component({
  moduleId: module.id,
  selector: 'content-simulation-view',
  templateUrl: 'content-simulation-view.component.html',
  styleUrls: ['content-simulation-view.component.scss']
})

export class ContentSimulationViewComponent implements OnChanges, AfterViewInit {
  @Input() document: DocumentMetadataModel = new DocumentMetadataModel();
  @Input() element: DocumentElement = null;
  @Input() simulationType: number = 0;
  @Input() simulationSettings: SimulationSetting = new SimulationSetting();
  @Input() quizAnswerReport: QuizElementReportModel = new QuizElementReportModel();
  @Input() showQuizResult: boolean = false;
  @Input() issues: ElementIssueModel[] = [];
  @Input() elements: DocumentElement [] = [];
  @Input() selectedDoctype: DocumentTypeModel = null;
  @Output() finishInitSimulationEmit: EventEmitter<any> = new EventEmitter<any>();
  @Output() finalInteractiveEmit: EventEmitter<any> = new EventEmitter<any>();
  @Output() finishTypingSimulation: EventEmitter<any> = new EventEmitter<any>();
  @Output() finishQuizEmit: EventEmitter<any> = new EventEmitter<any>();
  @Output() quizAnswerReportChange: EventEmitter<QuizElementReportModel> = new EventEmitter<QuizElementReportModel>();
  @Output() startOverEmit: EventEmitter<any> = new EventEmitter<any>();
  @Output() completeEmit: EventEmitter<any> = new EventEmitter<any>();
  @Output() nextOverVideoElementEmit: EventEmitter<any> = new EventEmitter<any>();
  @Output() nextOverNoteElementEmit: EventEmitter<any> = new EventEmitter<any>();
  @Output() nextOverFrameElementEmit: EventEmitter<any> = new EventEmitter<any>();
  @Output() selectChildElement: EventEmitter<number> = new EventEmitter<number>();
  @Output() onPauseSimulation: EventEmitter<any> = new EventEmitter<any>();
  @Output() viewQuizReportDetail: EventEmitter<[DocumentElement, number]> = new EventEmitter<[DocumentElement, number]>();
  @ViewChild('cursor') cursor: ElementRef;
  @ViewChild('screenImage') screenImage: ElementRef;
  @ViewChild('videoContainer') videoContainer: ElementRef;
  @ViewChild('bubble') bubble: ElementRef;
  @ViewChild('bubbleViewReport') bubbleViewReport: ElementRef;
  @ViewChild('triangleViewReport') triangleViewReport: ElementRef;
  @ViewChild('screenImageViewReport') screenImageViewReport: ElementRef;
  @ViewChild('imageContainer') imageContainer: ElementRef;
  @ViewChild('imageBlock') imageBlock: ElementRef;
  @ViewChild('hint') hint: ElementRef;
  @ViewChild('soundClickAudio') soundClickAudio: ElementRef;
  @ViewChild('audio') audio: ElementRef;
  @ViewChild('typingSimulation') typingSimulation: ElementRef;
  @ViewChild('typingAudio') typingAudio: ElementRef;
  @ViewChild('imageCanvas') imageCanvas: ImageCanvasComponent;
  @ViewChild(TrueFalseQuizDisplayComponent) trueFalseQuizRef: TrueFalseQuizDisplayComponent;
  @ViewChild(MultipleQuizDisplayComponent) multipleQuizRef: MultipleQuizDisplayComponent;
  @ViewChild(FillInTheBlankQuizDisplayComponent) fillQuizRef: FillInTheBlankQuizDisplayComponent;
  @ViewChild('noteelement') noteelement: ElementRef;
  @ViewChild('instructionTail') instructionTailElement: ElementRef;
  @ViewChild('controlSimulationStep') controlSimulationStep: ContentSimulationStepsComponent;

  screenImageUrl: string = '';
  cursorSelector: any = null;
  isStepElement: boolean = false;
  finishedLoadImage: boolean = false;
  numberOfWrongClick: number = 0;
  simulationRight: number = 0;
  simulationTop: number = 0;
  simulationLeft: number = 0;
  simulationBottom: number = 0;
  audioResourceUrlQueue: any [] = [];
  isPlayingAudio: boolean = false;
  isPlayingSimulation: boolean = false;
  showStepInstructions: boolean = false;
  handleTimer: any = null;
  charNum: number = 0;
  viewReportDetailMode: boolean = false;
  langDisplay: any;
  isCustomize: boolean = false;
  isSecondaryStandaloneNote: boolean = false;
  videoResourceUrls: any[] = [];
  instructionTail: any = null;
  readonly _NUMBER_OF_INCORRECT_ATTEMPTS = 3;
  elementTypeKind: typeof ElementTypeKind = ElementTypeKind;
  isAccurateTyping: boolean = true;
  rootURL: string = '';
  showNotePassGuide: boolean = false;
  percentZoom: number = 1;
  finishSetPosStepInstruction: string;
  isShowPlayNote: boolean = false;
  percentZoomDefault: number = 1;
  viewInit: boolean = false;
  scrollTimeout = null;
  isKeyControl: boolean = false;

  constructor(private authoringService: AuthoringService,
              private sanitazer: DomSanitizer,
              public _renderer: Renderer,
              private appRef: ApplicationRef) {
    const href = window.location.href;
    this.rootURL = href.substring(0, href.lastIndexOf('/')) + '/';
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['element'] && changes['element'].currentValue) {
      this.finishSetPosStepInstruction = 'hidden';
      if (this.imageContainer && this.imageContainer.nativeElement) {
        this.imageContainer.nativeElement.scrollLeft = 0;
        this.imageContainer.nativeElement.scrollTop = 0;
      }
      this.isSecondaryStandaloneNote = false;

      if (this.element.kind === ElementTypeKind.Note) {
        this.isSecondaryStandaloneNote = (this.element as NoteElement).refBackgroundStepElement
          && (this.element as NoteElement).refBackgroundStepElement.screenImage !== '';
      }

      this.clearInstructionTail();
      this.startSimulationElement();

      this.isCustomize = this.element.manualDisplayText !== null && this.element.manualDisplayText !== undefined;
      this.showNotePassGuide = this.simulationType === SimulationType.Demo
        || this.simulationType === SimulationType.Interactive
        || this.simulationType === SimulationType.Test ? this.simulationSettings.demoSimulationSetting.pauseDemoOnNotes : false;
    }

    if (changes['simulationSettings'] && changes['simulationSettings'].currentValue) {
      if (!this.simulationSettings.stepInstructionSetting.stepInstructionFont) {
        this.simulationSettings.stepInstructionSetting.stepInstructionFont = 'Open Sans';
      } else if (this.simulationSettings.stepInstructionSetting.isCommonFont === false) {
        this.loadFont(this.simulationSettings.stepInstructionSetting.stepInstructionFont);
      }
      this.showStepInstructions = this.simulationType === SimulationType.Test ?
        this.simulationSettings.testSimulationSetting.showStepInstruction : true;
    }


    if (changes['showQuizResult'] && changes['showQuizResult'].currentValue) {
      setTimeout(() => {
        if (this.controlSimulationStep) {
          this.controlSimulationStep.initViewModes(this.elements);
          this.controlSimulationStep.onReportQuiz();
        }
      }, 100);
    }

  }

  ngAfterViewInit() {
    this.viewInit = true;
  }

  startSimulationElement() {
    if (!this.element) {
      return;
    }

    if (this.showQuizResult) {
      return;
    }

    if (this.element.kind === ElementTypeKind.Step) {
      this.screenImageUrl = this.authoringService.getResourceUrl(this.document.name, (this.element as StepElement).screenImage, true) + '.png';
      this.isStepElement = true;
      this.isKeyControl = (this.element as StepElement).controlType === ControlTypeKind.Key;
      this.initAudioResourceUrlQueue();
    } else if (this.element.kind === ElementTypeKind.Fork) {
      if (this.element.subElements && this.element.subElements.length > 0) {
        this.element.subElements.forEach(sub => {
          if (sub.kind === ElementTypeKind.Path && sub.subElements && sub.subElements.length > 0) {
            for (let i = 0; i < sub.subElements.length; i++) {
              if (sub.subElements[i].kind === ElementTypeKind.Step) {
                (sub as PathElement).screenImageUrl = this.authoringService.getResourceUrl(this.document.name, (sub.subElements[i] as StepElement).screenImage, true) + '.png';
                break;
              }
            }
          }
        });
      }
    } else {
      this.isStepElement = false;

      if (this.isSecondaryStandaloneNote) {
        this.screenImageUrl = this.authoringService.getResourceUrl(this.document.name,
          (this.element as NoteElement).refBackgroundStepElement.screenImage, true) + '.png';

      } else if (this.element.kind === ElementTypeKind.PowerPointSlideStep) {
        this.screenImageUrl = this.authoringService.getResourceUrl(this.document.name,
          (this.element as PowerPointSlideStepElement).slideImage, true) + '.png';
        this.initAudioResourceUrlQueue();
        this.initVideoResourceUrls();
      }
    }

    this.finishedLoadImage = false;
    this.numberOfWrongClick = 0;
    this.isAccurateTyping = true;
    if (this.hint && this.hint.nativeElement) {
      this.hint.nativeElement.className = this.hint.nativeElement.className.replace(/ visible/g, '');
    }
    if (this._renderer && this.typingSimulation && this.typingSimulation.nativeElement) {
      this._renderer.setElementStyle(this.typingSimulation.nativeElement, 'border-color', 'transparent');
      this._renderer.setElementProperty(this.typingSimulation.nativeElement, 'value', '');
    }
  }

  initAudioResourceUrlQueue() {
    if (this.audio) {
      this.audio.nativeElement.pause();
      this.audio.nativeElement.src = '';
    }

    if (this.element.kind === ElementTypeKind.PowerPointSlideStep
      && (this.element as PowerPointSlideStepElement).slideAudios
      && (this.element as PowerPointSlideStepElement).slideAudios.length > 0) {
      this.audioResourceUrlQueue = (this.element as PowerPointSlideStepElement).slideAudios.map((audio) => {
        return {
          resourceUrl: this.authoringService.getResourceUrl(this.document.name, audio) + '.mp3',
          type: 'audio/mp3'
        };
      }).filter(obj => obj && obj.resourceUrl && obj.resourceUrl !== DocumentElement.EmptyResource);
      return;
    }

    this.audioResourceUrlQueue = this.element.subElements.filter(e => this.authoringService.getElementTypeById(e.elementTypeId).category === ElementTypeCategory.Audio).map(e => {
      return {
        resourceUrl: this.authoringService.getResourceUrl(this.document.name, (e as AudioElement).resourceId) + '.mp3',
        type: (e as AudioElement).resourceType
      };
    }).filter(obj => obj && obj.resourceUrl && obj.resourceUrl !== DocumentElement.EmptyResource);
  }

  initVideoResourceUrls() {
    if (this.element.kind === ElementTypeKind.PowerPointSlideStep
      && (this.element as PowerPointSlideStepElement).slideVideos
      && (this.element as PowerPointSlideStepElement).slideVideos.length > 0) {
      this.videoResourceUrls = (this.element as PowerPointSlideStepElement).slideVideos
        .map((video) => {
          return {
            url: this.authoringService.getResourceUrl(this.document.name, video.slideVideo),
            bound: video.slideVideoBound
          };
        });
      return;
    }
    this.videoResourceUrls = [];
  }

  startSimulationWithoutChangeElement(cachedImage?: boolean) {
    if (this.finishedLoadImage || cachedImage) {
      this.initAudioResourceUrlQueue();
      this.finishInitSimulationObject();
    }
  }

  initSimulation() {
    if (!this.finishedLoadImage) {
      return;
    }

    if (!this.cursor) {
      return;
    }

    if (!this.isStepElement) {
      return;
    }

    let element = this.element as StepElement;
    let movementTime = this.simulationSettings.demoSimulationSetting.movementTime * 1000;
    this.cursor.nativeElement.style.display = 'block';
    this._renderer.setElementAttribute(this.cursor.nativeElement.firstElementChild, 'fill', '#ffffff');

    let translateXValue = element.simRect.left - this.simulationLeft + element.simRect.width / 2;
    let translateYValue = element.simRect.top - this.simulationTop + element.simRect.height / 2
    if (this.isKeyControl) {
      translateXValue = null;
      translateYValue = null;
    }

    this.cursorSelector = anime({
      targets: this.cursor.nativeElement,
      translateX: {
        value: translateXValue,
        duration: movementTime
      },
      translateY: {
        value: translateYValue,
        duration: movementTime
      },
      easing: 'easeInOutQuart',
      autoplay: false
    });
  }

  playSimulation() {
    if (!this.isPlayingAudio && this.audio) {
      if (!this.isAudioEmpty(this.audio.nativeElement)) {
        this.isPlayingAudio = true;
        this.audio.nativeElement.play();
        return;
      }
    }

    if (this.element && this.element.kind === ElementTypeKind.PowerPointSlideStep) {
      return;
    }

    if (this.isTypeStep()) {
      this.isPlayingSimulation = true;
      this.initTypingSimulation();
    } else {
      if (!this.cursorSelector) {
        return;
      }

      this.isPlayingSimulation = true;
      this.cursorSelector.play();
    }

    this.scrollToInteractiveArea();
    this.isShowPlayNote = true;
  }

  pauseSimulation() {
    if (this.isPlayingAudio && this.audio) {
      this.isPlayingAudio = false;
      this.audio.nativeElement.pause();
    }

    if (this.isTypeStep()) {
      this.isPlayingSimulation = false;

      if (this.typingAudio && !this.typingAudio.nativeElement.paused) {
        this.typingAudio.nativeElement.pause();
      }

      this.resetTimer();
    } else {
      if (!this.cursorSelector) {
        return;
      }

      this.isPlayingSimulation = false;
      this.cursorSelector.pause();
    }
    this.isShowPlayNote = false;
  }

  isAudioEmpty(audio: HTMLAudioElement) {
    return !audio.src || audio.readyState === 0;
  }

  finishInitSimulationObject() {
    this.finishedLoadImage = true;
    this.isPlayingSimulation = true;
    const element = this.element as StepElement;
    const screenRect = element.kind === ElementTypeKind.Note ? (this.element as NoteElement).refBackgroundStepElement.screenRect : element.screenRect;
    this.simulationRight = screenRect.left + screenRect.width;
    this.simulationBottom = screenRect.top + screenRect.height;
    this.simulationTop = screenRect.top;
    this.simulationLeft = screenRect.left;
    this.screenImage.nativeElement.style.clip = `rect(${this.simulationTop}px, ${this.simulationRight}px, ${this.simulationBottom}px, ${this.simulationLeft}px)`;
    this.screenImage.nativeElement.style.top = `-${this.simulationTop}px`;
    this.screenImage.nativeElement.style.left = `-${this.simulationLeft}px`;
    const heightMenuBrowser = 10;

    const imageBlock = this.imageBlock.nativeElement.style;
    imageBlock.height = `${this.simulationBottom - this.simulationTop}px`;
    imageBlock.width = `${this.simulationRight - this.simulationLeft}px`;

    if (this.typingSimulation && this.typingSimulation.nativeElement) {
      this.typingSimulation.nativeElement.style.display = 'none';
    }

    if (this.screenImage) {
      setTimeout(() => {
        this.drawInstructionArea();
      }, 0);
    }

    if (this.simulationType === SimulationType.Demo) {
      let pauseBefore = this.simulationSettings.demoSimulationSetting.pauseBefore * 1000;
      setTimeout(() => {
        if (this.audioResourceUrlQueue.length) {
          this.handlePlayAudio();
        } else {
          this.startSimulation();
        }
      }, pauseBefore);
    } else {
      if (this.audioResourceUrlQueue.length) {
        this.handlePlayAudio();
      }

      this.scrollToInteractiveArea();
      if (this.isTypeStep()) {
        this.showTypingInput();
      }
    }

    if (this.imageCanvas) {
      this.imageCanvas.changeElement(this.element);
    }
    this.fitWidthToDefault();

    this.imageContainer.nativeElement.style.overflow = 'auto';
  }

  getDefaultPercentZoom(screenWidth, imageWidth) {
    let percent = 1;
    if (imageWidth >= screenWidth) {
      percent = screenWidth / imageWidth;
    } else {
      percent = imageWidth / screenWidth;
    }
    if (percent > 1) {
      percent = 1;
    }
    return percent;
  }

  fitWidthToDefault(alsoForLowerRes: boolean = false) {
    let tmpZoom = this.percentZoom;
    this.zoomContent(1);
    this.percentZoomDefault = 1;
    let imageHeight = this.isStepElement ? this.simulationBottom - this.simulationTop : this.screenImage.nativeElement.offsetHeight;
    let imageWidth = this.isStepElement ? this.simulationRight - this.simulationLeft : this.screenImage.nativeElement.offsetWidth;

    let contentWidth = imageHeight > this.imageContainer.nativeElement.offsetHeight ?
      this.imageContainer.nativeElement.offsetWidth - this.calScrollbarWidth() :  this.imageContainer.nativeElement.offsetWidth;
    if (imageWidth >= contentWidth) {
      let percent = this.getDefaultPercentZoom(contentWidth, imageWidth);
      if ((imageHeight) * percent <= this.imageContainer.nativeElement.offsetHeight) {
        percent = this.getDefaultPercentZoom(contentWidth + this.calScrollbarWidth(), imageWidth);
      }
      this.percentZoomDefault = percent;
    } else if (alsoForLowerRes) {
      let percent = this.imageContainer.nativeElement.offsetWidth / (imageWidth);
      if ((imageHeight) * percent > this.imageContainer.nativeElement.offsetHeight) {
        percent = (this.imageContainer.nativeElement.offsetWidth - this.calScrollbarWidth()) / (imageWidth);
      }
      this.percentZoom = percent;
    }
    if (!alsoForLowerRes) {
      this.percentZoom = tmpZoom;
    }

    if (this.percentZoom > Constants.maxContentZoom) {
      this.percentZoom = Constants.maxContentZoom;
    }

    this.zoomContent(this.percentZoom);
    return this.percentZoom;
  }

  finishInitPowerPointSimulationObject() {
    this.finishedLoadImage = true;
    this.isPlayingSimulation = true;
    let element = this.element as PowerPointSlideStepElement;
    this.simulationRight = element.slideImageRect.left + element.slideImageRect.width;
    this.simulationBottom = element.slideImageRect.top + element.slideImageRect.height;
    this.simulationTop = element.slideImageRect.top;
    this.simulationLeft = element.slideImageRect.left;
    this.screenImage.nativeElement.style.clip = `rect(${this.simulationTop}px, ${this.simulationRight}px, ${this.simulationBottom}px, ${this.simulationLeft}px)`;
    this.screenImage.nativeElement.style.top = `-${this.simulationTop}px`;
    this.screenImage.nativeElement.style.left = `-${this.simulationLeft}px`;

    if (this.simulationType === SimulationType.Demo) {
      let pauseBefore = this.simulationSettings.demoSimulationSetting.pauseBefore * 1000;
      setTimeout(() => {
        if (this.audioResourceUrlQueue.length) {
          this.handlePlayAudio();
        } else {
          this.startSimulation();
        }
      }, pauseBefore);
    } else {
      if (this.audioResourceUrlQueue.length) {
        this.handlePlayAudio();
      }
    }
  }

  handlePlayAudio() {
    if (!this.audioResourceUrlQueue.length || !this.audio) {
      this.isPlayingAudio = false;
      this.audio.nativeElement.src = '';
      this.startSimulation();
      return;
    }

    let audio = this.audioResourceUrlQueue.shift();
    this.audio.nativeElement.autoplay = false;
    if (!this.audio.nativeElement.onerror) {
      this.audio.nativeElement.onerror = () => {
        this.audio.nativeElement.onerror = null;
        this.handlePlayAudio();
      };
    }
    this.audio.nativeElement.src = audio.resourceUrl;

    if (this.audio.nativeElement.canPlayType(audio.type) === CAN_PLAY_TYPE.CannotPlay) {
      this.handlePlayAudio();
    }
  }

  finishLoadedAudio() {
    if (!this.audio.nativeElement.src) {
      return;
    }

    this.audio.nativeElement.pause();

    if (!this.isPlayingSimulation) {
      this.audio.nativeElement.pause();
      return;
    }

    this.isPlayingAudio = true;
    const promise = this.audio.nativeElement.play();
    if (promise !== undefined) {
      promise.then(_ => {
      }).catch(error => {
        this.handlePlayAudio();
      });
    }
  }

  startSimulation() {
    if (this.simulationType !== SimulationType.Demo) {
      return;
    }

    if (this.isTypeStep()) {
      this.charNum = 0;
      this.finishInitSimulationEmit.emit('emit');
    } else {
      this.initSimulation();
      this.finishInitSimulationEmit.emit('emit');
    }
  }

  initTypingSimulation() {
    let step = this.element as StepElement;

    if (!step.valueText) {
      return;
    }

    let animBetweenKeys = this.simulationSettings.demoSimulationSetting.betweenKeys * 1000;
    // Display typing simulation input
    if (this.cursor && this.cursor.nativeElement) {
      this.cursor.nativeElement.style.display = 'none';
    }
    this.showTypingInput();
    this.typingSimulation.nativeElement.focus();

    if (!this.charNum) {
      this.typingSimulation.nativeElement.value = '';
    }

    let typingSimulationProcess = (animBetweenKeys: number) => {
      if (this.charNum >= step.valueText.length) {
        // Finish typing simulation
        this.handleTimer = setTimeout(() => {
          if (this.typingAudio && !this.typingAudio.nativeElement.paused) {
            this.typingAudio.nativeElement.pause();
          }

          this.finishTypingSimulation.emit('emit');
          this.cursor.nativeElement.style.display = 'block';
          this.typingSimulation.nativeElement.style.display = 'none';
        }, animBetweenKeys);
        return;
      }

      // Type text
      this.handleTimer = setTimeout(() => {
        this.typingAudio.nativeElement.play();
        this.typingSimulation.nativeElement.value += step.valueText.charAt(this.charNum);
        this.charNum++;
        typingSimulationProcess(animBetweenKeys);
      }, animBetweenKeys);
    };

    typingSimulationProcess(animBetweenKeys);
  }

  showTypingInput() {
    let step = this.element as StepElement;
    this.typingSimulation.nativeElement.style.display = 'block';
    this.typingSimulation.nativeElement.style.zIndex = '1';
    this.typingSimulation.nativeElement.style.left = `${step.simRect.left - step.screenRect.left}px`;
    this.typingSimulation.nativeElement.style.top = `${step.simRect.top - step.screenRect.top}px`;
    this.typingSimulation.nativeElement.style.width = `${step.simRect.width}px`;
    this.typingSimulation.nativeElement.style.height = `${step.simRect.height}px`;
    this.typingSimulation.nativeElement.focus();
  }

  resetTimer() {
    if (this.handleTimer) {
      clearTimeout(this.handleTimer);
    }

    this.handleTimer = null;
  }

  onClick(e: any) {
    if (e.target.classList.contains('info-box-action') || e.target.classList.contains('mdi-drag')
      || e.target.classList.contains('description-text') || e.target.classList.contains('action-icon')) {
      return;
    }

    if (this.element.kind !== ElementTypeKind.Step || this.imageContainer.nativeElement === e.target || this.isKeyControl) {
      return;
    }

    if ((this.hint && this.hint.nativeElement === e.target) ||
      (this.typingSimulation && this.typingSimulation.nativeElement === e.target)) {
      if (!this.isTypeStep()) {
        this.hint.nativeElement.className = this.hint.nativeElement.className.replace(/ visible/g, '');
        this.updateSubmitQuizAnswer([[], new ElementResult(this.element.id, false)]);
        this.finalInteractiveEmit.emit(e.button);
      }

      return;
    }

    let {top, left, width, height} = (this.element as StepElement).simRect;
    let simRect = new Rect().fromString(`${left - this.simulationLeft}, ${top - this.simulationTop}, ${width}, ${height}`);

    if (!Utility.isPointInArea(new Point().fromJson({
      X: e.offsetX,
      Y: e.offsetY
    }), simRect)) {
      this.numberOfWrongClick++;
    } else {
      if (!this.isTypeStep()) {
        if (this.hint) {
          this.hint.nativeElement.className = this.hint.nativeElement.className.replace(/ visible/g, '');
        }
        this.updateSubmitQuizAnswer([[], new ElementResult(this.element.id, true)]);
        this.finalInteractiveEmit.emit(e.button);
      }

      return;
    }

    let numberOfWrongClickSetting = this.simulationType === SimulationType.Test
      ? this.simulationSettings.testSimulationSetting.numberOfIncorrectAttempts
      : this._NUMBER_OF_INCORRECT_ATTEMPTS;

    if (this.numberOfWrongClick >= numberOfWrongClickSetting) {
      if (this.isTypeStep()) {
        this.isAccurateTyping = false;
        this.hintProcess(true);
      } else {
        this.hintProcess();
      }
    }
  }

  validateTyping(e: any, isKeyUp: boolean = false) {
    if (typeof e.preventDefault === 'function' && typeof e.stopPropagation === 'function') {
      e.preventDefault();
      e.stopPropagation();
    }

    if (this.simulationType === SimulationType.Demo) {
      return;
    }

    if (this.element.kind !== ElementTypeKind.Step) {
      return;
    }

    let value = this.typingSimulation.nativeElement.value;
    let step = this.element as StepElement;

    if (value === step.valueText) {
      if (!this.isAccurateTyping) {
        this.hint.nativeElement.className = this.hint.nativeElement.className.replace(/ visible/g, '');
        this.updateSubmitQuizAnswer([[], new ElementResult(this.element.id, false)]);
        this.finalInteractiveEmit.emit(null);
      }
      else {
        this._renderer.setElementStyle(this.typingSimulation.nativeElement, 'border-color', 'transparent');
        this.updateSubmitQuizAnswer([[], new ElementResult(this.element.id, true)]);
        this.finalInteractiveEmit.emit(null);
        this.typingSimulation.nativeElement.style.display = 'none';
      }
      return;
    } else {
      if (isKeyUp) {
        return;
      }

      this.numberOfWrongClick++;
      let numberOfWrongClickSetting = this.simulationType === SimulationType.Test
        ? this.simulationSettings.testSimulationSetting.numberOfIncorrectAttempts
        : this._NUMBER_OF_INCORRECT_ATTEMPTS;
      if (this.numberOfWrongClick >= numberOfWrongClickSetting) {
        this.isAccurateTyping = false;
        this.hintProcess(true);
      }
    }
  }

  onSubmitQuizAnswer([userAnswer, correct]: [any, ElementResult]) {
    this.updateSubmitQuizAnswer([userAnswer, correct]);
    this.finishQuizEmit.emit(correct.result);
  }

  updateSubmitQuizAnswer([userAnswer, correct]: [any, ElementResult]) {
    let alreadyAnswerIndex = this.quizAnswerReport.results.findIndex(r => r.id === correct.id);
    if (alreadyAnswerIndex > -1) {
      this.quizAnswerReport.results.splice(alreadyAnswerIndex, 1);
      this.quizAnswerReport.results.push(correct);
      this.quizAnswerReport.userAnswers.splice(alreadyAnswerIndex, 1);
      this.quizAnswerReport.userAnswers.push(userAnswer);
    } else {
      this.quizAnswerReport.results.push(correct);
      this.quizAnswerReport.userAnswers.push(userAnswer);
    }
    this.quizAnswerReportChange.emit(this.quizAnswerReport);
  }

  isTypeStep() {
    return this.isStepElement && (this.element as StepElement).action === ActionKind.Type;
  }

  isNoInstructionFrame() {
    return this.element.kind === ElementTypeKind.Step && (this.element as StepElement).convertedFromFrame &&
      (!(this.element as StepElement).controlType && !(this.element as StepElement).action);
  }

  scrollTo(element: ElementRef, top: number, left: number, duration: number, simulationTop: number = 0, simulationLeft: number = 0) {
    if (this.scrollTimeout) {
      clearTimeout(this.scrollTimeout);
    }
    let startTop = element.nativeElement.scrollTop,
      startLeft = element.nativeElement.scrollLeft,
      changeTop = top - startTop - simulationTop,
      changeLeft = left - startLeft - simulationLeft,
      currentTime = 0,
      increment = 20;

    // t = current time
    // b = start value
    // c = change in value
    // d = duration
    let easeInOutQuad = (t: number, b: number, c: number, d: number) => {
      t /= d / 2;
      if (t < 1) return c / 2 * t * t + b;
      t--;
      return -c / 2 * (t * (t - 2) - 1) + b;
    };

    let that = this;
    let animateScroll = function () {
      currentTime += increment;

      let offsetTop = easeInOutQuad(currentTime, startTop, changeTop, duration);
      let offsetLeft = easeInOutQuad(currentTime, startLeft, changeLeft, duration);
      element.nativeElement.scrollTop = offsetTop;
      element.nativeElement.scrollLeft = offsetLeft;

      if (currentTime < duration) {
        that.scrollTimeout = setTimeout(animateScroll, increment);
      }
    };

    animateScroll();
  }

  onViewQuizElementReportDetail(userAnswer: any, kind: ElementTypeKind) {
    this.viewReportDetailMode = true;

    setTimeout(() => {
      switch (kind) {
        case ElementTypeKind.TrueFalseQuiz:
          if (!this.trueFalseQuizRef) {
            this.viewReportDetailMode = false;
            return;
          }

          this.trueFalseQuizRef.onVerifyUserAnswer(userAnswer);
          return;
        case ElementTypeKind.MultipleQuiz:
          if (!this.multipleQuizRef) {
            this.viewReportDetailMode = false;
            return;
          }

          this.multipleQuizRef.onVerifyUserAnswer(userAnswer);
          return;
        case ElementTypeKind.FillInTheBlankQuiz:
          if (!this.fillQuizRef) {
            this.viewReportDetailMode = false;
            return;
          }

          this.fillQuizRef.onVerifyUserAnswers(userAnswer);
          return;
      }
    }, 0);
  }

  onStartOver() {
    this.startOverEmit.emit();
  }

  onComplete() {
    this.completeEmit.emit();
  }

  onNextFrameElement() {
    this.nextOverFrameElementEmit.emit();
  }

  onNextOverVideoElement() {
    this.nextOverVideoElementEmit.emit();
  }

  onNextOverNoteElement() {
    if (this.simulationType !== SimulationType.Demo && this.isStepElement) {
      return;
    }

    this.nextOverNoteElementEmit.emit();
  }

  clearInstructionTail() {
    if (!this.instructionTail) {
      return;
    }

    let parent = this.instructionTail.parent();
    parent.removeChild(parent.childNodes[0]);
    this.instructionTail = null;
  }

  onSelectedPath(pathIndex: number) {
    this.selectChildElement.emit(pathIndex);
  }

  onViewQuizReportDetail(elementInfo: [DocumentElement, number]) {
    this.viewQuizReportDetail.emit(elementInfo);
  }

  clearScrollTimeout() {
    if (this.scrollTimeout) {
      clearTimeout(this.scrollTimeout);
      this.scrollTimeout = null;
    }
  }

  get elementVariantTitle(): string {
    if (!(this.element instanceof StepElement)) {
      return '';
    }

    let enterInstruction = this.authoringService.getPressEnterKeyToAdvanceInstruction()
      .replace('<', '&lt').replace('>', '&gt');

    if (this.element.action === ActionKind.Type &&
      [SimulationType.Interactive, SimulationType.Test].indexOf(this.simulationType) !== -1) {
      return this.element.variantTitle + `<br><span>${enterInstruction}</span>`;
    }

    return this.element.variantTitle;
  }

  private scrollToInteractiveArea() {
    if (!this.imageContainer) {
      return;
    }

    let animMovementTime = 1000;

    if (this.simulationType === SimulationType.Test) {
      this.scrollTo(this.imageContainer, 0, 0, animMovementTime, this.simulationTop, this.simulationLeft);
    } else {
      const element = this.element as StepElement;
      const scrollView = window.document.querySelector('.simulation-content') as HTMLElement;
      const scrollViewWidth = scrollView.offsetWidth;
      const scrollViewHeight = scrollView.offsetHeight;
      const controlAreaRect = element.simRect;
      const bubbleRect = element.instructionArea;
      let scrollX = 0, scrollY = 0;

      if (!controlAreaRect || controlAreaRect.empty) {
        this.scrollTo(this.imageContainer, 0, 0, animMovementTime, this.simulationTop, this.simulationLeft);
        return;
      }

      const scrollToCenterOfControlAreaX = controlAreaRect.left + controlAreaRect.width / 2 - scrollViewWidth / 2;
      const scrollToCenterOfControlAreaY = controlAreaRect.top + controlAreaRect.height / 2 - scrollViewHeight / 2;

      if (this.simulationType === SimulationType.Demo) {
        animMovementTime = this.isTypeStep()
          ? this.simulationSettings.demoSimulationSetting.betweenKeys * animMovementTime
          : this.simulationSettings.demoSimulationSetting.movementTime * animMovementTime;
      }

      const scrollRectAfterScrollToCenterOfControlArea =
        new Rect(scrollToCenterOfControlAreaX, scrollToCenterOfControlAreaY, scrollViewWidth, scrollViewHeight);
      // The smallest Rect that can cover control area and bubble
      const coveredRect = Utility.smallestRectCanCover(controlAreaRect, bubbleRect);
      if (
          // If the bubble is empty then scroll to the center of control area
          bubbleRect.empty
          // If the bubble still in the scroll view after scroll to center of control area, do this
          || Utility.isContainsRect(scrollRectAfterScrollToCenterOfControlArea, bubbleRect)
          // If the scroll view cannot cover both control area and bubble, just scroll to the center of control area
          || (scrollViewWidth < coveredRect.width || scrollViewHeight < coveredRect.height)
      ) {
        if (this.element.screenArea.width * this.percentZoom < scrollViewWidth && this.element.screenArea.height * this.percentZoom < scrollViewHeight) {
          this.scrollTo(this.imageContainer, 0, 0, animMovementTime, this.simulationTop, this.simulationLeft);
        } else {
          this.scrollTo(this.imageContainer, scrollToCenterOfControlAreaY * this.percentZoom,
            scrollToCenterOfControlAreaX * this.percentZoom, animMovementTime, this.simulationTop, this.simulationLeft);
        }
      } else {
        // Scroll to the position to make both control area and the bubble centered of scroll view
        scrollX = coveredRect.left + coveredRect.width / 2 - scrollViewWidth / 2;
        scrollY = coveredRect.top + coveredRect.height / 2 - scrollViewHeight / 2;
        this.scrollTo(this.imageContainer, scrollY, scrollX, animMovementTime, this.simulationTop, this.simulationLeft);
      }
    }
  }

  scrollToCenter() {
    const scrollViewRect = window.document.querySelector('.simulation-content') as HTMLElement;
    const imageRect = this.screenImage.nativeElement;
    if (!scrollViewRect || !imageRect) {
      return;
    }

    let left = scrollViewRect.offsetWidth < imageRect.width
        ? imageRect.width / 2 - scrollViewRect.offsetWidth / 2
        : 0;
    let top = scrollViewRect.offsetHeight < imageRect.height
        ? imageRect.height / 2 - scrollViewRect.offsetHeight / 2
        : 0;

    if (left < 0) { left = 0; }
    if (top < 0) { top = 0; }

    if (left > 0 || top > 0) {
      this.scrollTo(this.imageContainer, top, left, 1000, this.simulationTop, this.simulationLeft);
    }
  }

  get styleMoveToCenter() {
    if (this.element && (this.element.kind === ElementTypeKind.TrueFalseQuiz ||
      this.element.kind === ElementTypeKind.FillInTheBlankQuiz ||
      this.element.kind === ElementTypeKind.MultipleQuiz)) {
      return { 'padding': '10px 10px 0px 10px' };
    }

    let style = { 'position': 'absolute' };
    let image = this.showQuizResult ? this.imageBlock :
      (this.isStepElement ? this.imageBlock : (this.element.kind === ElementTypeKind.Video ? this.videoContainer : this.screenImage));
    let percentZoom = this.percentZoom *  (this.element.kind === ElementTypeKind.Video ? 1 : this.percentZoomDefault);
    if (image && image.nativeElement) {
      // move to center
      // width
      let contentWidth = Math.min(this.imageContainer.nativeElement.clientWidth, this.imageContainer.nativeElement.offsetWidth);
      let realWidth = image.nativeElement.offsetWidth * percentZoom;
      if (realWidth <= contentWidth) {
        let subWidth = contentWidth - realWidth;
        if (subWidth < 0) {
          subWidth = 0;
        }
        style['left'] = `${subWidth / 2}px`;
      }
      // height
      let contentHeight = this.imageContainer.nativeElement.offsetHeight;
      let realHeight = image.nativeElement.offsetHeight * percentZoom;
      if (realHeight < contentHeight) {
        let subHeight = contentHeight - realHeight;
        if (subHeight < 0) {
          subHeight = 0;
        }
        style['top'] = `${subHeight / 2}px`;
      }
    }
    return style;
  }

  get styleHideScrollbar() {
    if ((this.imageBlock || this.screenImage) && this.imageContainer) {
      let style = { 'position': 'absolute', 'overflow': 'hidden' };
      if (this.element) {
        let height = this.element.originalScreenArea.height * this.percentZoomDefault * this.percentZoom;
        let width = this.element.originalScreenArea.width * this.percentZoomDefault * this.percentZoom;

        if (height < this.imageContainer.nativeElement.clientHeight) {
          height = this.imageContainer.nativeElement.clientHeight;
        }

        if (width < this.imageContainer.nativeElement.clientWidth) {
          width = this.imageContainer.nativeElement.clientWidth;
        }

        style['height'] = `${height}px`;
        style['width'] = `${width}px`;
      }
      return style;
    }
    return {};
  }

  fitToScreen() {
    return this.fitWidthToDefault(true);
  }

  get getOffSetContainerWidth() {
    let a =  this.imageContainer && this.imageContainer.nativeElement && this.imageContainer.nativeElement.clientWidth ? this.imageContainer.nativeElement.clientWidth : 0;
    return a;
  }

  get getOffSetContainerHeight() {
    return this.imageContainer && this.imageContainer.nativeElement && this.imageContainer.nativeElement.clientHeight ? this.imageContainer.nativeElement.clientHeight : 0;
  }


  get isSimulationTest() {
    return this.simulationType === SimulationType.Test;
  }

  get getLeftStepList() {
    if (!this.imageContainer || !this.imageContainer.nativeElement) {
      return 0;
    }
    return this.imageContainer.nativeElement.clientWidth / 2 - 250;
  }


  private onClickPlayNote(isPlay: boolean) {
    if (isPlay) {
      this.onNextFrameElement();
    } else {
      this.onPauseSimulation.emit(isPlay);
    }
  }

  private hintProcess(showHighlightColorWhenTyping?: boolean) {
    if (!this.hint) {
      return;
    }

    let element = this.element as StepElement;
    // Draw hint
    if (this.isTypeStep()) {
      if (showHighlightColorWhenTyping) {
        this._renderer.setElementStyle(this.typingSimulation.nativeElement, 'border-color', this.simulationSettings.stepInstructionSetting.clickHighlightColor);
      } else {
        this._renderer.setElementStyle(this.typingSimulation.nativeElement, 'border-color', 'red');
      }

    } else {
      this.hint.nativeElement.style.top = `${(element.simRect.top - this.simulationTop) * this.percentZoom * this.percentZoomDefault}px`;
      this.hint.nativeElement.style.left = `${(element.simRect.left - this.simulationLeft) * this.percentZoom * this.percentZoomDefault}px`;
      this.hint.nativeElement.style.width = `${element.simRect.width * this.percentZoom * this.percentZoomDefault}px`;
      this.hint.nativeElement.style.height = `${element.simRect.height * this.percentZoom * this.percentZoomDefault}px`;
      this.hint.nativeElement.style['border-width'] = `${3 * this.percentZoom}px`;
      // Show/hide
      this.hint.nativeElement.className += ' visible';

    }
  }

  private drawInstructionArea(bufferWidthWhenEmpty = 5) {
    let targetBubble = !this.viewReportDetailMode ? this.bubble : this.bubbleViewReport;
    let targetScreenImage = !this.viewReportDetailMode ? this.screenImage : this.screenImageViewReport;
    let element = this.element as StepElement;

    if (!targetBubble || !targetScreenImage) {
      return;
    }

    let bubbleArea = new Rect();

    if (element.instructionArea.empty) {
      bubbleArea.width = targetBubble.nativeElement.offsetWidth + bufferWidthWhenEmpty;
      bubbleArea.height = targetBubble.nativeElement.offsetHeight;

      let bubbleLocation = this.calcBubbleLocationBasedOnControlLocation(bubbleArea.height, element.simRect, element.screenArea.height - element.screenArea.top);

      bubbleArea.left = bubbleLocation.x;
      bubbleArea.top = bubbleLocation.y;
    } else {
      bubbleArea.width = element.instructionArea.width;
      bubbleArea.left = element.instructionArea.left - this.simulationLeft;
      bubbleArea.top = element.instructionArea.top - this.simulationTop;

      if (this.element instanceof StepElement && this.element.action === ActionKind.Type &&
        [SimulationType.Interactive, SimulationType.Test].indexOf(this.simulationType) !== -1) {
        targetBubble.nativeElement.style.width = `${bubbleArea.width}px`;

        if (bubbleArea.top + targetBubble.nativeElement.offsetHeight > element.simRect.top - 20 &&
          bubbleArea.top + targetBubble.nativeElement.offsetHeight < element.simRect.top + element.simRect.height) {
          bubbleArea.top = element.simRect.top - targetBubble.nativeElement.offsetHeight - 20;

          if (bubbleArea.top < 0) {
            bubbleArea.top = element.simRect.top + element.simRect.height + 20;
          }
        }
      }
    }

    if (this.simulationType === SimulationType.Test && !this.viewReportDetailMode) {
      bubbleArea = this.calcBubbleLocationForTestSimulationBasedOnControlLocation(bubbleArea, element.simRect, element.simRect.width, element.simRect.height, this.imageContainer.nativeElement.offsetHeight, this.imageContainer.nativeElement.offsetWidth);
      if (this.noteelement && this.noteelement.nativeElement) {
        let childElements = this.noteelement.nativeElement.children;
        if (childElements && childElements.length) {
          let maxHeight = 0;
          for (let i = 0; i < childElements.length; i++) {
            let child = childElements[i];
            if (child.children && child.children.length) {
              let note = child.children[0];
              if (note.offsetTop === 0 && note.offsetLeft === 0 && note.offsetHeight > maxHeight) {
                maxHeight = note.offsetHeight;
              }
            }
          }

          if (maxHeight > 0 && bubbleArea.top < maxHeight) {
            bubbleArea.top = maxHeight + 10;
          }
        }
        targetBubble.nativeElement.firstElementChild.style.padding = `${0.75}em`;
        targetBubble.nativeElement.style.left = `${bubbleArea.left * this.percentZoom * this.percentZoomDefault}px`;
        targetBubble.nativeElement.style.top = `${bubbleArea.top * this.percentZoom * this.percentZoomDefault}px`;
        targetBubble.nativeElement.style.width = `${bubbleArea.width}px`;

        if (this.hint && this.hint.nativeElement) {
          this.hint.nativeElement.style.top = `${element.simRect.top - this.simulationTop}px`;
          this.hint.nativeElement.style.left = `${element.simRect.left - this.simulationLeft}px`;
          this.hint.nativeElement.style.width = `${element.simRect.width}px`;
          this.hint.nativeElement.style.height = `${element.simRect.height}px`;

          if (this.isColliding(targetBubble.nativeElement, this.hint.nativeElement)) {
            targetBubble.nativeElement.style.top = `${bubbleArea.top + this.hint.nativeElement.offsetHeight + 10}px`;
          }
        }
      }
    } else {
      targetBubble.nativeElement.firstElementChild.style.padding = `${0.75}em`;
      targetBubble.nativeElement.style.left = `${bubbleArea.left * this.percentZoom * this.percentZoomDefault}px`;
      targetBubble.nativeElement.style.top = `${bubbleArea.top * this.percentZoom * this.percentZoomDefault}px`;
      targetBubble.nativeElement.style.width = `${bubbleArea.width}px`;
    }

    if (this.simulationType !== SimulationType.Test || this.viewReportDetailMode) {
      let {top, left, width, height} = element.simRect;
      let simRect = new Rect().fromString(`${left - this.simulationLeft}, ${top - this.simulationTop}, ${width}, ${height}`);
      this.drawInstructionTail(bubbleArea, simRect, targetBubble.nativeElement.offsetHeight);
    }

    let topHint = (element.simRect.top - this.simulationTop) * this.percentZoom * this.percentZoomDefault;
    let leftHint  = (element.simRect.left - this.simulationLeft) * this.percentZoom * this.percentZoomDefault;
    if (this.simulationType === SimulationType.Test
      && topHint <= targetBubble.nativeElement.offsetHeight
      && leftHint <= targetBubble.nativeElement.offsetWidth) {
      let heightHint = element.simRect.height;
      targetBubble.nativeElement.style.top = `${(topHint + heightHint + 10)}px`;
    }

    this.finishSetPosStepInstruction = 'visible';
  }

  private isColliding($div1, $div2) {
    let d1DistanceFromTop = $div1.offsetTop + $div1.offsetHeight;
    let d1DistanceFromLeft = $div1.offsetLeft + $div1.offsetWidth;

    let d2DistanceFromTop = $div1.offsetTop + $div2.offsetHeight;
    let d2DistanceFromLeft = $div1.offsetLeft + $div2.offsetWidth;

    let notColliding = (d1DistanceFromTop < $div2.offsetTop || $div1.offsetTop > d2DistanceFromTop
      || d1DistanceFromLeft < $div2.offsetLeft || $div1.offsetLeft > d2DistanceFromLeft);

    return !notColliding;
  }

  private drawInstructionTail(bubbleBound: Rect, simArea: Rect, actualHeight: number) {
    if (this.isPressKeyStep()) {
      this.clearInstructionTail();
      return;
    }

    let startPoint = new Point(), middlePoint = new Point(), endPoint = new Point();
    if (simArea.empty) {
      simArea.setDefaultBound();
    }

    if (bubbleBound.top + 20 <= simArea.top) {
      if (bubbleBound.left <= simArea.left + simArea.width / 2) {
        startPoint = new Point().fromJson({
          X: bubbleBound.left + 25,
          Y: bubbleBound.top + 10
        });

        middlePoint = new Point().fromJson({
          X: startPoint.x + 40,
          Y: bubbleBound.top + 10
        });

        endPoint = new Point().fromJson({
          X: simArea.left + simArea.width / 5,
          Y: simArea.top
        });
      } else {
        let perIncrease = this.percentZoom > 1 ? 0.4 : 1;
        startPoint = new Point().fromJson({
          X: bubbleBound.left + bubbleBound.width * perIncrease - 65,
          Y: bubbleBound.top + 10
        });

        middlePoint = new Point().fromJson({
          X: startPoint.x + 40 * perIncrease,
          Y: bubbleBound.top + 10
        });

        endPoint = new Point().fromJson({
          X: simArea.left + simArea.width - simArea.width / 5,
          Y: simArea.top
        });
      }
    } else if (bubbleBound.top > simArea.top + simArea.height) {
      if (bubbleBound.left <= simArea.left + simArea.width / 2) {
        let perIncrease = this.percentZoom > 1 ? 0.4 : 1;
        startPoint = new Point().fromJson({
          X: bubbleBound.left + 25,
          Y: bubbleBound.top + actualHeight * perIncrease - 10
        });

        middlePoint = new Point().fromJson({
          X: startPoint.x + 40,
          Y: bubbleBound.top + actualHeight * perIncrease - 10
        });

        endPoint = new Point().fromJson({
          X: simArea.left + simArea.width / 5,
          Y: simArea.top + simArea.height
        });
      } else {
        let perIncrease = this.percentZoom > 1 ? 0.4 : 1;
        startPoint = new Point().fromJson({
          X: bubbleBound.left + bubbleBound.width * perIncrease - 65,
          Y: bubbleBound.top + actualHeight * perIncrease - 10
        });

        middlePoint = new Point().fromJson({
          X: startPoint.x + 40 * perIncrease,
          Y: bubbleBound.top + actualHeight * perIncrease - 10
        });

        endPoint = new Point().fromJson({
          X: simArea.left + simArea.width - simArea.width / 5,
          Y: simArea.top + simArea.height
        });
      }
    } else if (bubbleBound.top + 20 > simArea.top) {
      if (bubbleBound.left <= simArea.left) {
        startPoint = new Point().fromJson({
          X: bubbleBound.left + bubbleBound.width / 2,
          Y: bubbleBound.top + actualHeight / 2 - 10
        });

        middlePoint = new Point().fromJson({
          X: bubbleBound.left + bubbleBound.width / 2,
          Y: bubbleBound.top + actualHeight / 2 + 10
        });

        endPoint = new Point().fromJson({
          X: simArea.left,
          Y: simArea.top + simArea.height / 2
        });
      } else {
        startPoint = new Point().fromJson({
          X: bubbleBound.left + 40,
          Y: bubbleBound.top + actualHeight / 2 - 10
        });

        middlePoint = new Point().fromJson({
          X: bubbleBound.left + 40,
          Y: bubbleBound.top + actualHeight / 2 + 10
        });

        endPoint = new Point().fromJson({
          X: simArea.left + simArea.width,
          Y: simArea.top + simArea.height / 2
        });
      }
    }

    if (this.instructionTailElement) {
      this.clearInstructionTail();
      this.instructionTail = SVG('instruction-tail').size(`${this.element.screenArea.width}px`, `${this.element.screenArea.height}px`);
      this.instructionTail.node.style.position = 'absolute';
      this.instructionTail.node.style.zIndex = 1;
      let path = this.instructionTail.path(`M ${startPoint.x} ${startPoint.y} L ${middlePoint.x} ${middlePoint.y} L ${endPoint.x} ${endPoint.y} z`);
      path.fill(this.simulationSettings.stepInstructionSetting.stepInstructionColor);
    }
  }

  private calcBubbleLocationForTestSimulationBasedOnControlLocation(bubbleArea: Rect, simArea: Rect, limitRight: number, limitBottom: number, offetHeight: number, offetWidth: number): Rect {
    let expectArea = bubbleArea.clone();
    let rightInterX = simArea.left - this.simulationLeft + simArea.width;
    let rightInterY = simArea.top - this.simulationTop + simArea.height;
    let defaultPosition = 10;
    // Calculation visible position
    if (offetWidth < rightInterX && offetHeight < rightInterY) {
      expectArea.top = simArea.top - (simArea.height / 2);
      expectArea.left = simArea.left - (simArea.width / 2);
    } else if (offetWidth < rightInterX) {
      expectArea.left = simArea.left - (simArea.width / 2);
      expectArea.top = defaultPosition;
    } else if (offetHeight < rightInterY) {
      expectArea.top = simArea.top - (simArea.height / 2);
      expectArea.left = defaultPosition;
    } else {
      expectArea.left = defaultPosition;
      expectArea.top = defaultPosition;
    }

    // Beautiful Position
    let currentBottom = Math.abs(expectArea.top + expectArea.height);
    let currentRight = Math.abs(expectArea.left + expectArea.width);

    if (expectArea.top > simArea.top) {
      expectArea.top = Math.abs(currentBottom - limitBottom) < defaultPosition ? (expectArea.top + defaultPosition > limitBottom ? limitBottom - defaultPosition : expectArea.top + defaultPosition) : expectArea.top;
    } else {
      expectArea.top = expectArea.top - defaultPosition <= 0 ? defaultPosition : expectArea.top - defaultPosition;
    }

    if (expectArea.left > simArea.left) {
      expectArea.left = Math.abs(currentRight - limitRight) ? (expectArea.left + defaultPosition > limitRight ? limitRight - defaultPosition : expectArea.left + defaultPosition) : expectArea.left;
    } else {
      expectArea.left = expectArea.left - defaultPosition <= 0 ? defaultPosition : expectArea.left - defaultPosition;
    }

    if (Utility.checkAreaCrossAnotherArea(expectArea, simArea)) {
      let lamdaWidth = Math.floor(expectArea.top / offetHeight) * offetHeight;
      expectArea.top = limitBottom - expectArea.top < expectArea.top - lamdaWidth ? expectArea.top - 200 : expectArea.top + 200;
    }

    return expectArea;
  }

  private calcYBubbleLocationBasedOnControlLocation(bubbleHeight: number, maxHeight: number) {
    let element = this.element as StepElement;
    let simHeight = element.simRect.empty ? 30 : element.simRect.height,
      simTop = element.simRect.top - this.simulationTop;
    let yLocation = simTop + simHeight + 20;

    if (bubbleHeight && (yLocation + bubbleHeight >= maxHeight) && (simTop - bubbleHeight > 0)) {
      yLocation = simTop - bubbleHeight - 20;
    }

    return yLocation;
  }

  private calcBubbleLocationBasedOnControlLocation(bubbleHeight: number, simRect: Rect, maxHeight: number): Point {
    let location = new Point();
    if (simRect.empty) {
      location.x = 5;
    } else {
      location.x = simRect.left - this.simulationLeft + (simRect.width / 2) - 25;
    }

    location.y = this.calcYBubbleLocationBasedOnControlLocation(bubbleHeight, maxHeight);

    return location;
  }

  private isPressKeyStep () {
    return (this.element as StepElement).action === ActionKind.Press && (this.element as StepElement).controlType === ControlTypeKind.Key;
  }

  private calScrollbarWidth() {
    let scrollDiv = document.createElement('div');
    scrollDiv.setAttribute('style', `width: 100px;height: 100px;overflow: scroll;position: absolute;top: -9999px;`);
    document.body.appendChild(scrollDiv);
    let width = scrollDiv.offsetWidth - scrollDiv.clientWidth;
    document.body.removeChild(scrollDiv);
    return width;
  }

  zoomContent(percent: number) {
    this.percentZoom = percent;
    this.drawInstructionArea(0);
    const numberOfWrongClickSetting = this.simulationType === SimulationType.Test
      ? this.simulationSettings.testSimulationSetting.numberOfIncorrectAttempts
      : this._NUMBER_OF_INCORRECT_ATTEMPTS;
    if (this.numberOfWrongClick >= numberOfWrongClickSetting) {
      this.hintProcess();
    }
    if (this.viewInit) {
      this.appRef.tick();
    }
  }

  get isTableCustomize(): boolean {
    let regex = new RegExp('.*?<table.*?table>.*?');
    return regex.test(this.element.displayText);
  }

  get styleZoomContent() {
    return { 'transform': `scale(${this.percentZoom * this.percentZoomDefault})` };
  }

  get ResetStyleZoom() {
    return { 'transform': `scale(${1 / this.percentZoom})` };
  }

  loadFont(fileNameOfFont: any) {
    if (!fileNameOfFont) {
      return;
    }
    const path = document.location.pathname;
    const directory = path.substring(path.indexOf('/'), path.lastIndexOf('/'));
    const arrayPath = directory.split('/');
    const folderName = arrayPath.pop()
      .replace(/\(/g, '%28')
      .replace(/\)/g, '%29');
    const urlOffline = `${arrayPath.join('/')}/${folderName}/assets/${fileNameOfFont}`;
    const style = document.getElementById(`${fileNameOfFont}`);
    if (style) {
      style.remove();
    }
    const newStyle = document.createElement('style');
    newStyle.id = `${fileNameOfFont}`;
    newStyle.appendChild(document.createTextNode(`\
    @font-face {\
        font-family: ${fileNameOfFont};\
        src: url(${urlOffline}) format('truetype');
    }\
    `));
    document.head.appendChild(newStyle);
  }

  get elementHaveChildNote() {
    return this.element.subElements.find(f => f.kind === ElementTypeKind.Note) != null;
  }

  get screenImageNativeElement() {
    return this.imageBlock ? this.imageBlock.nativeElement : null;
  }

  get screenImageURLForSecondaryNote() {
    return `${this.screenImageUrl}?t=${new Date().getTime()}`;
  }

  get isFramesFromOdarc() {
    if (this.element.kind === ElementTypeKind.Step) {
      let step = this.element as StepElement;
      if (step && step.title) {
        return ['INTRO', 'EXPLANATION', 'END'].findIndex(f => f === step.title) >= 0;
      }
    }
    return false;
  }
}



