import { Injectable } from '@angular/core';
import { DocumentElement, ElementGroup } from 'opus-content-lib';
import { ThemeModel } from '../model/theme.model';
import { UserDetailProfile } from '../model/user-profile.model';
import { FontModel } from '../model/font.model';
import { SiteThemeColors } from '../model/site-theming.model';
import { DocumentMetadataModel, DocumentModel } from '../model/document.model';
import { BookContentType, BookContentTypeModel, BookModel } from '../model/book.model';
import { DocumentTypeModel } from '../model/document-type.model';
import { Observable } from 'rxjs/Observable';
import { SimulationSetting } from '../model/simulation.settings.model';
declare var dataJson: any;
declare var elementsJson: any;
declare var metadataJson: any;
declare var documentElementsJson: any;
declare var documentMetadataJson: any;
declare var documentInfoJson: any;
declare var bookInfoJson: any;
@Injectable()
export class OfflineService {
  private json: any;
  private jsonElements: any;
  private jsonMetadata: any;
  private jsonDocumentInfo: any;
  private jsonBookInfo: any;
  public requiredTypes : BookContentTypeModel[] = [];
  public documentTypes: DocumentTypeModel[] = [];
  constructor() {}

  getDocumentMetadata(): DocumentMetadataModel {
    let document = new DocumentMetadataModel();
    if (this.jsonMetadata) {
      document = new DocumentMetadataModel().fromJson(this.jsonMetadata);
    }
    return document;
  }

  getDocumentElements(): DocumentElement[]{
    let elements: DocumentElement[] = [];
    if (this.jsonElements) {
      elements = this.jsonElements.map((doc: any) => DocumentElement.fromDto(doc));
    }
    return elements;
  }

  getAllElementGroup(): ElementGroup[]{
    let elementGroups: ElementGroup[] = [];
    if (this.json && this.json.elementTypes) {
      elementGroups = this.json.elementTypes.map((group: any) => new ElementGroup().fromJson(group));
    }
    return elementGroups;
  }

  getAllDocumentTypes(): DocumentTypeModel[] {
    if (this.json && this.json.docTypes && this.json.docTypes.length) {
      
      if(this.json && (!this.json.bookContentTypes || this.json.bookContentTypes.length == 0)){
        this.documentTypes = this.json.docTypes.map((doc: any) => new DocumentTypeModel().fromJson(doc));
        this.documentTypes = this.documentTypes.filter(d => d.id == BookContentType.Enduser || d.id == BookContentType.DemoSimulation ||
          d.id == BookContentType.InteractiveSimulation || d.id == BookContentType.TestSimulation);
      }
      else{
        this.documentTypes = this.json.docTypes.map((doc: any) => new DocumentTypeModel().fromJson(doc));
      }
    }
    return this.documentTypes;
  }


  getDocTypeId() {
    return this.documentTypes[0].id;
  }

  getSiteTheme(): SiteThemeColors{
    let siteColors = new SiteThemeColors();
    if (this.json && this.json.siteTheming) {
      siteColors = new SiteThemeColors().fromJson(this.json.siteTheming);
    }
    return siteColors;
  }

  getTheme(): ThemeModel{
    let theme = new ThemeModel();
    if (this.json && this.json.theme) {
      theme = new ThemeModel().fromJson(this.json.theme);
    }
    return theme;
  }

  getFont(): FontModel{
    let font = new FontModel();
    if (this.json && this.json.font) {
      font = new FontModel().fromJson(this.json.font);
    }
    return font;
  }
  getInfo(): DocumentModel{
    let documentInfo = new DocumentModel();
    if (this.jsonDocumentInfo) {
      documentInfo = new DocumentModel().fromJson(this.jsonDocumentInfo);
    }
    return documentInfo;
  }
  getUserProfile(): UserDetailProfile{
    let user = new UserDetailProfile();
    if (this.json && this.json.userProfile) {
      user = new UserDetailProfile().fromJson(this.json.userProfile);
    }
    return user;
  }
  
  loadDataFromSrc() {
    this.json = dataJson;
    this.jsonElements = elementsJson;
    this.jsonMetadata = metadataJson;
    return Observable.of([
      this.getDocumentMetadata(),
      this.getDocumentElements(),
      this.getAllElementGroup(),
      this.getAllDocumentTypes(),
      this.getDocTypeId(),
      this.getSiteTheme(),
      this.getTheme(),
      this.getFont(),
      this.getUserProfile()]);
  }

  loadDataBookFromSrc() {
    this.json = dataJson;
    this.jsonElements = elementsJson;
    this.jsonMetadata = metadataJson;
    this.jsonBookInfo = bookInfoJson;
    return Observable.of([
      this.getDocumentMetadata(),
      this.getDocumentElements(),
      this.getAllElementGroup(),
      this.getAllDocumentTypes(),
      this.getDocTypeId(),
      this.getSiteTheme(),
      this.getTheme(),
      this.getFont(),
      this.getUserProfile(),
      this.getBookInfo(),
      this.getSimulationSettings(),
      this.getRequiredTypes()]);
  }

  getElements(documentName: string, documentId: number):  Observable<DocumentElement[]> {
    this.jsonElements = documentElementsJson[documentName];
    if (!this.jsonElements){
      this.jsonElements = documentElementsJson[documentId];
    }
    return Observable.of(this.getDocumentElements());
  }

  getMetadata(documentName: string, documentId: number):  Observable<DocumentMetadataModel> {
    this.jsonMetadata = documentMetadataJson[documentName];
    if (!this.jsonMetadata){
      this.jsonMetadata = documentMetadataJson[documentId];
    }
    return Observable.of(this.getDocumentMetadata());
  }

  getDocumentInfo(documentName: string, documentId: number): Observable<DocumentModel>{
    this.jsonDocumentInfo = documentInfoJson[documentName];
    if (!this.jsonDocumentInfo){
      this.jsonDocumentInfo = documentInfoJson[documentId];
    }
    return Observable.of(this.getInfo());
  }

  getBookInfo(): BookModel{
    let bookInfo = new BookModel();
    if (this.jsonBookInfo) {
      bookInfo = new BookModel().fromJson(this.jsonBookInfo);
    }
    return bookInfo;
  }

  getSimulationSettings(): SimulationSetting {
    if (!this.json || !this.json.simulationSetting) {
      return new SimulationSetting();
    }

    return new SimulationSetting().fromJson(this.json.simulationSetting);
  }

  getRequiredTypes() : BookContentTypeModel[]{
    if (!this.json || !this.json.bookContentTypes || this.json.bookContentTypes.length == 0) {
      this.requiredTypes = this.getDefaultContentTypes();
      return this.requiredTypes;
    }
    if (this.json && this.json.bookContentTypes) {
      var types = this.json.bookContentTypes.map((type: any) => new BookContentTypeModel().fromJson(type));
      var isAllNull = types.every(t => t.isRequired == null);
      if(isAllNull){
        this.requiredTypes  = types.map(t => t.isRequired = true);
      }
      this.requiredTypes  = types.filter(t => t.isActive == true && t.isRequired == true);
    }
    return this.requiredTypes;
  }

  setRequiredTypeVisited(doctypeId : number){
    this.requiredTypes.filter(t => t.documentTypeId == doctypeId).map(t=>t.isVisited = true);
  }

  getDoctypeById(doctypeId : number) : BookContentTypeModel[]{
    return this.requiredTypes.filter(type => type.documentTypeId == doctypeId);
  }

  getDefaultContentTypes(){
    var defaultContentTypes :BookContentTypeModel[] = [];

    if(this.documentTypes != null){
      this.documentTypes.map(doctype =>{
        var contentType = new BookContentTypeModel();
        contentType.documentTypeId = doctype.id,
        contentType.tenantId = doctype.tenantId,
        contentType.isActive = true,
        contentType.isRequired = true
        defaultContentTypes.push(contentType);
      }
      );
    }
    return defaultContentTypes;
  }

  resetRequiredTypeVisited(visited : boolean){
    this.requiredTypes.map(t=>t.isVisited = false);
  }
}



