import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import { MessageDialog, MessageType, InputFieldType } from '../shared/dialogs/message.dialog';
import { DefaultContentMessage } from '../shared/helper/constants';
import { MatDialog, MatSnackBar, MatSnackBarConfig } from '@angular/material';

@Injectable()
export class NotificationService {
  constructor(private snackBar: MatSnackBar,
              private dialog: MatDialog) {
  }

  showErrorCode(code: number, title: string = ''): Observable<number> {
    let message = 'The application has encountered an error. Please try again.';
    return this.showMessage(message, MessageType.Error, title);
  }

  showErrorResponse(error: string, title: string = ''): Observable<number> {
    if (!error) {
      error = DefaultContentMessage.DefaultContentGeneralLog;
    }

    return this.showError(error, DefaultContentMessage.DefaultTitleGeneralLog, ['Ok']);
  }

  showSuccess(message: string, title: string = '', buttons: string[] = []): Observable<number> {
    return this.showMessage(message, MessageType.Success, title, buttons);
  }

  showInfo(message: string, title: string = '', buttons: string[] = []): Observable<number> {
    return this.showMessage(message, MessageType.Info, title, buttons);
  }

  showWarning(message: string, title: string = '', buttons: string[] = []): Observable<number> {
    return this.showMessage(message, MessageType.Warning, title, buttons);
  }

  showError(message: string, title: string = '', buttons: string[] = []): Observable<number> {
    return this.showMessage(message, MessageType.Error, title, buttons);
  }

  showToast(message: string, action: string = null, duration: number = 3000): Observable<void> {
    let config = new MatSnackBarConfig();
    config.duration = duration;
    return this.snackBar.open(message, action, config).onAction();
  }

  showToastSuccess(message: string, action: string = null, duration: number = 3000): Observable<void> {
    let config = new MatSnackBarConfig();
    config.duration = duration;
    config.extraClasses = ['toast-success'];
    return this.snackBar.open(message, action, config).onAction();
  }

  showToastError(message: string, action: string = null, duration: number = 3000): Observable<void> {
    let config = new MatSnackBarConfig();
    config.duration = duration;
    config.extraClasses = ['toast-error'];
    return this.snackBar.open(message, action, config).onAction();
  }

  showConfirmOk(message: string, title: string = '', okLabel: string = 'Ok', disableClose: boolean): Observable<boolean> {
    let buttons = [okLabel];
    return this.showMessage(message, MessageType.Info, title, buttons,null, disableClose).map(index => index === 0);
  }

  showConfirm(message: string, title: string = '', yesLabel: string = 'Yes', noLabel: string = 'No'): Observable<boolean> {
    let buttons = [yesLabel, noLabel];
    return this.showMessage(message, MessageType.Info, title, buttons).map(index => index === 0);
  }

  showConfirmCustomButton(message: string, title: string = '', yesLabel: string = 'Yes', noLabel: string = 'No', buttonClasses: string[] = []): Observable<boolean> {
    let buttons = [yesLabel, noLabel];
    return this.showMessage(message, MessageType.Info, title, buttons, buttonClasses).map(index => index === 0);
  }

  showWarningConfirm(message: string, title: string = '', yesLabel: string = 'Yes', noLabel: string = 'No'): Observable<boolean> {
    let buttons = [yesLabel, noLabel];
    return this.showMessage(message, MessageType.Warning, title, buttons).map(index => index === 0);
  }

  showInfoStyle(message: string, title: string = '', buttons: string[] = [], buttonClasses: string[] = []): Observable<number> {
    return this.showMessage(message, MessageType.Info, title, buttons, buttonClasses);
  }

  showWarningConfirmWithInputField(message: string,
                                   title: string = '',
                                   yesLabel: string = 'Yes',
                                   noLabel: string = 'Cancel',
                                   inputLabel: string = 'Input',
                                   displayKey: string = 'name',
                                   hasInputSuggest: boolean = false,
                                   invalidWarningMessage: string = '',
                                   suggestData: any[] = []): Observable<any> {
    let buttons = [yesLabel, noLabel];
    return this.showInputDialog(message, MessageType.Input, title, buttons, inputLabel, displayKey, hasInputSuggest, InputFieldType.TextBox, invalidWarningMessage, suggestData);
  }

  showWarningConfirmWithInputFieldSelectBox(message: string,
                                   title: string = '',
                                   yesLabel: string = 'Yes',
                                   noLabel: string = 'Cancel',
                                   inputLabel: string = 'Input',
                                   displayKey: string = 'name',
                                   hasInputSuggest: boolean = false,
                                   invalidWarningMessage: string = '',
                                   emptyListWarningMessage: string = '',
                                   suggestData: any[] = []): Observable<any> {
    let buttons = [yesLabel, noLabel];
    return this.showInputSelectDialog(message, MessageType.Input, title, buttons, inputLabel, displayKey, hasInputSuggest, InputFieldType.SelectBox,
      invalidWarningMessage, emptyListWarningMessage, suggestData);
  }

  private showInputSelectDialog(message: string,
                          type: MessageType,
                          title: string = '',
                          buttons: string[] = [],
                          inputLabel: string,
                          displayKey: string,
                          hasInputSuggest: boolean,
                          inputFieldType: InputFieldType = InputFieldType.TextBox,
                          invalidWarningMessage: string = '',
                          emptyListWarningMessage: string = '',
                          suggestData: any[]): Observable<any> {
    let dialogRef = this.dialog.open(MessageDialog);
    dialogRef.componentInstance.title = title;
    dialogRef.componentInstance.type = type;
    dialogRef.componentInstance.message = message;
    dialogRef.componentInstance.hasInputField = true;
    dialogRef.componentInstance.inputLabel = inputLabel;
    dialogRef.componentInstance.displayKey = displayKey;
    dialogRef.componentInstance.hasInputSuggest = hasInputSuggest;
    dialogRef.componentInstance.inputSuggestList = suggestData;
    dialogRef.componentInstance.inputFieldType = inputFieldType;
    dialogRef.componentInstance.invalidWarningMessage = invalidWarningMessage;
    dialogRef.componentInstance.emptyListWarningMessage = emptyListWarningMessage;
    if (buttons && buttons.length) {
      dialogRef.componentInstance.buttons = buttons;
    }

    return dialogRef.afterClosed();
  }

  private showInputDialog(message: string,
                          type: MessageType,
                          title: string = '',
                          buttons: string[] = [],
                          inputLabel: string,
                          displayKey: string,
                          hasInputSuggest: boolean,
                          inputFieldType: InputFieldType = InputFieldType.TextBox,
                          invalidWarningMessage: string = '',
                          suggestData: any[]): Observable<any> {
    let dialogRef = this.dialog.open(MessageDialog);
    dialogRef.componentInstance.title = title;
    dialogRef.componentInstance.type = type;
    dialogRef.componentInstance.message = message;
    dialogRef.componentInstance.hasInputField = true;
    dialogRef.componentInstance.inputLabel = inputLabel;
    dialogRef.componentInstance.displayKey = displayKey;
    dialogRef.componentInstance.hasInputSuggest = hasInputSuggest;
    dialogRef.componentInstance.inputSuggestList = suggestData;
    dialogRef.componentInstance.inputFieldType = inputFieldType;
    dialogRef.componentInstance.invalidWarningMessage = invalidWarningMessage;
    if (buttons && buttons.length) {
      dialogRef.componentInstance.buttons = buttons;
    }

    return dialogRef.afterClosed();
  }

  private showMessage(message: string,
                      type: MessageType = MessageType.Info,
                      title: string = '',
                      buttons: string[] = [],
                      buttonClasses?:string[],
                      disableClose?: boolean): Observable<number> {
    let dialogRef = this.dialog.open(MessageDialog, {
        disableClose: disableClose
    });
    dialogRef.componentInstance.title = title;
    dialogRef.componentInstance.type = type;
    dialogRef.componentInstance.message = message;

    if (buttons && buttons.length) {
      dialogRef.componentInstance.buttons = buttons;
    }

    if (buttonClasses) {
      dialogRef.componentInstance.buttonClasses = buttonClasses;
    }
    return dialogRef.afterClosed();
  }
}



