import { Component, OnInit } from '@angular/core';
import { AuthoringService } from '../../services/authoring.service';
import { DocumentMetadataModel } from '../../model/document.model';
import { DocumentElement, VideoElement} from '../../model/document-element.model';
import { DocumentTypeModel, SimulationType } from '../../model/document-type.model';
import { SimulationSetting } from '../../model/simulation.settings.model';
import { Utility } from '../../shared/helper/utilities';
import { ElementTypeKind } from '../../model/element-type.model';
import { Title } from '@angular/platform-browser';
import { environment } from '../../../environments/environment';
import * as moment from 'moment';

@Component({
  selector: 'app-scorm-content',
  templateUrl: './scorm-content.component.html',
  styleUrls: ['./scorm-content.component.scss']
})
export class ScormContentComponent implements OnInit {
  elements: DocumentElement [] = [];
  document: DocumentMetadataModel = new DocumentMetadataModel();
  doctypes: DocumentTypeModel [] = [];
  setting: SimulationSetting = new SimulationSetting();
  simulationType: SimulationType = 0;
  title = '';
  constructor(private authoringService: AuthoringService, private titleService: Title) {
  }

  ngOnInit() {
    this.authoringService.loadDataFromSrc().subscribe(res => {
      this.document = res[0] as DocumentMetadataModel;
      this.setting = res[3] as SimulationSetting;
      this.elements = res[1] as DocumentElement [];
      this.doctypes = res[2] as DocumentTypeModel [];
      this.title = res[4] as string;
      this.titleService.setTitle(this.title);
      this.simulationType = (environment == null || environment.simulationType == null) ? SimulationType.Demo : environment.simulationType;
      const currentDoctype = this.doctypes.filter(doctype => doctype.isSimulation && doctype.simulationType === this.simulationType)[0];
      this.elements = Utility.flatternArray(this.authoringService.filterElementsByDoctype(this.elements, currentDoctype)
        .map(e => (e.kind === ElementTypeKind.Group) ? e.subElements.map(sub => sub.kind === ElementTypeKind.ConsolidateTableStep ? sub.subElements : sub) : e));

      this.elements.forEach(e => {
        if (e.displayText) {
          e.displayText = this.setDynamicFields(e.displayText);
        }
        if (e.kind === ElementTypeKind.Video && (e as VideoElement).header) {
          (e as VideoElement).header = this.setDynamicFields((e as VideoElement).header);
        }
        if (e.subElements && e.subElements.length) {
          e.subElements.forEach(s => {
            s.displayText = this.setDynamicFields(s.displayText);
          });
        }
      });
    });
  }

  setDynamicFields(displayText: string) {
    let jsonDynamicFields = this.authoringService.getDynamicFields();
    if (!displayText) return '';
    let companyLogo = `<img class="logo" alt="Company Logo" onerror="this.src='./assets/ES_Big_E.png';" src="${jsonDynamicFields['%%CompanyLogo%%'] + `?d=${Date.now().toString()}`}"></img>`;
    displayText = displayText.replace(/%%CompanyLogo%%/g, companyLogo);
	displayText = displayText.replace(/%%ContentTitle%%/g, jsonDynamicFields['%%ContentTitle%%']);
    displayText = displayText.replace(/%%FunctionalAreas%%/g, jsonDynamicFields['%%FunctionalAreas%%']);
    displayText = displayText.replace(/%%FunctionalSubareas%%/g, jsonDynamicFields['%%FunctionalSubareas%%']);

    let modificationTime = new Date(jsonDynamicFields['%%LastChangedTime%%'].toString());
    modificationTime = new Date(modificationTime.getTime() - modificationTime.getTimezoneOffset() * 60000);

    let currentFormatUpperCase = jsonDynamicFields['%%DateFormat%%'].toString().toUpperCase() + ' hh:mm A';

    displayText = displayText.replace(/%%LastChangedTime%%/g, moment(new Date(modificationTime)).format(currentFormatUpperCase));
    displayText = displayText.replace(/%%DocumentVersion%%/g, jsonDynamicFields['%%DocumentVersion%%']);
    displayText = displayText.replace(/%%DocumentName%%/g, jsonDynamicFields['%%DocumentName%%']);
    displayText = displayText.replace(/%%DocumentAuthor%%/g, jsonDynamicFields['%%DocumentAuthor%%'] ? jsonDynamicFields['%%DocumentAuthor%%'] : '');
    displayText = displayText.replace(/%%TotalPage%%/g, jsonDynamicFields['%%TotalPage%%'] ? jsonDynamicFields['%%TotalPage%%'] : '0');
    displayText = displayText.replace(/%%Department%%/g, jsonDynamicFields['%%Department%%']);
    displayText = displayText.replace(/%%Application%%/g, jsonDynamicFields['%%Application%%']);
    return displayText;
  }
}



