import 'hammerjs';
import { BrowserModule, HAMMER_GESTURE_CONFIG } from '@angular/platform-browser';
import {Injectable, NgModule} from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AppComponent } from './app.component';
import { AuthoringService } from './services/authoring.service';
import { SharedModule } from './shared/shared.module';
import { AppRoutingModule } from './app-routing,module';
import { ContentModule } from './content/content.module';
import { HttpClientModule } from '@angular/common/http';
import { NotificationService } from './services/notification.service';
import {GestureConfig} from './shared/material/core';
import { OfflineService } from './services/offline.service';
import { DocumentBranchServiceV2 } from './services/document.service';
import { SiteThemingService } from './services/site-theming.service';
import { ThemeService } from './services/theme.service';
import { HttpModule } from '@angular/http';
import { TranslateModule } from '@ngx-translate/core';



@Injectable()
export class HammerConfig extends GestureConfig {
  buildHammer(element: HTMLElement) {
    return new GestureConfig().buildHammer(element);
  }
}

@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    BrowserModule, BrowserAnimationsModule, SharedModule.forRoot(), ContentModule, HttpClientModule,
    HttpModule,AppRoutingModule, TranslateModule.forRoot()
  ],
  exports: [TranslateModule],
  providers: [AuthoringService, NotificationService,
    OfflineService,DocumentBranchServiceV2,SiteThemingService,ThemeService,
    {provide: HAMMER_GESTURE_CONFIG, useClass: HammerConfig}],
  bootstrap: [AppComponent]
})
export class AppModule {
}



