export class ThemeSettingConstants {
  public static readonly FONT_ID = 'font';
  public static readonly CUSTOM_FONT_ID = 'custom-font';
  public static readonly CUSTOM_FONT_FAMILY = 'custom-font';
  public static readonly CURRENT_FONT_ID = 'current-font';
  public static readonly CURRENT_FONT_FAMILY = 'current-font';
  public static readonly CUSTOM_FONT_NAME = 'Custom Font';
  public static readonly BOLD_FORMAT_INDEX = 0;
  public static readonly ITALIC_FORMAT_INDEX = 1;
  public static readonly UNDERLINE_FORMAT_INDEX = 2;
}

export class ThemeModel {
  id: number = 0;
  className: string = '';
  tenantId: number = 0;
  custom: CustomOptions = new CustomOptions();
  customized: boolean = false;
  fontId: number;

  fromJson(json: any): ThemeModel {
    if (!json) {
      return this;
    }

    this.id = json.id;
    this.className = json.className;
    this.tenantId = json.tenantId || null;
    if (json.custom) {
      let customObject = JSON.parse(json.custom);
      if (customObject) {
        this.custom = new CustomOptions().fromJson(customObject);
      }
    }
    this.fontId = json.fontId || null;
    return this;
  }

  toJson(): any {
    return {
      id: this.id,
      className: this.className,
      custom: JSON.stringify(this.custom),
      fontId: this.fontId > 0 ? this.fontId : null
    };
  }

  clone(): ThemeModel {
    let theme = new ThemeModel();

    theme.id = this.id;
    theme.className = this.className;
    theme.tenantId = this.tenantId || null;
    theme.fontId = this.fontId || null;
    let options = new CustomOptions();
    options.headerSecondaryFont = this.custom.headerSecondaryFont;
    options.headerPrimaryFont = this.custom.headerPrimaryFont;
    options.stepTitleFontSize = this.custom.stepTitleFontSize;
    options.stepNoteInstructionFontSize = this.custom.stepNoteInstructionFontSize;
    options.stepNoteCaptionFontSize = this.custom.stepNoteCaptionFontSize;
    options.stepDesFontSize = this.custom.stepDesFontSize;
    options.stepActionFontSize = this.custom.stepActionFontSize;
    options.headerIcon = this.custom.headerIcon;
    options.mainAccent = this.custom.mainAccent;
    options.stepActionBackgroundColor = this.custom.stepActionBackgroundColor;
    options.stepActionBorderColor = this.custom.stepActionBorderColor;
    options.stepActionFontColor = this.custom.stepActionFontColor;
    options.stepActionFontFormat = this.custom.stepActionFontFormat.concat();
    options.stepDesFontColor = this.custom.stepDesFontColor;
    options.stepDesFontFormat = this.custom.stepDesFontFormat.concat();
    options.stepDesIndexBackgroundColor = this.custom.stepDesIndexBackgroundColor;
    options.stepDesIndexFontColor = this.custom.stepDesIndexFontColor;
    options.stepNoteCaptionBackgroundColor = this.custom.stepNoteCaptionBackgroundColor;
    options.stepNoteCaptionBorderColor = this.custom.stepNoteCaptionBorderColor;
    options.stepNoteCaptionFontColor = this.custom.stepNoteCaptionFontColor;
    options.stepNoteCaptionFontFormat = this.custom.stepNoteCaptionFontFormat.concat();
    options.stepNoteInstructionFontColor = this.custom.stepNoteInstructionFontColor;
    options.stepNoteInstructionFontFormat = this.custom.stepNoteInstructionFontFormat.concat();
    options.stepTitleFontColor = this.custom.stepTitleFontColor;
    options.stepTitleFontFormat = this.custom.stepTitleFontFormat.concat();
    options.stepTitleSeparateColor = this.custom.stepTitleSeparateColor;
    options.stepControlNumber = this.custom.stepControlNumber;
    options.controlImageHighlight = this.custom.controlImageHighlight;
    theme.custom = options;
    theme.customized = true;
    return theme;
  }
}

export class CustomOptions {
  mainAccent: string = DefaultThemeAndFont.MAIN_ACCENT_COLOR;
  headerPrimaryFont: string = DefaultThemeAndFont.HEADER_FONT_COLOR;
  headerIcon: string = DefaultThemeAndFont.HEADER_ICON_COLOR;
  headerSecondaryFont: string = DefaultThemeAndFont.HEADER_FONT_COLOR;


  stepTitleFontSize: number = DefaultThemeAndFont.STEP_TITLE_FONT_SIZE;
  stepTitleFontColor: string = DefaultThemeAndFont.FONT_COLOR;
  stepTitleFontFormat: boolean[] = [true, false, false];
  stepTitleSeparateColor: string = DefaultThemeAndFont.FONT_COLOR;


  stepDesFontSize: number = DefaultThemeAndFont.STEP_DES_FONT_SIZE;
  stepDesFontColor: string = DefaultThemeAndFont.FONT_COLOR;
  stepDesFontFormat: boolean[] = [false, false, false];
  stepDesIndexBackgroundColor: string = DefaultThemeAndFont.STEP_DES_INDEX_BACKGROUND_COLOR;
  stepDesIndexFontColor: string = DefaultThemeAndFont.FONT_COLOR;

  stepNoteCaptionFontSize: number = DefaultThemeAndFont.STEP_NOTE_CAPTION_FONT_SIZE;
  stepNoteCaptionFontColor: string = DefaultThemeAndFont.FONT_COLOR;
  stepNoteCaptionFontFormat: boolean[] = [true, false, false];
  stepNoteCaptionBackgroundColor: string = DefaultThemeAndFont.MAIN_ACCENT_COLOR;
  stepNoteCaptionBorderColor: string = DefaultThemeAndFont.FONT_COLOR;

  stepNoteInstructionFontSize: number = DefaultThemeAndFont.STEP_NOTE_INSTRUCTION_FONT_SIZE;
  stepNoteInstructionFontColor: string = DefaultThemeAndFont.FONT_COLOR;
  stepNoteInstructionFontFormat: boolean[] = [false, false, false];

  stepActionFontSize: number = DefaultThemeAndFont.STEP_ACTION_FONT_SIZE;
  stepActionFontColor: string = DefaultThemeAndFont.FONT_COLOR;
  stepActionFontFormat: boolean[] = [true, false, false];
  stepActionBackgroundColor: string = DefaultThemeAndFont.MAIN_ACCENT_COLOR;
  stepActionBorderColor: string = DefaultThemeAndFont.FONT_COLOR;

  stepControlNumber: string = DefaultThemeAndFont.FONT_COLOR;
  controlImageHighlight: string = DefaultThemeAndFont.FONT_COLOR;

  fromJson(json: any): CustomOptions {
    if (!json) {
      return this;
    }

    this.mainAccent = json.mainAccent;
    this.headerPrimaryFont = json.headerPrimaryFont;
    this.headerIcon = json.headerIcon;
    this.headerSecondaryFont = json.headerSecondaryFont;

    this.stepTitleFontSize = json.stepTitleFontSize;
    this.stepTitleFontColor = json.stepTitleFontColor;
    this.stepTitleFontFormat = json.stepTitleFontFormat;
    this.stepTitleSeparateColor = json.stepTitleSeparateColor;

    this.stepDesFontSize = json.stepDesFontSize;
    this.stepDesFontColor = json.stepDesFontColor;
    this.stepDesFontFormat = json.stepDesFontFormat;
    this.stepDesIndexBackgroundColor = json.stepDesIndexBackgroundColor;
    this.stepDesIndexFontColor = json.stepDesIndexFontColor;

    this.stepNoteCaptionFontSize = json.stepNoteCaptionFontSize;
    this.stepNoteCaptionFontColor = json.stepNoteCaptionFontColor;
    this.stepNoteCaptionFontFormat = json.stepNoteCaptionFontFormat;
    this.stepNoteCaptionBackgroundColor = json.stepNoteCaptionBackgroundColor;
    this.stepNoteCaptionBorderColor = json.stepNoteCaptionBorderColor;

    this.stepNoteInstructionFontSize = json.stepNoteInstructionFontSize;
    this.stepNoteInstructionFontColor = json.stepNoteInstructionFontColor;
    this.stepNoteInstructionFontFormat = json.stepNoteInstructionFontFormat;

    this.stepActionFontSize = json.stepActionFontSize;
    this.stepActionFontColor = json.stepActionFontColor;
    this.stepActionFontFormat = json.stepActionFontFormat;
    this.stepActionBackgroundColor = json.stepActionBackgroundColor;
    this.stepActionBorderColor = json.stepActionBorderColor;

    this.controlImageHighlight = json.controlImageHighlight;
    this.stepControlNumber = json.stepControlNumber;

    return this;
  }
}

export class DefaultThemeAndFont {
  public static readonly MAIN_ACCENT_COLOR = '#ffd700';
  public static readonly HEADER_FONT_COLOR = '#000080';
  public static readonly HEADER_ICON_COLOR = '#000080';
  public static readonly THEME_NAME = 'Default Theme';
  public static readonly FONT_COLOR = '#000080';
  public static readonly FONT_SIZE = 12;
  public static readonly FONT_FAMILY = 'Open Sans';
  public static readonly FONT_NAME = 'Default Font';
  public static readonly STEP_TITLE_FONT_SIZE = 18;
  public static readonly STEP_DES_FONT_SIZE = 16;
  public static readonly STEP_DES_INDEX_BACKGROUND_COLOR = '#ffffff';
  public static readonly STEP_DES_INDEX_DOT_INCLUDE = true;
  public static readonly STEP_ACTION_FONT_SIZE = 16;
  public static readonly STEP_NOTE_CAPTION_FONT_SIZE = 32;
  public static readonly STEP_NOTE_INSTRUCTION_FONT_SIZE = 16;
}



