import { Injectable } from '@angular/core';
import { Constants } from '../shared/helper/constants';
import { Utility } from '../shared/helper/utilities';
import { HttpClient } from '@angular/common/http';
import { DoctypeId, DocumentTypeModel } from '../model/document-type.model';
import { ActionKind } from '../model/recorder-event.model';
import 'rxjs/add/operator/map';
import 'rxjs/add/observable/of';
import { DocumentMetadataModel } from '../model/document.model';
import { SimulationSetting } from '../model/simulation.settings.model';
import { Observable } from 'rxjs/Observable';
import { ElementGroup, ElementType, ElementTypeCategory, ElementTypeKind, StepElement } from 'opus-content-lib';
import { DocumentElement } from '../model/document-element.model';
//declare var documentJson: any;
declare var dataJson : any;
declare var elementsJson : any;
declare var metadataJson : any;

@Injectable()
export class AuthoringService {
  private elementGroups: ElementGroup [] = [];
  private json: any = {};
  private s3BucketLink = '';
  private s3RecordingBucketName = '';
  private elements: any = [];
  private jsonMetadata: any;
  constructor(private http: HttpClient) {
  }

  getAllElementGroup() {
    if (!this.elementGroups || !this.elementGroups.length) {
      let allElementGroups: ElementGroup[] = [];

      if (this.json && this.json.elementGroups && this.json.elementGroups.length) {
        allElementGroups = this.json.elementGroups.map((group: any) => new ElementGroup().fromJson(group));
      }

      this.elementGroups = allElementGroups;
      return allElementGroups;
    }
    return this.elementGroups;
  }

  getAllDocumentTypes(): DocumentTypeModel[] {
    let documentTypes: DocumentTypeModel[] = [];

    if (this.json && this.json.doctypes && this.json.doctypes.length) {
      documentTypes = this.json.doctypes.map((doc: any) => new DocumentTypeModel().fromJson(doc));
      documentTypes = documentTypes.filter(x => x.documentTypeSetting.isActive);
    }

    const doctype508Index = documentTypes.findIndex(d => d.id === DoctypeId.Doctype508);
    const doctype508NewIndex = documentTypes.findIndex(d => d.isSimulation);

    if (doctype508Index !== -1 && doctype508NewIndex !== -1) {
      documentTypes = Utility.moveElementInArray(documentTypes, doctype508Index, doctype508NewIndex);
    }

    return documentTypes;
  }

  getDocumentMetada(): DocumentMetadataModel {
    let document = new DocumentMetadataModel();
    if (this.json && this.json.document) {
      document = new DocumentMetadataModel().fromJson(this.json.document);
    }
    return document;
  }

  getElementTypeById(elementTypeId: number): ElementType {
    if (elementTypeId === 0) {
      let groupType = new ElementType();
      groupType.category = ElementTypeCategory.Group;
      groupType.kind = ElementTypeKind.Group;
      return groupType;
    }

    if (this.elementGroups.length) {
      let index = this.elementGroups.findIndex(group => (group.types.findIndex(type => type.id === elementTypeId)) > -1);

      if (index < 0) {
        return null;
      }

      let typeIndex = this.elementGroups[index].types.findIndex(type => type.id === elementTypeId);

      if (typeIndex < 0) {
        return null;
      }

      return this.elementGroups[index].types[typeIndex];
    }

    return null;
  }

  loadRecordingSteps(): DocumentElement [] {
    let elements: DocumentElement [] = [];
    if (this.json && this.json.elements && this.json.elements.length) {
      elements = this.json.elements.map(obj => {
        if (obj.kind === ElementTypeKind.Step) {
          return new StepElement().fromJson(obj);
        }

        return DocumentElement.fromDto(obj);
      });
    }

    return elements;
  }

  filterElementsByDoctype(elements: DocumentElement [], currentDoctype: DocumentTypeModel, allDocTypes?: DocumentTypeModel[],
                          inEditor?: boolean): DocumentElement[] {
    let filterElements: DocumentElement [] = [];
    let backupSuitableElementTypes: number[] = currentDoctype.suitableElementTypes;

    if (!elements || elements.length === 0) {
      return filterElements;
    }

    filterElements = elements.map(element => element.clone());

    if (allDocTypes && currentDoctype && currentDoctype.id === 0) {
      currentDoctype.suitableElementTypes = [];
      allDocTypes.forEach(type => {
        currentDoctype.suitableElementTypes = currentDoctype.suitableElementTypes.concat(type.suitableElementTypes);
      });

    } else if (!currentDoctype || currentDoctype.id === 0) {
      return filterElements;
    }

    filterElements = filterElements.map(element => {
      let getElementByType = (element: DocumentElement) => {
        let type = this.getElementTypeById(element.elementTypeId);

        if (type && type.specificToDocType) {
          let index = currentDoctype.suitableElementTypes.indexOf(element.elementTypeId);

          if (index < 0) {
            return null;
          }
        }

        let groups = element.subElements;

        if (!groups.length) {
          return element;
        }

        element.subElements = groups.map(element => getElementByType(element)).filter(element => element !== null);
        return element;
      };

      if (element.kind === ElementTypeKind.Group || element.kind === ElementTypeKind.Step || element.kind === ElementTypeKind.PowerPointSlideStep) {
        let stepElement = element as StepElement;
        if (currentDoctype.id > 0 && (element.kind === ElementTypeKind.Step || element.kind === ElementTypeKind.PowerPointSlideStep) && stepElement.specificToDocTypes
          && stepElement.availableDocTypes.length > 0 &&  stepElement.availableDocTypes.indexOf(currentDoctype.id) === -1)
        {
          return null;
        }

        let groups = element.subElements;
        if (!groups.length) {
          return element;
        }

        element.subElements = groups.map(element => getElementByType(element)).filter((element) => {
          if (element === null)
            return false;

          let subStepElement = element as StepElement;
          return !(currentDoctype.id > 0 && (element.kind === ElementTypeKind.Step || element.kind === ElementTypeKind.PowerPointSlideStep) && subStepElement.specificToDocTypes &&
            subStepElement.availableDocTypes.length > 0 &&  subStepElement.availableDocTypes.indexOf(currentDoctype.id) === -1);
        } );

        if (currentDoctype.isSimulation) {
          element.subElements = element.subElements.filter(element => this.isElementAbleInSimulationDoctypes(element, inEditor));
        }

        if (element.kind === ElementTypeKind.Group) {
          if (element.subElements.length === 1 && element.subElements[0].kind !== ElementTypeKind.ConsolidateTableStep) {
            element = element.subElements[0];
          } else if (element.subElements.length === 0) {
            element = null;
          }
        }

        return element;
      }

      return getElementByType(element);
    });

    currentDoctype.suitableElementTypes = backupSuitableElementTypes;

    filterElements = filterElements.filter(element => element !== null);

    if (currentDoctype.isSimulation) {
      return filterElements.filter(element => this.isElementAbleInSimulationDoctypes(element, inEditor));
    }

    return filterElements;
  }

  getResourceUrl(recordingId: string, resourceId: string, notCached?: boolean) {
    if (!resourceId) {
      return DocumentElement.EmptyResource;
    }
    const href = window.location.href;
    const dir = href.substring(0, href.lastIndexOf('/'));
    if(dataJson.scormType == Constants.BOOK_SCORM)
    {
      let url = `${dir}/data/resource/${resourceId}` ;
      return url;
    }
    let url = `${dir}/${Constants.ResourceAssetPath}/${resourceId}` ;
    return url;
  }

  getSimulationSettings(): SimulationSetting {
    if (!this.json || !this.json.simulationSetting) {
      return new SimulationSetting();
    }

    return new SimulationSetting().fromJson(this.json.simulationSetting);
  }

  loadDataFromSrc() {
    const href = window.location.href;
    const dir = href.substring(0, href.lastIndexOf('/')) + '/';
    
    if (typeof dataJson !== 'undefined'){
      this.json = dataJson;
      this.s3BucketLink = dataJson.s3BucketLink;
      this.s3RecordingBucketName = dataJson.s3RecordingBucketName;
    }

    this.getAllElementGroup();
    return Observable.of([this.getDocumentMetada(), this.loadRecordingSteps(), this.getAllDocumentTypes(), this.getSimulationSettings(), this.json ? this.json.title : '']);
  }

  getPressEnterKeyToAdvanceInstruction() {
    return this.json && this.json.languageData && this.json.languageData.pressEnterKeyToAdvanceInstruction ? this.json.languageData.pressEnterKeyToAdvanceInstruction : '';
  }

  getPressEnterKeyToAdvanceForNoteInstruction() {
    return this.json && this.json.languageData && this.json.languageData.pressEnterKeyToAdvanceForNoteInstruction ? this.json.languageData.pressEnterKeyToAdvanceForNoteInstruction : '';
  }

  getDynamicFields() {
    return this.json && this.json.dynamicFields ? this.json.dynamicFields : [];
  }

  private isElementAbleInSimulationDoctypes(element: DocumentElement, inEditor: boolean) {
    if (element.kind === ElementTypeKind.Step && inEditor && !(element as StepElement).isBuildAStepElement) {
      if ((element as StepElement).simRect.empty && !(element as StepElement).convertedFromFrame && !((element as StepElement).action === ActionKind.Press)) {
        return false;
      }

      if ((element as StepElement).action === ActionKind.Unknown && !(element as StepElement).convertedFromFrame) {
        return false;
      }
    } else if (element.kind === ElementTypeKind.Group) {
      return true;
    }

    let elementType = this.getElementTypeById(element.elementTypeId);
    if (elementType && elementType.category) {
      return [ElementTypeCategory.Title, ElementTypeCategory.Footer, ElementTypeCategory.Header].indexOf(elementType.category) < 0;
    }
    return false;
  }
}



